import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';
import AWSDKVisitCost from '../../../model/visit/awsdk_visit_cost';

/**
 * Represents a response object wrapper to a single {@link model.AWSDKVisitCost|AWSDKVisitCost} object type
 * @property {model.AWSDKVisitCost} visitCost the estimated cost of a visit
 * @extends model.AWSDKResponse
 */
class AWSDKVisitCostResponse extends AWSDKResponse {
  get cost() {
    return GenericParser.parseObject(this.__data.cost, AWSDKVisitCost);
  }
}
export default AWSDKVisitCostResponse;
