import log from 'loglevel';

/**
 * A wrapper class around the loglevel logger. If no configuration for a logger is passed, then this logger will log to
 * stdout with level set to info by default.
 * For a more in-depth understanding of what a configuration for a logger is @see {@link https://www.npmjs.com/packageloglevel|loglevel}
 * @private
 *
 */
class Logger {
  constructor() {
    this.__log = log.getLogger('AWSDKLogger');
    this.__log.setDefaultLevel('Error');
  }

  setDefaultLevel(defaultLevel) {
    this.__log.setDefaultLevel(defaultLevel);
  }

  setLevel(level) {
    this.__log.setLevel(level);
  }

  getLevel() {
    return this.__log.getLevel();
  }

  getLevelString() {
    return Object.keys(this.__log.levels).find(key => this.__log.levels[key] === this.__log.getLevel());
  }

  disableAll() {
    this.__log.disableAll();
  }

  enableAll() {
    this.__log.enableAll();
  }
  /**
   * Log the message at a 'fatal' level. This should imply that a non-recoverable exception has occurred and that the application is about to exit
   * @param {String} message the message to be logged
   * @param {Object|Object[]} optionalParams any objects and/or array of objects to be appended to the message that may provide clarification on what happened
   *
   */
  fatal(message, ...optionalParams) {
    const logMessage = `AWSDK ${message}`;
    this.__log.fatal(logMessage, ...optionalParams);
  }

  /**
   * Log the message at an 'error' level. This logging level means an unexpected exception has been found and should be investigated, however application will continue execution.
   * @param {String} message the message to be logged
   * @param {Object|Object[]} optionalParams any objects and/or array of objects to be appended to the message that may provide clarification on what happened
   *
   */
  error(message, ...optionalParams) {
    const logMessage = `AWSDK ${message}`;
    this.__log.error(logMessage, ...optionalParams);
  }

  /**
   * Log the message at a 'warn' level. This logging level indicates that an unexpected behavior that should be investigated eventually.
   * @param {String} message the message to be logged
   * @param {Object|Object[]} optionalParams any objects and/or array of objects to be appended to the message that may provide clarification on what happened
   *
   */
  warn(message, ...optionalParams) {
    const logMessage = `AWSDK ${message}`;
    this.__log.warn(logMessage, ...optionalParams);
  }

  /**
   * Log the message at an 'info' level. This loggin level is normally used for loggin regular application operations.
   * @param {String} message the message to be logged
   * @param {Object|Object[]} optionalParams any objects and/or array of objects to be appended to the message that may provide clarification on what happened
   *
   */
  info(message, ...optionalParams) {
    const logMessage = `AWSDK ${message}`;
    this.__log.info(logMessage, ...optionalParams);
  }

  /**
   * Log the message at a 'debug' level. This logging level should *NOT* be used in a production environment and should be used to log detailed information that is useful in debugging
   * @param {String} message the message to be logged
   * @param {Object|Object[]} optionalParams any objects and/or array of objects to be appended to the message that may provide clarification on what happened
   *
   */
  debug(message, ...optionalParams) {
    const logMessage = `AWSDK ${message}`;
    this.__log.debug(logMessage, ...optionalParams);
  }

  /**
   * Log the message at a 'trace' level. This logging level should *NOT* be used in production, since it tends to be quite verbose. These logs tend to be very detailed.
   * @param {String} message the message to be logged
   * @param {Object|Object[]} optionalParams any objects and/or array of objects to be appended to the message that may provide clarification on what happened
   *
   */
  trace(message, ...optionalParams) {
    const logMessage = `AWSDK ${message}`;
    this.__log.trace(logMessage, ...optionalParams);
  }

  get levels() {
    return this.__log.levels;
  }
}

export default Logger;
