import AWSDKResponse from './awsdk_response';
import AWSDKSentMessages from '../../../model/secure_message/awsdk_sent_messages';
import GenericParser from '../../parser/generic_parser';

/**
 * Simple wrapper around an {@link model.AWSDKSentMessages}
 * @property {model.AWSDKSentMessages} sentMessages the {@link model.AWSDKSentMessages}
 * @extends model.AWSDKResponse
 */
class AWSDKSentMessagesResponse extends AWSDKResponse {
  get sentMessages() {
    return this.__data.mailbox ? GenericParser.parseObject(this.__data.mailbox, AWSDKSentMessages) : null;
  }
}

export default AWSDKSentMessagesResponse;
