import GenericParser from '../../internal/parser/generic_parser';
import AWSDKSecureMessage from './awsdk_secure_message';

/**
 * This class contains all necessary information for representing a mailbox message.
 * @property {String[]} memberRecipients an array of all member recipients
 * @property {String[]} nonMemberRecipients an array of all non-member recipients
 * @property {String} senderName the sender's name
 * @property {String} bodyPreview the preview of the body of the message
 * @property {Boolean} hasAttachment whether or not the message has an attachment
 * @property {Boolean} isReplied whether or not the message is replied to
 * @since 1.1.0
 * @extends model.AWSDKSecureMessage
 */
class AWSDKMailboxMessage extends AWSDKSecureMessage {
  get memberRecipients() {
    return this.__data.memberRecipients;
  }
  get nonMemberRecipients() {
    return this.__data.nonMemberRecipients;
  }
  get senderName() {
    return this.__data.senderName;
  }
  get bodyPreview() {
    return this.__data.bodyPreview;
  }
  get hasAttachment() {
    return GenericParser.parseBoolean(this.__data.hasAttachment);
  }
  get isReplied() {
    return GenericParser.parseBoolean(this.__data.isReplied);
  }
}
export default AWSDKMailboxMessage;
