import GenericParser from '../../parser/generic_parser';
import AWSDKResponse from './awsdk_response';
import AWSDKSubscription from '../../../model/insurance/awsdk_subscription';

/**
 * The AWSDKSubscriptionResponse class represents the response object to a call for a insurance details
 * @property {model.AWSDKPractice} insuranceDetails the insurance details for a given practice
 * @extends model.AWSDKResponse
 */
class AWSDKSubscriptionResponse extends AWSDKResponse {
  get insuranceDetails() {
    return GenericParser.parseObject(this.__data.subscription, AWSDKSubscription);
  }
}

export default AWSDKSubscriptionResponse;
