import GenericParser from '../../internal/parser/generic_parser';
import AWSDKDataObject from '../awsdk_dataobject';

/**
 * This class describes a procedure done, or to be done, to the {@link model.AWSDKConsumer|AWSDKConsumer}
 * @property {String} description the description of the procedure
 * @property {Number} priority the priority of the procedure. 0 is the highest priority
 * @property {Boolean} isBillable boolean true if the procedure is billable
 * @property {String} modifiers any additional data for the procedure
 * @property {String} code the medical code for this procedure
 * @property {String} displayName the displayName for this procedure
 * @extends model.AWSDKDataObject
 */
class AWSDKVisitProcedure extends AWSDKDataObject {
  get description() {
    return this.__data.procedure.description;
  }

  get priority() {
    return this.__data.priority;
  }

  get isBillable() {
    return GenericParser.parseBoolean(this.__data.billable);
  }

  get modifiers() {
    return this.__data.modifiers;
  }

  get code() {
    return this.__data.procedure.code;
  }

  get displayName() {
    return this.__data.procedure.displayName;
  }
}
export default AWSDKVisitProcedure;
