import GenericParser from '../../internal/parser/generic_parser';
import AWSDKEntity from '../awsdk_entity';
import AWSDKConsumer from '../consumer/awsdk_consumer';
import AWSDKProviderDetails from '../provider/awsdk_provider_details';
import AWSDKAddress from '../awsdk_address';
import AWSDKConsumerFeedbackQuestion from '../visit/awsdk_consumer_feedback_question';
import AWSDKStateFormularyRestriction from '../visit/awsdk_state_formulary_restriction';
import AWSDKVisitCost from '../visit/awsdk_visit_cost';
import AWSDKPharmacy from '../pharmacy/awsdk_pharmacy';
import AWSDKState from '../awsdk_state';
import AWSDKTriageQuestion from '../visit/awsdk_triage_question';
import AWSDKVisitSchedule from '../visit/awsdk_visit_schedule';
import AWSDKProviderEntries from '../visit/awsdk_provider_entries';
import AWSDKIntake from '../visit/awsdk_intake';
import AWSDKDisposition from './awsdk_disposition';
import AWSDKEndReason from './awsdk_end_reason';
import AWSDKModality from '../awsdk_modality';

/**
 * The visit summary, that is the 'wrapUp'.
 * @property {model.AWSDKStateFormularyRestriction} formularyRestriction any restrictions the state might enforce over the prescription
 * @property {model.AWSDKConsumerFeedbackQuestion} consumerFeedbackQuestion the question to be presented to consumer for feedback
 * @property {model.AWSDKTriageQuestion[]} triageQuestions an array of {@link model.AWSDKTriageQuestion|AWSDKTriageQuestion} with Q&A presented to consumer during triage
 * @property {model.AWSDKProviderEntries} providerEntries represents information entered by provider during visit: notes, diagnoses, prescriptions, etc.
 * @property {model.AWSDKVisitCost} visitCost contains details about the Visit's cost
 * @property {model.AWSDKPharmacy} pharmacy the pharmacy to send prescriptions to.
 * @property {model.AWSDKAddress} shippingAddress the address to ship prescriptions to.
 * @property {model.AWSDKProviderDetails} assignedProvider the assigned {@link model.AWSDKProvider|AWSDKProvider} for the visit
 * @property {model.AWSDKProviderDetails} providerProxy represents an individual acting on the {@link model.AWSDKProvider|AWSDKProvider}'s behalf
 * @property {model.AWSDKConsumer} consumer represents the {@link model.AWSDKConsumer|AWSDKConsumer} who had the visit
 * @property {model.AWSDKConsumer} consumerProxy represents the {@link model.AWSDKConsumer|AWSDKConsumer}'s parent.
 * @property {model.AWSDKState} consumerLocation the {@link model.AWSDKConsumer|AWSDKConsumer}'s state location
 * @property {String} practiceName  the name of the practice for which the {@link model.AWSDKProvider|AWSDKConsumer} is affiliated with
 * @property {String[]} inviteEmails an array of emails for any guests who were invited via email
 * @property {model.AWSDKVisitSchedule} schedule contains schedule information about the visit
 * @property {String} hipaaNoticeText the HIPAA notice text which must be accepted in order to send visit summary via email
 * @property {String} additionalHipaaNoticeText additional HIPAA terms which must be accepted in order to send visit summary via email
 * @property {String} initiatorEngagementOverridePhone the consumer's preferred phone number for this visit
 * @property {string} formattedInitiatorEngagementOverridePhone the consumer's preferred formatted phone number for this visit
 * @since 1.3.0
 * @property {model.AWSDKDisposition} disposition  the disposition of the visit
 * @since 1.3.0
 * @property {model.AWSDKEndReason} endReason the reason for ending the visit
 * @since 1.4.0
 * @property {model.AWSDKModality} modality represents the modality for the visit
 * @since 3.0.0
 * @property {model.AWSDKIntake} intake the intake completed for the visit
 * @extends model.AWSDKEntity
 */
class AWSDKVisitSummary extends AWSDKEntity {
  get formularyRestriction() {
    return GenericParser.parseObject(this.__data.formularyRestriction, AWSDKStateFormularyRestriction);
  }

  get providerEntries() {
    return this.__data.providerEntries ? GenericParser.parseObject(this.__data.providerEntries, AWSDKProviderEntries) : null;
  }

  get visitCost() {
    return GenericParser.parseObject(this.__data.cost, AWSDKVisitCost);
  }

  get pharmacy() {
    return GenericParser.parseObject(this.__data.pharmacy, AWSDKPharmacy);
  }

  get shippingAddress() {
    return this.__data.shippingAddress ? GenericParser(this.__data.shippingAddress, AWSDKAddress) : null;
  }

  get assignedProvider() {
    return GenericParser.parseObject(this.__data.assignedProvider, AWSDKProviderDetails);
  }

  get providerProxy() {
    return GenericParser.parseObject(this.__data.providerProxy, AWSDKProviderDetails);
  }

  get consumer() {
    return GenericParser.parseObject(this.__data.member, AWSDKConsumer);
  }

  get consumerProxy() {
    return GenericParser.parseObject(this.__data.memberProxy, AWSDKConsumer);
  }

  get consumerLocation() {
    return GenericParser.parseObject(this.__data.memberLocation, AWSDKState);
  }

  get practiceName() {
    return this.__data.practiceName;
  }

  get consumerFeedbackQuestion() {
    return GenericParser.parseObject(this.__data.consumerFeedbackQuestion, AWSDKConsumerFeedbackQuestion);
  }

  get triageQuestions() {
    if (this.__data.triageQA === undefined) {
      let res = [];
      const triageQuestions = this.__data.triageQuestions;
      const triageAnswers = this.__data.triageAnswers;
      if (triageQuestions && triageQuestions.length > 0) {
        res = triageQuestions.map((item, index) => {
          const temp = new AWSDKTriageQuestion();
          temp.question = item;
          if (triageAnswers && index < triageAnswers.length) {
            temp.answer = triageAnswers[index];
          }
          return temp;
        });
      }
      this.__data.triageQA = res;
    }
    return this.__data.triageQA;
  }

  get inviteEmails() {
    return this.__data.videoInviteEmails;
  }

  get schedule() {
    return this.__data.schedule ? GenericParser.parseObject(this.__data.schedule, AWSDKVisitSchedule) : null;
  }

  get hipaaNoticeText() {
    return this.__data.hipaaNoticeText;
  }

  get additionalHipaaNoticeText() {
    return this.__data.additionalHipaaNoticeText;
  }

  get initiatorEngagementOverridePhone() {
    return this.__data.initiatorEngagementOverridePhone;
  }

  get formattedInitiatorEngagementOverridePhone() {
    return this.__data.formattedInitiatorEngagementOverridePhone;
  }

  get disposition() {
    return GenericParser.parseEnum(this.__data.disposition, AWSDKDisposition);
  }

  get endReason() {
    return GenericParser.parseEnum(this.__data.endReason, AWSDKEndReason);
  }

  /**
   * @since 1.3.1
   */
  set proxies(proxies) {
    if (Array.isArray(proxies) && proxies.every(item => item instanceof AWSDKConsumer)) {
      this.__proxies = proxies;
    } else {
      this.__proxies = null;
    }
  }

  /**
   * @since 1.4.0
   */
  get modality() {
    return GenericParser.parseObject(this.__data.modality, AWSDKModality);
  }

  /**
   * @since 3.0.0
   */
  get intake() {
    return this.__data.intake ? GenericParser.parseObject(this.__data.intake, AWSDKIntake) : null;
  }
}
export default AWSDKVisitSummary;
