import AWSDKEntity from '../awsdk_entity';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKMailboxMessage from './awsdk_inbox_message';

/**
 * This class contains all necessary information for representing a secure message mailbox.
 * @property {model.AWSDKMailboxMessage[]} messages the messages in the mailbox
 * @property {Date} timestamp the timestamp the mailbox was retrieved
 * @property {Number} total the total number of messages
 * @since 1.1.0
 * @extends model.AWSDKEntity
 */
class AWSDKMailbox extends AWSDKEntity {
  get messages() {
    return (this.__data.messages && this.__data.messages.map(item => GenericParser.parseObject(item, AWSDKMailboxMessage))) || [];
  }
  get timestamp() {
    return GenericParser.parseDate(this.__data.timestamp);
  }
  get total() {
    return this.__data.total;
  }

  /**
   * @private
   */
  __assimilate(otherMailbox) {
    this.__data.timestamp = otherMailbox.timestamp;
    this.__data.total = otherMailbox.total;

    const messageProcessedIds = {};
    const combinedMessages = [];
    otherMailbox.messages.concat(this.messages).forEach((message) => {
      if (!messageProcessedIds[message.id.persistentId]) {
        combinedMessages.push(message);
        messageProcessedIds[message.id.persistentId] = 1;
      }
    });

    this.__data.messages = combinedMessages.filter(message => !message.isDeleted);
  }
}
export default AWSDKMailbox;
