import GenericParser from '../../../internal/parser/generic_parser';
import AWSDKTopicType from '../../awsdk_topic_type';
import AWSDKError from '../../../error/awsdk_error';
import AWSDKUploadAttachment from './awsdk_upload_attachment';
import AWSDKDetailedMessage from '../awsdk_detailed_message';

/**
 * This class contains all necessary information for representing a message draft. Use one of the more specific subclasses
 * to send a New, Reply, or Forward message rather than using this class directly.
 * @property {String} subject the subject of the message
 * @property {String} body the body of the message
 * @property {model.AWSDKTopicType} topicType the topicType of the message
 * @property {model.AWSDKMessageActionType} actionType the actionType of the message @since 1.3.0
 * @property {String} recipientEncryptedId the encrypted id of the recipient for the message
 * @property {String} recipientName the name of the recipient for the message
 * @property {String} sourceMessageType the type of the sourceMessage
 * @property {String} sourceMessageId the encrypted id of the sourceMessage
 * @property {Boolean} attachHealthSummary TRUE if health summary should be attached to the message
 * @since 1.3.0
 * @property {Boolean} sourceIsSystemNotification TRUE if the sourceMessage is a System Notification Message
 * @property {model.AWSDKUploadAttachment} uploadAttachment the uploadAttachment for the message
 * @since 1.1.0
 */
class AWSDKMessageDraft {
  set subject(subject) {
    this.__subject = subject;
  }

  get subject() {
    return this.__subject;
  }

  set body(body) {
    this.__body = body;
  }

  get body() {
    return this.__body;
  }

  get topicType() {
    return this.__topicType ? GenericParser.parseObject(this.__topicType, AWSDKTopicType) : null;
  }

  get actionType() {
    return this.__actionType;
  }

  get recipientEncryptedId() {
    return this.__recipientEncryptedId;
  }

  get recipientName() {
    return this.__recipientName;
  }

  get sourceMessageType() {
    return this.__sourceMessageType;
  }

  get sourceMessageId() {
    return this.__sourceMessageId;
  }

  get sourceIsSystemNotification() {
    return GenericParser.parseBoolean(this.__sourceIsSystemNotification);
  }

  get attachHealthSummary() {
    return GenericParser.parseBoolean(this.__attachHealthSummary);
  }

  set attachHealthSummary(attachHealthSummary) {
    this.__attachHealthSummary = attachHealthSummary;
  }

  set uploadAttachment(uploadAttachment) {
    this.__uploadAttachment = uploadAttachment;
  }

  get uploadAttachment() {
    return this.__uploadAttachment ? GenericParser.parseObject(this.__uploadAttachment, AWSDKUploadAttachment) : null;
  }
}
export default AWSDKMessageDraft;
