import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';
import AWSDKProviderType from '../../../model/provider/awsdk_provider_type';

/**
 * A wrapper class representing a collection of {@link model.AWSDKProviderType} objects
 * @property {model.AWSDKProviderType[]} providerTypes representing an array of {@link model.AWSDKProviderType} objects
 * @extends model.AWSDKResponse
 */
class AWSDKProviderTypesResponse extends AWSDKResponse {
  get providerTypes() {
    return (Array.isArray(this.__data.providerTypes) && this.__data.providerTypes.map(item => GenericParser.parseObject(item, AWSDKProviderType))) || [];
  }
}
export default AWSDKProviderTypesResponse;
