/**
 * This enum represents a type filter that can be used when searching for PostVisitFollowUpItem
 * entities with {@link service.ConsumerService#searchPostVisitFollowUpItems()|ConsumerService.searchPostVisitFollowUpItems}
 * @since 2.8.0
 */
const AWSDKPostVisitFollowUpItemsTypeFilter = {
  /**
   * Filter to get all results
   */
  all: 'all',
  /**
   * Filter to get only imaging referral types
   */
  imagingReferral: 'imagingReferral',
  /**
   * Filter to get only lab referral types
   */
  labReferral: 'labReferral',
  /**
   * Filter to get only visit referral types
   */
  visitReferral: 'visitReferral',
  /**
   * Filter to get only referral types
   */
  referral: 'referral',
  /**
   * Filter to get only sick slip types
   */
  sickSlip: 'sickSlip',
  /**
   * Filter to get only appointment types
   */
  appointment: 'appointment',
};
Object.freeze(AWSDKPostVisitFollowUpItemsTypeFilter);
export default AWSDKPostVisitFollowUpItemsTypeFilter;
