import AWSDKProvider from '../../../model/provider/awsdk_provider';
import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';

/**
 * A wrapper class representing a collection of {@link model.AWSDKProvider} objects
 * @property {model.AWSDKProvider[]} providers representing an array of {@link model.AWSDKProvider} objects
 * @extends model.AWSDKResponse
 */
class AWSDKProvidersResponse extends AWSDKResponse {
  get providers() {
    return this.__data.providers.map(item => GenericParser.parseObject(item, AWSDKProvider));
  }
}
export default AWSDKProvidersResponse;
