import AWSDKEntity from '../awsdk_entity';
import GenericParser from '../../internal/parser/generic_parser';

/**
 * This class represents a scheduled visit
 * @property {Number} scheduledStartTime the scheduled start time
 * @property {Number} actualStartTime the appointment's actual start time
 * @property {Number} allotedDurationMs the alloted duration for the visit in millisenconds
 * @property {Number} allotedExtensionDurationMins the alloted extension duration for the visit
 * @property {Boolean} isScheduled boolean true if this was a scheduled visit, false otherwise
 * @property {Number} initialPreVisitStartTime the pre-visit start time as a long timestamp
 * @property {Number} postVisitStartTime the post-visit start time as a long timestamp
 * @property {Number} postVisitEndTime the post-visit end time as a long timestamp
 * @property {Number} waitingRoomEntryTime the appointment's waiting room entry time as a long timestamp
 * @property {Number} allotedBufferMs the amount of free time the provider has after a visit and before a next visit or a visit can be scheduled in milliseconds
 * @property {Number} waitDurationSecs the wait duration in seconds
 * @property {Number} actualDurationMs the length of the actual visit in milliseconds
 * @property {Number} cancelledEventTime the time the event was cancelled as a long timestamp
 * @extends model.AWSDKEntity
 */
class AWSDKVisitScheduled extends AWSDKEntity {
  get scheduledStartTime() {
    return this.__data.scheduledStartTime;
  }

  get actualStartTime() {
    return this.__data.actualStartTime;
  }

  get allotedDurationMs() {
    return this.__data.allotedDurationMs;
  }

  get allotedExtensionDurationMins() {
    return this.__data.allotedExtensionDurationMins;
  }

  get isScheduled() {
    return GenericParser.parseBoolean(this.__data.isScheduled);
  }

  get initialPreVisitStartTime() {
    return this.__data.initialPreVisitStartTime;
  }

  get postVisitStartTime() {
    return this.__data.postVisitStartTime;
  }

  get postVisitEndTime() {
    return this.__data.postVisitEndTime;
  }

  get waitingRoomEntryTime() {
    return this.__data.waitingRoomEntryTime;
  }

  get allotedBufferMs() {
    return this.__data.allotedBufferMs;
  }

  get waitDurationSec() {
    return this.__data.waitDurationSec;
  }

  get actualDurationMs() {
    return this.__data.actualDurationMs;
  }

  get cancelledEventTime() {
    return this.__data.cancelledEventTime;
  }
}
export default AWSDKVisitScheduled;
