import AWSDKError from '../../error/awsdk_error';
import Validator from '../../internal/validator/validator';
import AWSDKProviderDetails from './awsdk_provider_details';

/**
 * This class represents the search criteria for finding a provider's availability.
 * Get an instance of this via {@link service.ProviderService#getNewProviderAvailabilityCriteria()|ProviderService.getNewProviderAvailabilityCriteria}
 * @property {model.AWSDKProviderDetails} providerDetails the {@link model.AWSDKProviderDetails|AWSDKProviderDetails} to use
 * @property {String} timeZone the canonical time zone to use, i.e. America/New_York
 * @property {Date} [appointmentDate] the appointment date to filter the available dates on.
 * @property {Number} [limitInDays] the number of days past the specified appointment date to filter providers on.
 * @since 1.3.1
 */
class AWSDKProviderAvailabilityCriteria {
  constructor(criteria = {}) {
    if (criteria) {
      this.__providerDetails = criteria.providerDetails;
      this.__timeZone = criteria.timeZone;
      this.__appointmentDate = criteria.appointmentDate;
      this.__limitInDays = criteria.limitInDays;
    }
  }

  get timeZone() {
    return this.__timeZone;
  }

  set timeZone(timeZone) {
    this.__timeZone = timeZone;
  }

  get providerDetails() {
    return this.__providerDetails;
  }

  set providerDetails(providerDetails) {
    if (!(providerDetails instanceof AWSDKProviderDetails)) {
      throw AWSDKError.AWSDKIllegalArgument('providerDetails must be an instance of AWSDKPractice or AWSDKProviderDetails');
    }
    this.__providerDetails = providerDetails;
  }

  get appointmentDate() {
    return this.__appointmentDate;
  }

  set appointmentDate(appointmentDate) {
    if (!(appointmentDate instanceof Date)) {
      throw AWSDKError.AWSDKIllegalArgument('appointmentDate must be of type Date');
    }
    this.__appointmentDate = appointmentDate;
  }

  get limitInDays() {
    return this.__limitInDays;
  }

  set limitInDays(limitInDays) {
    if (!(Validator.isInt(limitInDays))) {
      throw AWSDKError.AWSDKIllegalArgument('limitInDays must be of type int');
    }
    this.__limitInDays = limitInDays;
  }
}
export default AWSDKProviderAvailabilityCriteria;
