import AWSDKConsumerHealthItem from './awsdk_consumer_health_item';

/**
 * Represents a medical condition that can be added or removed from a consumer's health record.
 * @property {Integer} termId the term ID of the condition
 * @property {String} displayName the name of the condition
 * @property {Boolean} current whether or not the condition is currently exhibited
 * @extends model.AWSDKConsumerHealthItem
 */
class AWSDKCondition extends AWSDKConsumerHealthItem {
  get displayName() {
    return this.__data.displayName;
  }
}
export default AWSDKCondition;
