import AWSDKResponse from './awsdk_response';
import AWSDKPharmacy from '../../../model/pharmacy/awsdk_pharmacy';
import GenericParser from '../../parser/generic_parser';

/**
 * Simple wrapper around an {@link model.AWSDKPharmacy}
 * @property {model.AWSDKPharmacy} pharmacy the {@link model.AWSDKPharmacy}
 * @extends model.AWSDKResponse
 */
class AWSDKPharmacyResponse extends AWSDKResponse {
  get pharmacy() {
    return GenericParser.parseObject(this.__data.pharmacy, AWSDKPharmacy);
  }
}

export default AWSDKPharmacyResponse;
