import Service from '../../service/service';
import AWSDKResponse from '../model/response/awsdk_response';

/**
 * Logs messages remotely to the configured installation
 *
 * @private
 * @extends service.Service
 */
class ServerLogger extends Service {
  /**
   * @private
   */
  logToServer(level, message, visitLoggingDataObject) {
    const link = this.findNamedLink(this.__links, 'logMessage');
    const options = this.generateOptions('POST', link.url, false);
    options.headers['Content-Type'] = 'application/json';
    options.auth = this.getSdkAuth();
    options.body = this.__buildMessageBody(message, level, visitLoggingDataObject);
    return this.executeRequest(options, AWSDKResponse)
      .catch((error) => {
        this.__logger.error('Failed to log message to server', error);
      });
  }

  setLinks(links) {
    this.__links = links;
  }

  setConfig(config) {
    this.__config = config;
  }

  __buildMessageBody(message, level, visitLoggingDataObject) {
    // the server expects a list of objects with TYPE and ENCRYPTEDID for member, provider, engagement
    const params = visitLoggingDataObject ? this.__buildParams(visitLoggingDataObject) : [];

    // the message MUST be a string
    const stringifiedMessage = typeof message !== 'string' ? JSON.stringify(message) : message;

    // adding the string param {0} as the server doesn't do it automatically
    const preparedMessage = this.__addParameterPlaceholdersToMessage(stringifiedMessage, params.length);

    return JSON.stringify({ level, message: preparedMessage, params });
  }

  // adds the appropriate placeholders from {0} to {n} where n = the number of params passed in if and only if they don't already exist in the string
  __addParameterPlaceholdersToMessage(message, numOfParams) {
    let modifiedMessage = message;

    for (let i = 0; i < numOfParams; i += 1) {
      if (!message.includes(`{${i}}`)) {
        modifiedMessage = `${modifiedMessage} {${i}}`;
      }
    }
    return modifiedMessage;
  }

  // only adds the param if the id exists
  __buildParams(visitLoggingDataObject) {
    const params = [];
    if (visitLoggingDataObject.visitId) {
      params.push({ type: 'engagement', encryptedId: visitLoggingDataObject.visitId });
    }
    if (visitLoggingDataObject.consumerId) {
      params.push({ type: 'member', encryptedId: visitLoggingDataObject.consumerId });
    }
    if (visitLoggingDataObject.providerId) {
      params.push({ type: 'provider', encryptedId: visitLoggingDataObject.providerId });
    }
    return params;
  }
}

export default ServerLogger;
