import AWSDKAgendaItemFollowUp from './awsdk_agenda_item_follow_up';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKProvider from '../provider/awsdk_provider';
import AWSDKProviderType from '../provider/awsdk_provider_type';

/**
 * This Entity represents the details of an online follow up in detail
 * and is more specific than the {@link model.AWSDKAgendaItemFollowUp|AWSDKAgendaItemFollowUp}
 * @property {model.AWSDKProviderType} assignedProviderType The type or specialty of the provider being recommended for a follow up, exclusive of the assigned provider being set
 * @property {model.AWSDKProvider} assignedProvider Provider that the consumer was referred to have a visit with
 * @property {model.AWSDKProvider} referringProvider Provider that had the original visit with the consumer that did the referring
 * @property {string} notes These are the topic notes provided by the referringProvider
 * @property {Boolean} resolved Boolean true if the online visit follow-up has been resolved, false otherwise
 * @property {Date} createdDate The date this online visit follow-up was created
 * @extends model.AWSDKAgendaItemFollowUp
 * @since 3.0.0
 */
class AWSDKOnlineVisitFollowUpItem extends AWSDKAgendaItemFollowUp {
  get assignedProviderType() {
    return GenericParser.parseObject(this.__data.assignedProviderType, AWSDKProviderType);
  }

  get assignedProvider() {
    return GenericParser.parseObject(this.__data.assignedProvider, AWSDKProvider);
  }

  get referringProvider() {
    return GenericParser.parseObject(this.__data.referringProvider, AWSDKProvider);
  }

  get notes() {
    return this.__data.notes;
  }

  get resolved() {
    return GenericParser.parseBoolean(this.__data.resolved);
  }

  /**
   * @since 3.2.0
   */
  get createdDate() {
    return GenericParser.parseDate(this.__data.createdDate);
  }
}
export default AWSDKOnlineVisitFollowUpItem;
