import AWSDKEntity from '../awsdk_entity';
import AWSDKAddress from '../awsdk_address';
import GenericParser from '../../internal/parser/generic_parser';

/**
 * This class contains all necessary information for representing a pharmacy.
 * @property {String} type the pharmacy type
 * @property {String} phone the pharmacy's phone number
 * @property {String} formattedPhone the pharmacy's formatted phone number
 * @property {String} fax the pharmacy's fax number
 * @property {String} formattedFax the pharmacy's formatted fax number
 * @property {String} email the pharmacy's email address
 * @property {model.AWSDKAddress} address the pharmacy's street address
 * @property {Boolean} isActive boolean true if the pharmacy is available for selection
 * @property {Number} longitude the longitude of the pharmacy's location
 * @property {Number} latitude the latitude of the pharmacy's location
 * @property {String} name the name of the pharmacy
 * @property {number} distance the distance in miles from the user's "Search-Near" location to this pharmacy. This property is only returned when
 * this object was returned through the {@link service.PharmacyService.getPharmaciesByLongitudeAndLatitude()|PharmacyService.getPharmaciesByLongitudeAndLatitude}
 * function, and will be null otherwise.
 * @extends model.AWSDKEntity
 */
class AWSDKPharmacy extends AWSDKEntity {
  get type() {
    return this.__data.type;
  }

  get phone() {
    return this.__data.phone;
  }

  get formattedPhone() {
    return this.__data.formattedPhone;
  }

  get fax() {
    return this.__data.fax;
  }

  /**
   * @since 3.0.0
   * @type {string}
   */
  get formattedFax() {
    return this.__data.formattedFax;
  }

  get email() {
    return this.__data.email;
  }

  get address() {
    return this.__data.address ? GenericParser.parseObject(this.__data.address, AWSDKAddress) : null;
  }

  get isActive() {
    return GenericParser.parseBoolean(this.__data.isActive);
  }

  get longitude() {
    return this.__data.longitude;
  }

  get latitude() {
    return this.__data.latitude;
  }

  get name() {
    return this.__data.name;
  }

  /**
   * @since 2.8.0
   */
  get distance() {
    return this.__data.distance || null;
  }
}
export default AWSDKPharmacy;
