import AWSDKEntity from '../awsdk_entity';
import AWSDKPracticeSubCategory from './awsdk_practice_sub_category';
import GenericParser from '../../internal/parser/generic_parser';
/**
 * The AWSDKPracticeCategory class represents a practice category object
 * @property {String} title the title of the practice category
 * @property {model.AWSDKPracticeSubCategory[]} subCategories an array of {@link model.AWSDKPracticeSubCategory|AWSDKPracticeSubCategory}
 * @extends model.AWSDKEntity
 */
class AWSDKPracticeCategory extends AWSDKEntity {
  get title() {
    return this.__data.title;
  }

  get subCategories() {
    return this.__data.subCategories.map(item => GenericParser.parseObject(item, AWSDKPracticeSubCategory));
  }
}

export default AWSDKPracticeCategory;
