import AWSDKTrackerUnitOfMeasure from './awsdk_tracker_unit_of_measure';
import GenericParser from '../../internal/parser/generic_parser';

/**
 * This class represents a single data point in terms of a tracker measurement.
 * @property {Date} measurementDate the date that this measurement was taken
 * @property {Number} value the value that was registered by this measurement
 * @since 1.4.0
 * @extends model.AWSDKUnitOfMeasure
 */
class AWSDKTrackerDataPoint extends AWSDKTrackerUnitOfMeasure {
  get measurementDate() {
    return GenericParser.parseDate(this.__data.measurementDate);
  }
  get value() {
    return this.__data.value;
  }
}
export default AWSDKTrackerDataPoint;
