import AWSDKDataObject from '../awsdk_dataobject';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKUserType from '../../awsdk_user_type';

/**
 * Describes the recipient of a video invitation and the type of user that issued the invitation
 * @property {String} email the email of the person invited
 * @property {model.AWSDKUserType} inviter the {@link model.AWSDKUserType|AWSDKUserType} of the user that issued the invitation

 * @since 1.1.0
 * @extends model.AWSDKDataObject
 */
class AWSDKVideoInvitation extends AWSDKDataObject {
  get email() {
    return this.__data.email;
  }

  get inviter() {
    return GenericParser.parseEnum(this.__data.inviter, AWSDKUserType);
  }
}
export default AWSDKVideoInvitation;
