import GenericParser from '../../internal/parser/generic_parser';
import AWSDKAddress from '../awsdk_address';
import AWSDKTopic from './awsdk_topic';
import AWSDKChatReport from '../chat/awsdk_chat_report';
import AWSDKIntake from './awsdk_intake';
import AWSDKDataObject from '../awsdk_dataobject';
import AWSDKVisitScheduled from './awsdk_visit_schedule';
import AWSDKProvider from '../provider/awsdk_provider';
import AWSDKProviderEntries from './awsdk_provider_entries';
import AWSDKPharmacy from '../pharmacy/awsdk_pharmacy';
import AWSDKConsumer from '../consumer/awsdk_consumer';
import AWSDKTriageQuestion from './awsdk_triage_question';
import AWSDKDisposition from './awsdk_disposition';
import AWSDKEndReason from './awsdk_end_reason';
import AWSDKVisitCost from '../visit/awsdk_visit_cost';
import AWSDKModality from '../awsdk_modality';
import AWSDKEntityId from '../../internal/model/awsdk_entity_id';
import AWSDKPractice from '../practice/awsdk_practice';

/**
 * A detailed representation of an {@link model.AWSDKVisit|AWSDKVisit} that has already occurred.
 * @property {String} title the title of the visitReport
 * @property {String[]} acceptedDisclaimers an array of string names of the disclaimers the consumer has accepted
 * @property {String} transferredFromProviderName the name of the provider this consumer was transferred from
 * @property {String} transferredFromSpecialty the specialty of the provider this consumer was transferred from
 * @property {String} transferNote the transfer note
 * @property {model.AWSDKAddress} consumerBillingAddress the consumer's billing address
 * @property {String} paymentType the payment type
 * @property {Number} paymentAmount the payment amount
 * @property {String} couponCode the coupon code applied to the visit
 * @property {model.AWSDKTopic[]} consumerSummaryTopics the consumer summary topics picked for the visit
 * @property {Number} visitRating the visit rating
 * @property {Number} providerRating the provider rating
 * @property {model.AWSDKChatReport} chatReport the chat report for the visit
 * @property {model.AWSDKIntake} intake the intake for the visit
 * @property {model.AWSDKVisitScheduled} schedule the schedule of the visit
 * @property {model.AWSDKProvider} assignedProvider the assigned provider of the visit
 * @property {model.AWSDKTriageQuestion[]} triageQuestions the triage questions associated with the visit
 * @property {model.AWSDKProviderEntries} providerEntries the provider entries associated with the visit
 * @property {model.AWSDKPharmacy} pharmacy the pharmacy associated with the visit
 * @property {model.AWSDKConsumer} consumer the consumer associated with the visit
 * @since 1.4.0
 * @property {model.AWSDKVisitCost} visitCost contains details about the visit's cost
 * @since 1.4.0
 * @property {Boolean} isSuppressChargeTimeout indicates if the associated visit has a Suppress Payment cost
 * @since 1.3.0
 * @property {model.AWSDKDisposition} disposition  the disposition of the visit
 * @since 1.3.0
 * @property {model.AWSDKEndReason} endReason the reason for ending the visit
 * @since 1.4.0
 * @property {model.AWSDKModality} modality represents the modality associated with the visit @since 1.2.0
 * @extends model.AWSDKDataObject
 */
class AWSDKVisitReportDetail extends AWSDKDataObject {
  get title() {
    return this.__data.title;
  }

  get acceptedDisclaimers() {
    return this.__data.acceptedDisclaimers;
  }

  get transferredFromProviderName() {
    return this.__data.transferredFromProviderName;
  }

  get transferredFromSpecialty() {
    return this.__data.transferredFromSpecialty;
  }

  get transferNote() {
    return this.__data.transferNote;
  }

  get consumerBillingAddress() {
    return GenericParser.parseObject(this.__data.memberBillingAddress, AWSDKAddress);
  }

  get paymentType() {
    return this.__data.paymentType;
  }

  get paymentAmount() {
    return this.__data.paymentAmount;
  }

  get couponCode() {
    return this.__data.couponCode;
  }

  get consumerSummaryTopics() {
    this.__data.memberSummaryTopics.map(item => GenericParser.parseObject(item, AWSDKTopic));
  }

  get visitRating() {
    return this.__data.visitRating;
  }

  get providerRating() {
    return this.__data.providerRating;
  }

  get chatReport() {
    return GenericParser.parseObject(this.__data.chatReport, AWSDKChatReport);
  }

  get intake() {
    return GenericParser.parseObject(this.__data.intake, AWSDKIntake);
  }

  get schedule() {
    return GenericParser.parseObject(this.__data.schedule, AWSDKVisitScheduled);
  }

  get assignedProvider() {
    return GenericParser.parseObject(this.__data.assignedProvider, AWSDKProvider);
  }

  get triageQuestions() {
    if (this.__data.triageQuestions && Array.isArray(this.__data.triageQuestions)) {
      return this.__data.triageQuestions.map((question, index) => {
        const triageQuestion = new AWSDKTriageQuestion();
        triageQuestion.question = question;
        triageQuestion.answer = this.__data.triageAnswers && this.__data.triageAnswers[index];
        return triageQuestion;
      });
    }
    return [];
  }

  get providerEntries() {
    return GenericParser.parseObject(this.__data.providerEntries, AWSDKProviderEntries);
  }

  get pharmacy() {
    return GenericParser.parseObject(this.__data.pharmacy, AWSDKPharmacy);
  }

  get consumer() {
    return GenericParser.parseObject(this.__data.member, AWSDKConsumer);
  }

  get disposition() {
    return GenericParser.parseEnum(this.__data.disposition, AWSDKDisposition);
  }

  get endReason() {
    return GenericParser.parseEnum(this.__data.endReason, AWSDKEndReason);
  }

  /**
   * @since 1.4.0
   */
  get visitCost() {
    return GenericParser.parseObject(this.__data.cost, AWSDKVisitCost);
  }

  /**
   * @since 1.4.0
   */
  get isSuppressChargeTimeout() {
    return GenericParser.parseBoolean(this.__data.member && this.__data.member.subscription && this.__data.member.subscription.healthPlan.payerInfo.suppressCharge &&
      this.__data.cost && !this.__data.cost.zeroCostVisit &&
      this.__data.cost.eligibilityRequestError === 'CONNECTION_TIMEOUT' && this.__data.cost.costCalculationStatus === 'FINISHED');
  }

  /**
   * @since 1.4.0
   */
  get modality() {
    return GenericParser.parseObject(this.__data.modality, AWSDKModality);
  }

  /**
   * The practice associated with this visit report details
   * @type {model.AWSDKPractice}
   * @since 2.3.0
   */
  get practice() {
    return GenericParser.parseObject(this.__data.practice, AWSDKPractice);
  }

  /**
   * @private
   */
  __getEngagementId() {
    return GenericParser.parseObject(this.__data.engagementId, AWSDKEntityId);
  }
}
export default AWSDKVisitReportDetail;
