import AWSDKPlatformType from '../../../model/awsdk_platform_type';
import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';

/**
 * This class represents a wrapper around the response obtained from getting a list of supported platformTypes from the Telehealth platform
 * @property {model.AWSDKPlatformType[]} platformTypes an array of {@link model.AWSDKPlatformType|AWSDKPlatformType}
 * @extends model.AWSDKResponse
 */
class AWSDKPlatformTypeResponse extends AWSDKResponse {
  get platformTypes() {
    return Array.isArray(this.__data.platformTypes) ? this.__data.platformTypes.map(entry => GenericParser.parseObject(entry, AWSDKPlatformType)) : [];
  }
}
export default AWSDKPlatformTypeResponse;
