import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';
import AWSDKFirstAvailableData from '../../../model/visit/awsdk_first_available_data';

/**
 * Wrapper around an {@link model.AWSDKFirstAvailableData}
 * @property {model.AWSDKFirstAvailableData} firstAvailableData the {@link model.AWSDKFirstAvailableData}
 * @since 1.1.0
 * @extends model.AWSDKResponse
 */
class AWSDKFirstAvailableResponse extends AWSDKResponse {
  get firstAvailableData() {
    return this.__data.status ? GenericParser.parseObject(this.__data.status, AWSDKFirstAvailableData) : null;
  }
}

export default AWSDKFirstAvailableResponse;
