import AWSDKResponse from './awsdk_response';
import AWSDKCountry from '../../../model/awsdk_country';
import GenericParser from '../../parser/generic_parser';

class AWSDKCountriesResponse extends AWSDKResponse {
  get countries() {
    return this.__data.countries.map(item => GenericParser.parseObject(item, AWSDKCountry));
  }
}

export default AWSDKCountriesResponse;
