import AWSDKDataObject from '../awsdk_dataobject';
/**
 * This class represents a placeholder for payment (credit card) information for updating a consumer's payment method on file
 * @extends model.AWSDKDataObject
 * @property {String} nameOnCard the name of the credit card holder. This is the {@link model.AWSDKConsumer|AWSDKConsumer} whose payment information we are updating
 * @property {String} creditCardNumber the (new) credit card number
 * @property {String} creditCardZip the zip (postal) code associated with the credit card
 * @property {String} creditCardSecCode the security code of the credit card
 * @property {String} address1 the street address associated with this {@link model.AWSDKConsumer|AWSDKConsumer}'s credit card account
 * @property {String} address2 additional address information associated with the {@link model.AWSDKConsumer|AWSDKConsumer} credit card account
 * @property {String} city the city name associated with the {@link model.AWSDKConsumer|AWSDKConsumer}'s credit card account
 * @property {String} state the state name associated with the {@link model.AWSDKConsumer|AWSDKConsumer}'s credit card account
 * @property {String} country the country name associated with the {@link model.AWSDKConsumer|AWSDKConsumer}'s credit card account
 * @property {Number} creditCardMonth the expiration month for this credit card
 * @property {Number} creditCardYear the expiration year for this credit card
 */
class AWSDKPaymentRequest extends AWSDKDataObject {
  get nameOnCard() {
    return this.__data.nameOnCard;
  }

  set nameOnCard(nameOnCard) {
    this.__data.nameOnCard = nameOnCard;
  }

  get creditCardNumber() {
    return this.__data.creditCardNumber;
  }

  set creditCardNumber(creditCardNumber) {
    this.__data.creditCardNumber = creditCardNumber;
  }

  get creditCardZip() {
    return this.__data.creditCardZip;
  }

  set creditCardZip(creditCardZip) {
    this.__data.creditCardZip = creditCardZip;
  }

  get creditCardSecCode() {
    return this.__data.creditCardSecCode;
  }

  set creditCardSecCode(creditCardSecCode) {
    this.__data.creditCardSecCode = creditCardSecCode;
  }

  get address1() {
    return this.__data.address1;
  }

  set address1(address1) {
    this.__data.address1 = address1;
  }

  get address2() {
    return this.__data.address2;
  }

  set address2(address2) {
    this.__data.address2 = address2;
  }

  get city() {
    return this.__data.city;
  }

  set city(city) {
    this.__data.city = city;
  }

  get country() {
    return this.__data.country;
  }

  set country(country) {
    this.__data.country = country;
  }

  get state() {
    return this.__data.state;
  }

  set state(state) {
    this.__data.state = state;
  }

  get creditCardMonth() {
    return parseInt(this.__data.creditCardMonth, 10);
  }

  set creditCardMonth(creditCardMonth) {
    this.__data.creditCardMonth = creditCardMonth;
  }

  get creditCardYear() {
    return parseInt(this.__data.creditCardYear, 10);
  }

  set creditCardYear(creditCardYear) {
    this.__data.creditCardYear = creditCardYear;
  }
}
export default AWSDKPaymentRequest;

