import AWSDKEntity from '../../model/awsdk_entity';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKDeviceIntegrationMode from '../../model/device_integration/awsdk_device_integration_mode';

/**
 * The AWSDKPracticeBase is the base class for all Practice objects
 * @property {String} name the name of the Practice
 * @property {String} practiceType the type of practice that this Practice refers to
 * @property {Boolean} acceptInsuranceEnabled boolean true if the insurance details should be shown to users of this practice, false otherwise.
 * @property {Boolean} screenSharingEnabled boolean TRUE if this practice allows screen sharing, FALSE otherwise.
 * @property {model.AWSDKDeviceIntegrationMode} deviceIntegrationMode represents the mode of the device integration to be used for the practice. @since 2.9.0
 * @extends model.AWSDKEntity
 */
class AWSDKPracticeBase extends AWSDKEntity {
  get href() {
    return this.__data.href;
  }

  get name() {
    return this.__data.name;
  }

  get practiceType() {
    return this.__data.practiceType;
  }

  get acceptInsuranceEnabled() {
    return GenericParser.parseBoolean(this.__data.acceptInsuranceEnabled);
  }

  /**
   * @type {Boolean}
   * @since 3.1.0
   */
  get screenSharingEnabled() {
    return GenericParser.parseBoolean(this.__data.screenSharingEnabled);
  }

  /**
   * @since 2.9.0
   */
  get deviceIntegrationMode() {
    return GenericParser.parseEnum(this.__data.deviceIntegrationMode, AWSDKDeviceIntegrationMode);
  }
}
export default AWSDKPracticeBase;
