import Validator from './../validator/validator';
import Logger from '../logger/logger';

const he = require('he');

const log = new Logger();

class Util {
  /**
   * Finds the url on a link whose name attribute matches that of one of the link names in the array or returns null if no name matches. <br>
   * @param {Array} links an array of links {name: 'some', url: 'someUrl', method: 'GET'}
   * @param {String} linkName the name attribute we're trying to match
   * @returns {String} the url for the matching link, or null if no link has a name that matches the one specified.
   */
  static findNamedLinkUrl(links, linkName) {
    if (links == null || !Array.isArray(links) || links.length === 0) {
      return null;
    }
    const link = links.find(item => item.name === linkName);
    if (Validator.isValidLink(link)) {
      return link.url;
    }
    return null;
  }

  /**
   * Format a date in ISO 8601 Calendar date format (YYYY-MM-DD).
   * @param {Date} date The date to be formatted.
   * @returns {String} the date formatted to YYYY-MM-DD.
   */
  static formatISODate(date) {
    if (!(date instanceof Date)) {
      return null;
    }
    return date.toISOString().substr(0, 10);
  }

  static formatISODateTime(date) {
    if (!(date instanceof Date)) {
      return null;
    }
    const isoDateTimeString = date.toISOString().substr(0, 19);
    return `${isoDateTimeString}Z`;
  }

  static b64DecodeUnicode(str) {
    try {
      // chat messages are delivered to the client in base64 encoded format.
      // we need to decode them on the browser before showing them to clients.
      // atob alone doesnt work for non ascii characters, so we need this workaround.
      const decodeString = Util.bin2String(Util.decode(Util.urlSafeDecoding(str)));
      return he.decode(Util.base64DecodeUnicodeURI(decodeString));
    } catch (e) {
      // account for the possibility of chat messages being undecodable on the client side.
      log.error('Util.b64DecodeUnicode', 'Error', e);
      return '';
    }
  }

  static urlSafeDecoding(str) {
    const newStr = (`${str}===`).slice(0, str.length + (str.length % 4));
    return newStr.replace(/-/g, '+').replace(/_/g, '/');
  }

  static bin2String(array) {
    let result = '';
    for (let i = 0; i < array.length; i += 1) {
      result += String.fromCharCode(parseInt(array[i], 10));
    }
    return result;
  }

  static base64DecodeUnicodeURI(str) {
    return decodeURIComponent(Array.prototype.map.call(str, c => `%${(`00${c.charCodeAt(0).toString(16)}`).slice(-2)}`).join(''));
  }

  static decode(str) {
    /* eslint-disable no-plusplus */
    /* eslint-disable no-bitwise */
    const tab = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/';
    const p = '=';
    const s = str.split('');
    const out = [];
    let l = s.length;
    /* eslint-disable no-empty */
    while (s[--l] === p) { } // strip off trailing padding
    for (let i = 0; i < l;) {
      let t = tab.indexOf(s[i++]) << 18;
      if (i <= l) { t |= tab.indexOf(s[i++]) << 12; }
      if (i <= l) { t |= tab.indexOf(s[i++]) << 6; }
      if (i <= l) { t |= tab.indexOf(s[i++]); }
      out.push((t >>> 16) & 0xff);
      out.push((t >>> 8) & 0xff);
      out.push(t & 0xff);
    }
    // strip off any null bytes
    while (out[out.length - 1] === 0) { out.pop(); }
    return out;
  }
  /**
   * Utility method for understanding the operating system on which the user is interacting with the SDK.
   *
   * @since 1.2.1
   * @returns {boolean} Indicating that the user is using a mac computer.
   */
  static isMac() {
    return navigator.platform.toUpperCase().indexOf('MAC') >= 0;
  }
  /**
   * Utility method for understanding the operating system on which the user is interacting with the SDK.
   *
   * @since 1.2.1
   * @returns {boolean} Indicating that the user is using a Windows computer.
   */
  static isWindows() {
    return navigator.platform.toUpperCase().indexOf('WIN') >= 0;
  }

  /**
   * Utility method for determining the client's ClientMetrics.NetworkType - one of WAN_UNKNOWN, WAN_EDGE_OR_1X, WAN_3G, WAN_LTE, or WIFI -
   * using the limited and sparsely-supported Network API.
   *
   * Note that even among browsers that do support the API, the support applies only to mobile versions of that browser.
   * Desktop browsers do not expose the 'type' property of the NetworkInformation interface, instead offering only the estimated 'effectiveType'.
   *
   * See http://wicg.github.io/netinfo/#dfn-connection-type for specification as of 9/1/18.
   * @returns {'WIFI'|null} either 'WIFI' or null
   */
  static getNetworkType() {
    const normalize = (type) => {
      const lowered = type && (typeof type.toLowerCase === 'function') ? type.toLowerCase() : '';
      switch (lowered) {
        case 'wifi':
          return 'WIFI';
        case 'wimax':
        case 'other':
        case 'unknown':
        case 'bluetooth':
        case 'cellular':
        case 'ethernet':
        default:
          return null;
      }
    };
    if (window && window.navigator && window.navigator.connection) {
      return normalize(window.navigator.connection.type);
    }
    return null;
  }

  /**
   * @private
   * Adding this method here from sample_app to unit test it.
   */
  static __truncateForDisplay(textToDisplay, maxLen) {
    Number.isInteger = Number.isInteger || (value => typeof value === 'number' && isFinite(value) && Math.floor(value) === value);
    if (typeof textToDisplay !== 'string' || !Number.isInteger(maxLen) || maxLen < 0 || maxLen >= textToDisplay.length) {
      return textToDisplay;
    }
    if (maxLen <= 3) { // don't add ellipsis if 3 or less chars
      return textToDisplay.substring(0, maxLen);
    }
    return textToDisplay.substring(0, maxLen - 3).concat('...');
  }

  /**
   * @private
   */
  static __concatNames(firstName, lastName, middleName, separator, direction) {
    const sep = separator || ' ';
    const res = [];
    if (middleName) res.push(middleName);
    if (direction !== 'rtl') {
      res.unshift(firstName);
      res.push(lastName);
    } else {
      res.unshift(lastName);
      res.push(firstName);
    }
    return res.join(sep);
  }
}
export default Util;
