import AWSDKError from '../../error/awsdk_error';
import AWSDKTrackerTemplate from './awsdk_tracker_template';
import Validator from '../../internal/validator/validator';


/**
 * This class represents the search criteria for finding trackers.
 * Get an instance of this via {@link service.ConsumerService#getNewTrackersSearchCriteria()|PracticeService.getNewTrackersSearchCriteria}
 * @property {model.AWSDKTrackerTemplate} criteria.trackerTemplate the tracker template associated with this search criteria
 * @property {String} criteria.timeZone the consumer's canonical time zone to ensure correct dates are used. This needs to be a canonical id as define in {@link http://joda-time.sourceforge.net/timezones.html}. For example: 'America/New_York'
 * @property {Date} [criteria.startDate] the datetime to start the search from
 * @property {Date} [criteria.endDate] the datetime to terminate the search at
 * @since 1.4.0
 */
class AWSDKTrackersSearchCriteria {
  constructor(criteria = {}) {
    this.trackerTemplate = criteria.trackerTemplate;
    this.timeZone = criteria.timeZone;
    this.startDate = criteria.startDate;
    this.endDate = criteria.endDate;
  }
  get trackerTemplate() {
    return this.__trackerTemplate;
  }
  get timeZone() {
    return this.__timeZone;
  }
  get startDate() {
    return this.__startDate;
  }
  get endDate() {
    return this.__endDate;
  }
  set trackerTemplate(trackerTemplate) {
    if (trackerTemplate && !(trackerTemplate instanceof AWSDKTrackerTemplate)) {
      throw AWSDKError.AWSDKIllegalArgument('trackerTemplate must be of type AWSDKTrackerTemplate');
    }
    this.__trackerTemplate = trackerTemplate;
  }
  set timeZone(timeZone) {
    if (timeZone && (typeof timeZone !== 'string' || !Validator.isValidString(timeZone))) {
      throw AWSDKError.AWSDKIllegalArgument('timeZone must be of type string');
    }
    this.__timeZone = timeZone;
  }
  set startDate(startDate) {
    if (startDate && !(startDate instanceof Date)) {
      throw AWSDKError.AWSDKIllegalArgument('startDate must be an instance of Date');
    }
    this.__startDate = startDate;
  }
  set endDate(endDate) {
    if (endDate && !(endDate instanceof Date)) {
      throw AWSDKError.AWSDKIllegalArgument('endDate must be an instance of Date');
    }
    this.__endDate = endDate;
  }
  /**
   * @private
   */
  __uuid() {
    return (this.__trackerTemplate && this.__trackerTemplate.uuid) || null;
  }
}
export default AWSDKTrackersSearchCriteria;
