import AWSDKMessageDraft from './awsdk_message_draft';
import AWSDKMessageActionType from './awsdk_message_action_type';

/**
 * This class contains all necessary information for representing a reply message draft.
 * Get an instance of this via {@link service.SecureMessageService#getReplyMessageDraft|SecureMessageService.getReplyMessageDraft}
 * @since 1.1.0
 * @extends model.AWSDKMessageDraft
 */
class AWSDKReplyMessageDraft extends AWSDKMessageDraft {
  constructor(sourceMessage) {
    super();
    this.__actionType = AWSDKMessageActionType.Reply;
    this.__recipientEncryptedId = sourceMessage.sender.id.encryptedId;
    this.__recipientName = sourceMessage.sender.fullName;
    this.__topicType = sourceMessage.topicType;
    this.__sourceMessageId = sourceMessage.id.encryptedId;
    this.__sourceMessageType = sourceMessage.messageType;
    this.__subject = sourceMessage.subject;
    this.__body = sourceMessage.body;
  }
}
export default AWSDKReplyMessageDraft;

