import AWSDKDataObject from '../awsdk_dataobject';

/**
 * This class contains information used to set an appointment reminder.
 * @property {String} label the label for the Reminder Option
 * @property {String} value the value for the Reminder Option.
 * @property {Number} minutes the number of minutes before the appointment to sent the reminder.
 * @extends model.AWSDKDataObject
 */
class AWSDKReminderOption extends AWSDKDataObject {
  get label() {
    return this.__data.label;
  }
  get value() {
    return this.__data.value;
  }
  get minutes() {
    return this.__data.minutes;
  }
}
export default AWSDKReminderOption;
