import AWSDKAddress from '../awsdk_address';
import AWSDKDataObject from '../awsdk_dataobject';
import GenericParser from '../../internal/parser/generic_parser';

/**
 *
 * This class represents details of a {@link model.AWSDKPostVisitFollowUpItem|AWSDKPostVisitFollowUpItem}
 * @property {String} facilityName the name of a medical facility that a consumer is referred to
 * @property {String} providerName the name of a provider that a consumer is referred to
 * @property {String} phone the phone number to contact the facility that a consumer is referred to
 * @property {model.AWSDKAddress} address the address of the facility that a consumer was referred to
 * @extends model.AWSDKDataObject
 * @since 3.0.0
 */
class AWSDKPostVisitFollowUpDetail extends AWSDKDataObject {

  /**
   * Name of the facility that the consumer is being referred to.
   */
  get facilityName() {
    return this.__data.facilityName;
  }

  /**
   * Name of the provider that the consumer is being referred to.
   */
  get providerName() {
    return this.__data.providerName;
  }

  /**
   * Phone number for the facility tha the consumer is being referred to.
   */
  get phone() {
    return this.__data.phone;
  }

  /**
   * Address of the facility that the consumer is being referred to.
   */
  get address() {
    return GenericParser.parseObject(this.__data.address, AWSDKAddress);
  }
}
export default AWSDKPostVisitFollowUpDetail;
