import AWSDKBaseConsumerUpdate from '../consumer/awsdk_base_consumer_update';

/**
 * Contains data needed to register a new {@link model.AWSDKConsumer|AWSDKConsumer} dependent <br>
 * Get an instance of this via {@link service.ConsumerService#getNewDependentRegistration()|ConsumerService.getNewDependentRegistration}
 * @extends model.AWSDKBaseConsumerUpdate
 */
class AWSDKDependentRegistration extends AWSDKBaseConsumerUpdate {
  set performDependentCheck(performDependentCheck) {
    this.__data.performDependentCheck = performDependentCheck;
  }

  /**
   * TRUE indicating that there should be a check for existence of the {@link model.AWSDKDependent|AWSDKDependent} we're trying to register. FALSE otherwise.
   * @type {Boolean}
   * @since 2.0.0
   */
  get performDependentCheck() {
    return this.__data.performDependentCheck;
  }

  /**
   * @private
   */
  set encryptedMemberId(encryptedMemberId) {
    this.__data.encryptedMemberId = encryptedMemberId;
  }

  /**
   * @private
   */
  get encryptedMemberId() {
    return this.__data.encryptedMemberId;
  }

  /**
   * @private
   */
  __toRequestObject(systemConfiguration) {
    const request = super.__toRequestObject(systemConfiguration);
    request.performDependentCheck = !!this.__data.performDependentCheck;
    request.memberId = this.__data.encryptedMemberId;
    return request;
  }

  /**
   * @private
   */
  __toRequestBody(systemConfiguration) {
    return JSON.stringify(this.__toRequestObject(systemConfiguration));
  }
}
export default AWSDKDependentRegistration;
