/**
 * The base class for all data objects in AWSDK
 */
class AWSDKDataObject {
  constructor(data = {}) {
    this.__data = data;
  }

  toString() {
    const dataString = JSON.stringify(this.__data);
    return dataString;
  }
}

export default AWSDKDataObject;
