import AWSDKProvider from '../provider/awsdk_provider';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKDataObject from '../awsdk_dataobject';
import Util from '../../internal/util/util';
import AWSDKFirstAvailableStatus from './awsdk_first_available_status';

/**
 * Encompasses data associated with a {@link model.AWSDKConsumer}'s request for a first-available visit
 * @property {model.AWSDKFirstAvailableStatus} status the status of the first available search
 * @property {model.AWSDKProvider} provider the {@link model.AWSDKProvider} that has accepted the consumer's request, if any
 * @property {Number} firstAvailableProviderAskCount the number of providers involved in the first available search
 * @property {Boolean} isMatchmaking whether the first-available request is the result of matchmaking
 * @property {Date} firstAvailableSearchStartTime when the first available search was started
 * @property {Date} firstAvailableSearchEndTime when the first available search completed

 * @since 1.1.0
 * @extends model.AWSDKDataObject
 */
class AWSDKFirstAvailableData extends AWSDKDataObject {
  get status() {
    return GenericParser.parseEnum(this.__data.status, AWSDKFirstAvailableStatus);
  }

  get provider() {
    return GenericParser.parseObject(this.__data.provider, AWSDKProvider);
  }

  get firstAvailableProviderAskCount() {
    return this.__data ? this.__data.firstAvailableProviderAskCount : null;
  }

  get isMatchmaking() {
    return GenericParser.parseBoolean(this.__data.matchmaking);
  }

  get firstAvailableSearchStartTime() {
    return this.__data.firstAvailableSearchStartTime;
  }

  getFormattedStartTime() {
    return this.__data.firstAvailableSearchStartTime ? Util.formatISODateTime(this.__data.firstAvailableSearchStartTime) : '';
  }

  set firstAvailableSearchStartTime(firstAvailableSearchStartTime) {
    this.__data.firstAvailableSearchStartTime = firstAvailableSearchStartTime;
  }

  get firstAvailableSearchEndTime() {
    return this.__data.firstAvailableSearchEndTime;
  }

  getFormattedEndTime() {
    return this.__data.firstAvailableSearchEndTime ? Util.formatISODateTime(this.__data.firstAvailableSearchEndTime) : '';
  }

  set firstAvailableSearchEndTime(firstAvailableSearchEndTime) {
    this.__data.firstAvailableSearchEndTime = firstAvailableSearchEndTime;
  }
}

export default AWSDKFirstAvailableData;
