import AWSDKEntity from '../../../model/awsdk_entity';

import GenericParser from '../../parser/generic_parser';
import AWSDKConferenceMetrics from './awsdk_conference_metrics';
import AWSDKConferenceModalityType from './awsdk_conference_modality_type';
import AWSDKConferenceParticipant from './awsdk_conference_participant';

/**
 * Contains details video details for a telehealth visit.
 * @property {Date} addDate The date the the telehealth visit was started.
 * @property {number} accessCode The accessCode required for the telehealth visit.
 * @property {string} uuid Unique identifier for this telehealth visit.
 * @property {string} roomId The id for the telehealth visit room.
 * @property {string} serviceURL Url for the telehealth visit video service used.
 * @property {boolean} isActive Indicates if the telehealth visit is currently active.
 * @property {model.AWSDKConferenceMetrics} metrics the {@link model.AWSDKConferenceMetrics|AWSDKConferenceMetrics} associated with the conference.
 * @property {model.AWSDKConferenceModalityType} selectedModalityType the {@link model.AWSDKConferenceModalityType|AWSDKConferenceModalityType} of the conference.
 * @property {model.AWSDKConferenceParticipant[]} participants the list of {@link model.AWSDKConferenceParticipant|AWSDKConferenceParticipant} in the conference.
 * @private
 * @extends model.AWSDKEntity
 */
class AWSDKConferenceDetails extends AWSDKEntity {
  get addDate() {
    return GenericParser.parseDate(this.__data.addDate);
  }

  get accessCode() {
    return this.__data.accessCode;
  }

  get uuid() {
    return this.__data.uuid;
  }

  get roomId() {
    return this.__data.roomId;
  }

  get serviceURL() {
    return this.__data.serviceURL;
  }

  get isActive() {
    return GenericParser.parseBoolean(this.__data.isActive);
  }

  get metrics() {
    return GenericParser.parseObject(this.__data.metrics, AWSDKConferenceMetrics);
  }

  get selectedModalityType() {
    return GenericParser.parseEnum(this.__data.selectedModalityType, AWSDKConferenceModalityType);
  }

  get participants() {
    return (Array.isArray(this.__data.participants) && this.__data.participants.map(item => GenericParser.parseObject(item, AWSDKConferenceParticipant))) || [];
  }
}
export default AWSDKConferenceDetails;
