import GenericParser from '../../internal/parser/generic_parser';
import Util from '../../internal/util/util';

/**
 * This class contains all necessary information for representing an exam's data request.
 * @property {String} id the exam data Id
 * @property {String} type the exam data type
 * @property {Date} dateTime the exam date and time
 * @since 1.4.0
 */
class AWSDKExamDataRequest {
  get id() {
    return this.__id;
  }

  set id(id) {
    this.__id = id;
  }

  get type() {
    return this.__type;
  }

  set type(type) {
    this.__type = type;
  }

  get dateTime() {
    return GenericParser.parseDate(this.__dateTime);
  }

  set dateTime(dateTime) {
    this.__dateTime = dateTime;
  }
  /**
   * This method is a convenience method for presenting this object as an array element within a JSON string
   * @returns {Object} an array element representation of this object suitable to be used as an element on a request that adds a device integration exam data
   */
  __toRequestElement() {
    return {
      id: this.id,
      type: this.type,
      dateTime: Util.formatISODateTime(this.dateTime),
    };
  }
}
export default AWSDKExamDataRequest;
