import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';
import AWSDKTopicType from '../../../model/awsdk_topic_type';
import AWSDKSentMessages from '../../../model/secure_message/awsdk_sent_messages';
import AWSDKSentMessage from '../../../model/secure_message/awsdk_sent_message';

/**
 * Simple wrapper around an {@link model.AWSDKTopicType[]}
 * @property {model.AWSDKTopicType[]} topicTypes an array of {@link model.AWSDKTopicType}
 * @extends model.AWSDKResponse
 */
class AWSDKTopicTypesResponse extends AWSDKResponse {
  get topicTypes() {
    return this.__data.topics ? this.__data.topics.map(item => GenericParser.parseObject(item, AWSDKTopicType)) : null;
  }
}

export default AWSDKTopicTypesResponse;
