import GenericParser from '../../internal/parser/generic_parser';
import AWSDKPostVisitFollowUpItem from './awsdk_post_visit_follow_up_item';
import AWSDKPaginated from '../awsdk_paginated';

/**
 * This class represents the search result for finding a consumer's {@link model.AWSDKPostVisitFollowUpItem}s.
 * @property {model.AWSDKPostVisitFollowUpItem[]} postVisitFollowUpItems the result list of {@link model.AWSDKPostVisitFollowUpItem}s that satisfied the search query
 * @extends model.AWSDKPaginated
 * @since 2.8.0
 */
class AWSDKPaginatedPostVisitFollowUpItems extends AWSDKPaginated {
  get postVisitFollowUpItems() {
    return (Array.isArray(this.__data.list) && this.__data.list.map(item => GenericParser.parseObject(item, AWSDKPostVisitFollowUpItem))) || [];
  }
}
export default AWSDKPaginatedPostVisitFollowUpItems;
