import GenericParser from '../../parser/generic_parser';
import AWSDKAllergy from '../../../model/health/awsdk_allergy';
import AWSDKResponse from './awsdk_response';

/**
 * This class represents the response obtained from a call to {@link service.ConsumerService#getAllergies(model.AWSDKConsumer)|getAllergies(consumer)}
 * which results in an array of {@link model.AWSDKAllergy} objects.
 * @property {model.AWSDKAllergy[]} allergies the collection of allergies for a given consumer
 * @extends model.AWSDKResponse
 */
class AWSDKAllergiesResponse extends AWSDKResponse {
  get allergies() {
    return (this.__data.allergies && Array.isArray(this.__data.allergies)) ?
      this.__data.allergies.map(item => GenericParser.parseObject(item, AWSDKAllergy)) : [];
  }
}
export default AWSDKAllergiesResponse;
