import AWSDKEntity from './awsdk_entity';
import GenericParser from '../internal/parser/generic_parser';
import AWSDKDisclaimerEntityType from './awsdk_disclaimer_entity_type';
/**
 * The AWSDKDisclaimer class represents a single legal disclaimer for an entity
 * @property {model.AWSDKDisclaimerEntityType} entityType the entity type
 * @property {String} entityTypeEnum the entity type enum value
 * @property {String} title the title of this disclaimer
 * @property {String} legalText the text to be displayed in legal jargon
 * @property {Boolean} required boolean true if this is a required disclaimer, false otherwise
 * @extends model.AWSDKEntity
 */
class AWSDKDisclaimer extends AWSDKEntity {
  get entityType() {
    return GenericParser.parseEnum(this.__data.entityType, AWSDKDisclaimerEntityType);
  }

  get entityTypeEnum() {
    return this.__data.entityTypeEnum;
  }

  get title() {
    return this.__data.title;
  }

  get legalText() {
    return this.__data.legalText;
  }

  get required() {
    return GenericParser.parseBoolean(this.__data.required);
  }
}
export default AWSDKDisclaimer;
