import AWSDKBaseConsumerUpdate from './awsdk_base_consumer_update';

/**
 * AWSDKConsumerUpdate represents a data container for collecting data to update a {@link model.AWSDKConsumer|AWSDKConsumer} <br>
 * @property {String} email the Consumer's email address
 * @property {String} password the consumer's password
 * @property {String} legalResidence the consumer's current legal residence state code
 * @property {String} legalResidenceCountryCode the consumer's current legal residence country code
 * @property {String} preferredLocale the consumer's preferred locale.
 * @property {String} phone the consumer's phone number
 * @property {String} patientMrnId the consumer's medical record number id
 * @property {String} consumerAuthKey the auth key pertaining to this consumer
 * @property {String} address1 the consumer's street name or P.O box
 * @property {String} address2 the consumer's apartment, suite, or any other extra identifying field for address
 * @property {String} city the city where the consumer's address is located
 * @property {String} zipCode the consumer's zip/postal code
 * @property {String} stateCode the consumer's state code. example: MA
 * @property {String} countryCode the consumer's country code
 * @property {Boolean} isAppointmentReminderTextsEnabled set to TRUE to enable appointment reminders or FALSE to disable them. Defaults to undefined.
 * @property {String} timeZone the consumer's canonical time zone. Example: America/New_York
 * @extends model.AWSDKBaseConsumerUpdate
 */
class AWSDKConsumerUpdate extends AWSDKBaseConsumerUpdate {
  get email() {
    return this.__data.email;
  }

  get password() {
    return this.__data.password;
  }

  get legalResidence() {
    return this.__data.legalResidence;
  }

  get zipCode() {
    return this.__data.zipCode;
  }

  get address1() {
    return this.__data.address1;
  }

  get address2() {
    return this.__data.address2;
  }

  get stateCode() {
    return this.__data.stateCode;
  }

  get city() {
    return this.__data.city;
  }

  get countryCode() {
    return this.__data.countryCode;
  }

  get legalResidenceCountryCode() {
    return this.__data.legalResidenceCountryCode;
  }

  get preferredLocale() {
    return this.__data.preferredLocale;
  }

  get phone() {
    return this.__data.phone;
  }

  get patientMrnId() {
    return this.__data.patientMrnId;
  }

  get consumerAuthKey() {
    return this.__data.consumerAuthKey;
  }

  get isAppointmentReminderTextsEnabled() {
    // not using parse boolean because 'undefined' is meaningful here
    return this.__data.isAppointmentReminderTextsEnabled;
  }

  /**
   * @since 1.4.0
   */
  get timeZone() {
    return this.__data.timeZone;
  }

  set address1(address1) {
    this.__data.address1 = address1;
  }

  set address2(address2) {
    this.__data.address2 = address2;
  }

  set zipCode(zipCode) {
    this.__data.zipCode = zipCode;
  }

  set stateCode(stateCode) {
    this.__data.stateCode = stateCode;
  }

  set city(city) {
    this.__data.city = city;
  }

  set countryCode(countryCode) {
    this.__data.countryCode = countryCode;
  }

  set email(email) {
    this.__data.email = email;
  }

  set password(password) {
    this.__data.password = password;
  }

  set legalResidence(legalResidence) {
    this.__data.legalResidence = legalResidence;
  }

  set legalResidenceCountryCode(legalResidenceCountryCode) {
    this.__data.legalResidenceCountryCode = legalResidenceCountryCode;
  }

  set preferredLocale(preferredLocale) {
    this.__data.preferredLocale = preferredLocale;
  }

  set phone(phone) {
    this.__data.phone = phone;
  }

  set patientMrnId(patientMrnId) {
    this.__data.patientMrnId = patientMrnId;
  }

  set consumerAuthKey(consumerAuthKey) {
    this.__data.consumerAuthKey = consumerAuthKey;
  }

  set isAppointmentReminderTextsEnabled(isAppointmentReminderTextsEnabled) {
    this.__data.isAppointmentReminderTextsEnabled = isAppointmentReminderTextsEnabled;
  }

  /**
   * @since 1.4.0
   */
  set timeZone(timeZone) {
    this.__data.timeZone = timeZone;
  }

  /**
   * @private
   */
  __toRequestBody(systemConfiguration, ignorePropagation) {
    const request = super.__toRequestObject(systemConfiguration);

    // Server uses 'middleName' for dependents and 'middleInitial' for consumers
    request.middleInitial = request.middleName;
    request.middleName = undefined;

    request.email = this.email;
    request.currentPassword = this.password;
    request.password = this.password;
    request.currentLocationState = this.legalResidence;
    request.currentLocationCountry = this.legalResidenceCountryCode;
    request.phoneNumber = this.phone;
    request.patientMrn = this.patientMrnId;
    request.address1 = this.address1;
    request.address2 = this.address2;
    request.city = this.city;
    request.zipCode = this.zipCode;
    request.addressState = this.stateCode;
    request.addressCountry = this.countryCode;
    request.sdkApiKey = this.sdkApiKey;
    request.sdkUserAuthKey = this.consumerAuthKey;
    request.appointmentReminderTextsEnabled = this.isAppointmentReminderTextsEnabled;
    request.preferredLocale = this.preferredLocale;
    request.timeZone = this.timeZone;
    request.ignorePropagation = !!ignorePropagation;
    request.brandings = this.brandings;

    return JSON.stringify(request);
  }
}
export default AWSDKConsumerUpdate;
