import AWSDKDataObject from '../awsdk_dataobject';

/**
 * The AWSDKPracticeSearchType class represents a practice search type that can be used to filter a list of providers based on their practice type.
 * @property {String} label the localized label for this search type
 * @property {String} type the non-localized actual value of this type
 * @extends model.AWSDKDataObject
 * @since 3.0.0
 */
class AWSDKPracticeSearchType extends AWSDKDataObject {
  get label() {
    return this.__data.label;
  }

  get type() {
    return this.__data.searchType;
  }
}

export default AWSDKPracticeSearchType;
