import AWSDKProvidersAvailability from '../../../model/provider/awsdk_providers_availability';
import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';

/**
 *  Represents a response object wrapper to a single {@link model.AWSDKProvidersAvailability} object type
 * @property {model.AWSDKProvidersAvailability} providers the providers future availability
 * @extends model.AWSDKResponse
 */
class AWSDKProvidersAvailabilityResponse extends AWSDKResponse {
  get providers() {
    return GenericParser.parseObject(this.__data.providers, AWSDKProvidersAvailability);
  }
}
export default AWSDKProvidersAvailabilityResponse;
