import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';
import AWSDKDevicePairingDetails from '../../../model/device_live_streaming/awsdk_device_pairing_details';

/**
 * A class that represents the device pairing details response
 * @property {model.AWSDKDevicePairingDetails} devicePairingDetails contains pairing details for the device.
 * @since 2.6.0
 * @ignore
 * @extends model.AWSDKResponse
 */
class AWSDKDevicePairingDetailsResponse extends AWSDKResponse {
  get devicePairingDetails() {
    return GenericParser.parseObject(this.__data.deviceLiveStreamPairingResponse, AWSDKDevicePairingDetails);
  }
}
export default AWSDKDevicePairingDetailsResponse;
