import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';
import AWSDKDevicePairingStatus from '../../../model/device_live_streaming/awsdk_device_pairing_status';

/**
 * A class that represents the device pairing status response.
 * @property {model.AWSDKDevicePairingStatus} pairingStatus contains the device pairing status.
 * @since 2.6.0
 * @ignore
 * @extends model.AWSDKResponse
 */
class AWSDKDevicePairingStatusResponse extends AWSDKResponse {
  get devicePairingStatus() {
    return GenericParser.parseObject(this.__data.deviceLiveStreamPairingResponse, AWSDKDevicePairingStatus);
  }
}
export default AWSDKDevicePairingStatusResponse;
