import AWSDKEntity from '../awsdk_entity';
/**
 * This class represents a single medication for a {@link model.AWSDKConsumer|AWSDKConsumer}.
 * @property {String} code the code that identifies this medication
 * @property {String} description information pertaining to this medication
 * @property {String} displayName the name to be displayed for this medication
 * @extends model.AWSDKEntity
 */
class AWSDKMedication extends AWSDKEntity {
  get code() {
    return this.__data.code;
  }

  get description() {
    return this.__data.description;
  }

  get displayName() {
    return this.__data.displayName;
  }
}
export default AWSDKMedication;
