import AWSDKDataObject from '../awsdk_dataobject';
import AWSDKProviderAvailability from './awsdk_provider_availability';
import GenericParser from '../../internal/parser/generic_parser';

/**
 * A class representing a collection of {@link model.AWSDKProviderAvailability} objects
 * @property {model.AWSDKProviderAvailability[]} futureAvailableProviders representing an array of {@link model.AWSDKProviderAvailability} objects
 * @property {Date} date the date for which these providers are available
 * @since 1.1.0
 * @extends model.AWSDKDataObject
 */
class AWSDKProvidersAvailability extends AWSDKDataObject {
  get futureAvailableProviders() {
    return this.__data.futureAvailableProviders.map(item => GenericParser.parseObject(item, AWSDKProviderAvailability));
  }

  get date() {
    return GenericParser.parseDate(this.__data.date);
  }
}

export default AWSDKProvidersAvailability;
