import AWSDKDataObject from '../awsdk_dataobject';

/**
 * The AWSDKTriageQuestion class represents a wrapper for a single Q&A during triage
 * @property {String} question the triage question to display
 * @property {String} answer the answer to the question
 * @extends model.AWSDKDataObject
 */
class AWSDKTriageQuestion extends AWSDKDataObject {
  get question() {
    return this.__data.question;
  }

  get answer() {
    return this.__data.answer;
  }

  set question(question) {
    this.__data.question = question;
  }

  set answer(answer) {
    this.__data.answer = answer;
  }
}
export default AWSDKTriageQuestion;
