import AWSDKResponse from './awsdk_response';
import AWSDKAddress from '../../../model/awsdk_address';
import GenericParser from '../../parser/generic_parser';

/**
 * Simple wrapper around an {@link model.AWSDKAddress}
 * @property {model.AWSDKAddress} address the {@link model.AWSDKAddress}
 * @extends model.AWSDKResponse
 */
class AWSDKAddressResponse extends AWSDKResponse {
  get address() {
    return this.__data.address ? GenericParser.parseObject(this.__data.address, AWSDKAddress) : null;
  }
}

export default AWSDKAddressResponse;
