import AWSDKPracticeBase from '../../internal/model/awsdk_practice_base';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKModality from '../awsdk_modality';

/**
 * The AWSDKPracticeInfo class represents a stub of a practice with no links, normally used for quick practice display
 * @property {String} paymentRequiredForScheduledVisitsText the text to be displayed when payment is required for a scheduled visit
 * @property {Boolean} paymentRequiredForScheduledVisits boolean true if scheduled visits with this provider require payment, false otherwise
 * @property {Boolean} showAvailableNow boolean true if the showAvailableNow flag is set, false otherwise
 * @property {Boolean} showScheduling boolean true if the provider is accepting scheduled visits
 * @property {model.AWSDKModality[]} availableModalities represents the modalities that can be offered for a visit within this practice @since 1.4.0
 * @property {Boolean} hideProviderVisitCostEstimate TRUE if the provider's estimated visit cost should be hidden for this practice
 * @property {Boolean} hideCancelAppointmentLink TRUE if the practice is configured to hide the cancel appointment link
 * @extends model.AWSDKPracticeBase
 */
class AWSDKPracticeInfo extends AWSDKPracticeBase {
  get paymentRequiredForScheduledVisitsText() {
    return this.__data.paymentRequiredForScheduledVisitsText;
  }

  get paymentRequiredForScheduledVisits() {
    return GenericParser.parseBoolean(this.__data.paymentRequiredForScheduledVisits);
  }

  get showAvailableNow() {
    return GenericParser.parseBoolean(this.__data.showAvailableNow);
  }

  get showScheduling() {
    return GenericParser.parseBoolean(this.__data.showScheduling);
  }

  /**
   * @since 1.4.0
   */
  get availableModalities() {
    return (Array.isArray(this.__data.availableModalities) && this.__data.availableModalities.map(item => GenericParser.parseObject(item, AWSDKModality))) || [];
  }

  /**
   * @since 1.5.0
   */
  get hideProviderVisitCostEstimate() {
    return GenericParser.parseBoolean(this.__data.hideProviderVisitCostEstimate);
  }

  /**
   * @since 1.5.0
   */
  get hideCancelAppointmentLink() {
    return GenericParser.parseBoolean(this.__data.hideCancelAppointmentLink);
  }
}
export default AWSDKPracticeInfo;
