import AWSDKEntity from '../awsdk_entity';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKEntityId from '../../internal/model/awsdk_entity_id';

/**
 * This class contains all necessary information for representing a secure message attachment.
 * @property {String} name the name of the attachment
 * @property {Number} size the size of the attachment in bytes
 * @property {String} type the type of the attachment
 * @property {String} sourceId the sourceId of the attachment
 * @since 1.2.3
 * @extends model.AWSDKEntity
 */
class AWSDKAttachment extends AWSDKEntity {
  get name() {
    return this.__data.name;
  }
  get size() {
    return this.__data.size;
  }
  get type() {
    return this.__data.type;
  }
  get sourceId() {
    return this.__data.sourceId;
  }
}
export default AWSDKAttachment;
