import AWSDKSecureMessage from './awsdk_secure_message';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKAccount from '../awsdk_account';
import AWSDKMessageType from './awsdk_message_type';
import AWSDKAttachment from './awsdk_attachment';

/**
 * This class contains all necessary information for representing a detailed message.
 * @property {String} body the full message body
 * @property {Boolean} canReply whether or not you can reply to the message
 * @property {Number} attachmentCount number of attachments on the message
 * @property {Boolean} threadOpen whether or not the thread is still open
 * @property {model.AWSDKAccount} sender the sender of the message
 * @property {model.AWSDKAccount[]} recipients the recipients of the message
 * @property {model.AWSDKMessageType} messageType the type of this message
 * @since 1.1.0
 * @extends model.AWSDKSecureMessage
 */
class AWSDKDetailedMessage extends AWSDKSecureMessage {
  get body() {
    return this.__data.body;
  }
  get canReply() {
    return GenericParser.parseBoolean(this.__data.canReply);
  }
  get attachmentCount() {
    return this.__data.attachmentCount;
  }
  get attachments() {
    return (this.__data.attachments && this.__data.attachments.map(attachment => GenericParser.parseObject(attachment, AWSDKAttachment))) || [];
  }
  get threadOpen() {
    return GenericParser.parseBoolean(this.__data.threadOpen);
  }
  get sender() {
    return GenericParser.parseObject(this.__data.sender, AWSDKAccount);
  }
  get recipients() {
    const recipients = [];
    if (this.__data.selfRecipient) {
      recipients.push(GenericParser.parseObject(this.__data.selfRecipient, AWSDKAccount));
    }
    if (this.__data.nonMemberRecipients) {
      recipients.push(...this.__data.nonMemberRecipients.map(recipient => GenericParser.parseObject(recipient, AWSDKAccount)));
    }
    return recipients;
  }
  get messageType() {
    return GenericParser.parseEnum(this.__data.messageType, AWSDKMessageType);
  }
}
export default AWSDKDetailedMessage;
