import GenericParser from '../../internal/parser/generic_parser';
import AWSDKPractice from './awsdk_practice';
import AWSDKPracticeCategory from './awsdk_practice_category';
import AWSDKResponse from '../../internal/model/response/awsdk_response';

/**
 * The AWSDKPracticesResponse class represents a wrapper object that encapsulates a response to a request for a list of practices.
 * @property {model.AWSDKPractice[]} practices an array of {@link model.AWSDKPractice|AWSDKPractice} objects
 * @property {model.AWSDKPracticeCategory[]} practiceCategories an array of {@link model.AWSDKPracticeCategory|AWSDKPracticeCategory} objects
 * @extends model.AWSDKResponse
 */
class AWSDKPracticeList extends AWSDKResponse {
  get practices() {
    return this.__data.practiceList.practices.map(item => GenericParser.parseObject(item, AWSDKPractice));
  }

  get practiceCategories() {
    return this.__data.practiceList.categories.map(item => GenericParser.parseObject(item, AWSDKPracticeCategory));
  }
}

export default AWSDKPracticeList;
