import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';
import AWSDKVisit from '../../../model/visit/awsdk_visit';

/**
 * Represents a response object wrapper to a single {@link model.AWSDKVisit|AWSDKVisit} object type
 * @property {model.AWSDKVisit} visit the visit object representing information about the visit
 */
class AWSDKVisitResponse extends AWSDKResponse {
  get visit() {
    return GenericParser.parseObject(this.__data.visit, AWSDKVisit);
  }
}
export default AWSDKVisitResponse;
