import AWSDKResponse from './awsdk_response';
import AWSDKAppointmentReadiness from '../../../model/appointment/awsdk_appointment_readiness';
import GenericParser from '../../parser/generic_parser';

/**
 * Simple wrapper around an {@link model.AWSDKAppointmentReadiness}
 * @property {model.AWSDKAppointmentReadiness} appointmentReadiness the {@link model.AWSDKAppointmentReadiness}
 * @since 1.1.0
 * @extends model.AWSDKResponse
 */
class AWSDKAppointmentReadinessResponse extends AWSDKResponse {
  get appointmentReadiness() {
    return GenericParser.parseObject(this.__data.appointmentReadiness, AWSDKAppointmentReadiness);
  }
}

export default AWSDKAppointmentReadinessResponse;
