import GenericParser from '../../internal/parser/generic_parser';
import AWSDKEntity from '../../model/awsdk_entity';
/**
 * This class represents the estimated cost of a visit with a Provider.
 * @property {Number} cost the estimated cost of the visit
 * @property {String} formattedCost the localized string of the estimated visit cost
 * @property {Number} extensionCost the estimated cost of a visit extension
 * @property {String} formattedExtensionCost the localized string of the estimated visit extension cost
 * @property {String} currencyCode the code of the currency in which the estimated cost is represented
 * @property {Boolean} isDeferredBillingSupported boolean true if deferred billing is supported for cost estimation, false otherwise.
 * @since 1.1.0
 * @extends model.AWSDKEntity
 */
class AWSDKEstimatedVisitCost extends AWSDKEntity {
  get cost() {
    return this.__data.cost;
  }

  get formattedCost() {
    return this.__data.formattedCost;
  }

  get extensionCost() {
    return this.__data.extensionCost;
  }

  get formattedExtensionCost() {
    return this.__data.formattedExtensionCost;
  }

  get currencyCode() {
    return this.__data.currencyCode;
  }

  get isDeferredBillingSupported() {
    return GenericParser.parseBoolean(this.__data.deferredBillingSupported);
  }
}
export default AWSDKEstimatedVisitCost;
