import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';
import AWSDKSecureMessageContact from '../../../model/secure_message/awsdk_secure_message_contact';

/**
 * Simple wrapper around an {@link model.AWSDKSecureMessageContact[]}
 * @property {model.AWSDKSecureMessageContact[]} contacts an array of {@link model.AWSDKSecureMessageContact}
 * @extends model.AWSDKResponse
 */
class AWSDKSecureMessageContactsResponse extends AWSDKResponse {
  get contacts() {
    return this.__data.contacts ? this.__data.contacts.map(item => GenericParser.parseObject(item, AWSDKSecureMessageContact)) : null;
  }
}

export default AWSDKSecureMessageContactsResponse;
