import AWSDKDataObject from './awsdk_dataobject';

/**
 * This class represents a platform type that an instance of an AWSDK in running on
 * @property {String} name the string representing the name of the platform
 * @property {String} localizedDisplayName the string representing the localized name for the platform
 * @since 1.4.0
 * @extends model.AWSDKDataObject
 */
class AWSDKPlatformType extends AWSDKDataObject {
  get name() {
    return this.__data.name;
  }
  get localizedDisplayName() {
    return this.__data.localizedDisplayName;
  }
}
export default AWSDKPlatformType;
