import AWSDKEntity from '../awsdk_entity';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKHealthDocumentAttachment from './awsdk_health_document_attachment';

/**
 * This class represents a single instance of a {@link model.AWSDKConsumer|AWSDKConsumer}'s health record document. It is not <br>
 * the actual file, but instead an object with meta-data about the actual file.
 * @property {String} name the name of this record
 * @property {String} description textual description of this health record
 * @property {String} href string url that points to this health record
 * @property {model.AWSDKHealthDocumentAttachment} attachment contains information about the file and provides link for downloading.
 * @property {String} sourceId the sourceId associated with this health document.
 * @property {Date} uploadDate the date that this health document was uploaded.
 * @extends model.AWSDKEntity
 */
class AWSDKHealthDocumentRecord extends AWSDKEntity {
  get name() {
    return this.__data.name;
  }

  get description() {
    return this.__data.description;
  }

  get href() {
    return this.__data.href;
  }

  get attachment() {
    return GenericParser.parseObject(this.__data.attachment, AWSDKHealthDocumentAttachment);
  }

  /**
   * @since 1.2.1
   */
  get sourceId() {
    return this.__data.sourceId;
  }

  /**
   * @since 1.2.1
   */
  get uploadDate() {
    return this.__data.uploadDate && GenericParser.parseDate(this.__data.uploadDate);
  }
}
export default AWSDKHealthDocumentRecord;
