import GenericParser from '../../internal/parser/generic_parser';
import AWSDKPaginated from '../awsdk_paginated';
import AWSDKOnlineVisitFollowUpItem from './awsdk_online_visit_follow_up_item';

/**
 * This class represents the unscheduled follow ups for a consumer {@link model.AWSDKOnlineVisitFollowUpItem}s.
 * @property {model.AWSDKOnlineVisitFollowUpItem[]} onlineFollowUpItems The result list of {@link model.AWSDKOnlineVisitFollowUpItem}s for the authenticated consumer.
 * @extends model.AWSDKPaginated
 * @since 3.0.0
 */
class AWSDKPaginatedOnlineVisitFollowUpItems extends AWSDKPaginated {
  get onlineFollowUpItems() {
    return (Array.isArray(this.__data.list) && this.__data.list.map(item => GenericParser.parseObject(item, AWSDKOnlineVisitFollowUpItem))) || [];
  }
}
export default AWSDKPaginatedOnlineVisitFollowUpItems;
