import AWSDKDataObject from '../awsdk_dataobject';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKChatItem from './awsdk_chat_item';

/**
 * This class encapsulates information about a chat session between a consumer and a provider.
 * @property {model.AWSDKChatItem[]} chatItems an array of {@link model.AWSDKChatItem|AWSDKChatItem} associated with the visit
 * @property {Number} lastOrdinal represents the last ordinal found in this report
 * @since 1.1.0
 * @extends model.AWSDKDataObject
 */
class AWSDKChatReport extends AWSDKDataObject {
  get lastOrdinal() {
    return this.__data.lastOrdinal;
  }

  get chatItems() {
    return this.__data.chatItems.map(item => GenericParser.parseObject(item, AWSDKChatItem));
  }
}
export default AWSDKChatReport;
