import GenericParser from '../../internal/parser/generic_parser';
import AWSDKVisitReport from './awsdk_visit_report';
import AWSDKPaginated from '../awsdk_paginated';

/**
 * A paginated wrapper around a collection of {@link model.AWSDKVisitReport|AWSDKVisitReport}s
 * @property {Array} visitReports a collection of {@link model.AWSDKVisitReport|AWSDKVisitReport}s
 * @extends model.AWSDKPaginated
 * @since 2.8.0
 */
class AWSDKPaginatedVisitReports extends AWSDKPaginated {
  get visitReports() {
    return (Array.isArray(this.__data.list) && this.__data.list.map(item => GenericParser.parseObject(item, AWSDKVisitReport))) || [];
  }
}

export default AWSDKPaginatedVisitReports;
