import AWSDKEntity from '../awsdk_entity';
import GenericParser from '../../internal/parser/generic_parser';

/**
 * This class represents any restrictions that could be applicable with the ability to prescribe for a given {@link model.AWSDKProvider|AWSDKProvider} within the state of actual residency of the {@link model.AWSDKConsumer|AWSDKConsumer}.
 * @property {Boolean} isStateFormularyActive boolean true if state formulary is active, false otherwise.
 * @property {Boolean} isRestrictedToOtc boolean true if the state formulary is restricted to otc (over-the-counter), false otherwise.
 * @extends model.AWSDKEntity
 */
class AWSDKStateFormularyRestriction extends AWSDKEntity {
  get isStateFormularyActive() {
    return GenericParser.parseBoolean(this.__data.isStateFormularyActive);
  }

  get isRestrictedToOtc() {
    return GenericParser.parseBoolean(this.__data.isRestrictedToOtc);
  }
}
export default AWSDKStateFormularyRestriction;
