import AWSDKEntity from '../awsdk_entity';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKPlatformType from '../awsdk_platform_type';

/**
 * This class contains all necessary information for representing appointment readiness.
 * @property {Boolean} registered whether or not the consumer is registered
 * @property {Boolean} techCheckEnabled whether or not the tech check is enabled
 * @property {Boolean} techCheckPassed whether or not the tech check has been passed
 * @property {Boolean} readyForAppointment whether or not the consumer is ready for the appointment
 * @property {Date} datePassedTechCheck the date in which the consumer passed tech check
 * @property {Boolean} microphonePassed a boolean describing whether or not the microphone test was successfully completed
 * @property {Boolean} speakerPassed a boolean describing whether or not the speaker test was successfully completed
 * @property {Boolean} cameraPassed a boolean describing whether or not the camera test was successfully completed
 * @property {Number} downloadSpeedMbps the numeric value representing the download speed registered by the testing tool
 * @property {Number} uploadSpeedMbps the numeric float value representing the upload speed registered by the testing tool
 * @property {Number} latencyMs the numeric float value representing the latency registered by the testing tool
 * @property {Number} jitter the numeric integer value representing the variation in latency on a packet flow between two systems
 * @property {String} microphoneDeviceName the string name of the microphone device
 * @property {String} speakerDeviceName the string name of the speaker device
 * @property {String} cameraDeviceName the string name of the front-facing camera
 * @property {String} sourceId the source id for this appointment readiness request
 * @property {model.AWSDKPlatformType} platformType the platform type that the latest readiness test was performed on
 * @property {model.AWSDKAppointment} appointment the appointment for this appointment readiness request
 * @property {String} platformVersion the platform version that the latest readiness test was performed on
 * @since 1.1.0
 * @extends model.AWSDKEntity
 */

class AWSDKAppointmentReadiness extends AWSDKEntity {
  get registered() {
    return GenericParser.parseBoolean(this.__data.enrolled);
  }
  get techCheckEnabled() {
    return GenericParser.parseBoolean(this.__data.techCheckEnabled);
  }
  get techCheckPassed() {
    return GenericParser.parseBoolean(this.__data.techCheckPassed);
  }
  get readyForAppointment() {
    return GenericParser.parseBoolean(this.__data.readyForAppointment);
  }
  /**
   * @since 1.4.0
   */
  get datePassedTechCheck() {
    return GenericParser.parseDate(this.__data.datePassedTechCheck);
  }
  /**
   * @since 1.4.0
   */
  get microphonePassed() {
    return GenericParser.parseBoolean(this.__data.microphonePassed);
  }
  /**
   * @since 1.4.0
   */
  get speakerPassed() {
    return GenericParser.parseBoolean(this.__speakerPassed);
  }
  /**
   * @since 1.4.0
   */
  get cameraPassed() {
    return GenericParser.parseBoolean(this.__data.cameraPassed);
  }
  /**
   * @since 1.4.0
   */
  get downloadSpeedMbps() {
    return this.__data.downloadSpeedMbps;
  }
  /**
   * @since 1.4.0
   */
  get uploadSpeedMbps() {
    return this.__data.uploadSpeedMbps;
  }
  /**
   * @since 1.4.0
   */
  get latencyMs() {
    return this.__data.latencyMs;
  }
  /**
   * @since 1.4.0
   */
  get jitter() {
    return this.__data.jitter;
  }
  /**
   * @since 1.4.0
   */
  get microphoneDeviceName() {
    return this.__data.microphoneDeviceName;
  }
  /**
   * @since 1.4.0
   */
  get cameraDeviceName() {
    return this.__data.cameraDeviceName;
  }
  /**
   * @since 1.4.0
   */
  get speakerDeviceName() {
    return this.__data.speakerDeviceName;
  }
  /**
   * @since 1.4.0
   */
  get appointment() {
    return this.__data.appointment;
  }
  /**
   * @since 1.4.0
   */
  get sourceId() {
    return this.__sourceId;
  }
  /**
   * @since 1.4.0
   */
  get platformVersion() {
    return this.__data.platformVersion;
  }
  /**
   * @since 1.4.0
   */
  get platformType() {
    return this.__data.platformType ? GenericParser.parseObject(this.__data.platformType, AWSDKPlatformType) : null;
  }
  // setters
  /**
   * @since 1.4.0
   */
  set sourceId(sourceId) {
    this.__sourceId = sourceId;
  }
}

export default AWSDKAppointmentReadiness;
