import AWSDKEntity from '../awsdk_entity';

/**
 * The AWSDKPracticeSubCategory class representing a subCategory object in a {@link model.AWSDKPracticeCategory|AWSDKPracticeCategory}
 * @property {String} name the name of this subCategory
 * @property {Number} practiceCount the number of practices in this subCategory
 * @extends model.AWSDKEntity
 */
class AWSDKPracticeSubCategory extends AWSDKEntity {
  get name() {
    return this.__data.name;
  }

  get practiceCount() {
    return this.__data.practiceCount;
  }
}

export default AWSDKPracticeSubCategory;
