import AWSDKMessageDraft from './awsdk_message_draft';
import AWSDKError from '../../../error/awsdk_error';
import GenericParser from '../../../internal/parser/generic_parser';
import AWSDKSecureMessageContact from '../awsdk_secure_message_contact';
import AWSDKMessageActionType from './awsdk_message_action_type';

/**
 * This class contains all necessary information for representing a forward message draft.
 * Get an instance of this via {@link service.SecureMessageService#getForwardMessageDraft|SecureMessageService.getForwardMessageDraft}
 * @property {model.AWSDKSecureMessageContact} recipient the recipient of the message
 * @since 1.1.0
 * @extends model.AWSDKMessageDraft
 */
class AWSDKForwardMessageDraft extends AWSDKMessageDraft {
  constructor(sourceMessage) {
    super();
    this.__actionType = AWSDKMessageActionType.Forward;
    this.__topicType = sourceMessage.topicType;
    this.__sourceMessageId = sourceMessage.id.encryptedId;
    this.__sourceMessageType = sourceMessage.messageType;
    this.__sourceIsSystemNotification = sourceMessage.isSystemNotification;
    this.__subject = sourceMessage.subject;
    this.__body = sourceMessage.body;
  }

  /**
  * @since 1.3.0
  */
  get recipient() {
    return this.__recipient ? GenericParser.parseObject(this.__recipient, AWSDKSecureMessageContact) : null;
  }

  set recipient(recipient) {
    if (recipient) {
      if (!(recipient instanceof AWSDKSecureMessageContact)) {
        throw AWSDKError.AWSDKIllegalArgument('recipient must be of type AWSDKSecureMessageContact');
      }
      this.__recipient = recipient;
      this.__recipientEncryptedId = recipient.id.encryptedId;
      this.__recipientName = recipient.name;
    }
  }
}
export default AWSDKForwardMessageDraft;
