import GenericParser from '../../internal/parser/generic_parser';
import AWSDKEntity from '../awsdk_entity';
import AWSDKVisitRx from './awsdk_visit_rx';
import AWSDKVisitProcedure from './awsdk_visit_procedure';
import AWSDKVisitDiagnosis from './awsdk_visit_diagnosis';
import AWSDKAgendaItemFollowUp from './awsdk_agenda_item_follow_up';
import AWSDKPostVisitFollowUpItem from '../post_visit_follow_up_items/awsdk_post_visit_follow_up_item';

/**
 * This class represents all entries that a {@link model.AWSDKProvider|AWSDKProvider} makes during and after a visit with a {@link model.AWSDKConsumer|AWSDKConsumer}
 * @property {model.AWSDKVisitRx[]} prescriptions an array of {@link model.AWSDKVisitRx} prescriptions for this visit (if any)
 * @property {model.AWSDKVisitProcedure[]} procedures an array of {@link model.AWSDKVisitProcedure} for this visit (if any)
 * @property {model.AWSDKVisitDiagnosis[]} diagnoses an array of {@link model.AWSDKVisitDiagnosis} for this visit (if any)
 * @property {String} notes provider notes for this visit
 * @property {model.AWSDKAgendaItemFollowUp[]} agendaItemFollowUps an array of {@link model.AWSDKAgendaItemFollowUp}, which represents follow up agenda items (if any)
 * @property {model.AWSDKPostVisitFollowUpItem[]} postVisitFollowUpitems an array of {@link model.AWSDKPostVisitFollowUpItem}, which represents post visit follow up items for this visit (if any)
 * @extends model.AWSDKEntity
 */
class AWSDKProviderEntries extends AWSDKEntity {
  get prescriptions() {
    return this.__data.prescriptions.map(item => GenericParser.parseObject(item, AWSDKVisitRx));
  }

  get procedures() {
    return this.__data.procedures.map(item => GenericParser.parseObject(item, AWSDKVisitProcedure));
  }

  get diagnoses() {
    return this.__data.diagnoses.map(item => GenericParser.parseObject(item, AWSDKVisitDiagnosis));
  }

  get notes() {
    return this.__data.notes;
  }

  get agendaItemFollowUps() {
    return this.__data.followUpAgendaItems ? this.__data.followUpAgendaItems.map(item => GenericParser.parseObject(item, AWSDKAgendaItemFollowUp)) : null;
  }

  get postVisitFollowUpItems() {
    return this.__data.postVisitFollowUpItems ? this.__data.postVisitFollowUpItems.map(item => GenericParser.parseObject(item, AWSDKPostVisitFollowUpItem)) : null;
  }
}
export default AWSDKProviderEntries;
