import AWSDKError from '../../error/awsdk_error';
import Validator from '../../internal/validator/validator';
import AWSDKDisposition from './awsdk_disposition';

/**
 * This class represents the search criteria for finding a visit.
 * Get an instance of this via {@link service.VisitService#getNewVisitSearchCriteria()|VisitService.getNewVisitSearchCriteria}
 * @property {Date} [startDate] the start date of the visit to filter by
 * @property {Date} [endDate] the end date of the visit to filter by
 * @property {String} [sourceId] the source id of the visit to filter by
 * @property {model.AWSDKDisposition[]} [dispositions] the list of dispositions to filter by
 * @property {Number} [maxResults] the max results to return
 * @since 1.4.0
 *
 */
class AWSDKVisitSearchCriteria {
  constructor(criteria = {}) {
    this.__startDate = criteria.startDate;
    this.__endDate = criteria.endDate;
    this.__sourceId = criteria.sourceId;
    this.__dispositions = criteria.dispositions;
    this.__maxResults = criteria.maxResults;
  }

  set startDate(startDate) {
    if (!(startDate instanceof Date)) {
      throw AWSDKError.AWSDKIllegalArgument('startDate must be of type Date');
    }
    this.__startDate = startDate;
  }

  get startDate() {
    return this.__startDate;
  }

  set endDate(endDate) {
    if (!(endDate instanceof Date)) {
      throw AWSDKError.AWSDKIllegalArgument('endDate must be of type Date');
    }
    this.__endDate = endDate;
  }

  get endDate() {
    return this.__endDate;
  }

  get sourceId() {
    return this.__sourceId;
  }

  set sourceId(sourceId) {
    this.__sourceId = sourceId;
  }

  set dispositions(dispositions) {
    if (!Array.isArray(dispositions) || !dispositions.every(d => AWSDKDisposition.__dispositions.includes(d))) {
      throw AWSDKError.AWSDKIllegalArgument('dispositions must be of type AWSDKDisposition[]');
    }
    this.__dispositions = dispositions;
  }

  get dispositions() {
    return this.__dispositions;
  }

  set maxResults(maxResults) {
    if (!(Validator.isInt(maxResults))) {
      throw AWSDKError.AWSDKIllegalArgument('maxResults must be of type int');
    }
    this.__maxResults = maxResults;
  }

  get maxResults() {
    return this.__maxResults;
  }
}
export default AWSDKVisitSearchCriteria;
