import AWSDKEntity from '../awsdk_entity';
import AWSDKProvider from '../provider/awsdk_provider';
import AWSDKConsumer from '../consumer/awsdk_consumer';
import AWSDKVisitCost from '../visit/awsdk_visit_cost';
import AWSDKVisitSchedule from '../visit/awsdk_visit_schedule';
import AWSDKTopic from '../visit/awsdk_topic';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKDisposition from '../visit/awsdk_disposition';
import AWSDKCheckInStatus from './awsdk_check_in_status';
import AWSDKProviderType from '../provider/awsdk_provider_type';
import AWSDKModality from '../awsdk_modality';

/**
 * This class contains all necessary information for representing an appointment.
 * @property {model.AWSDKProvider} assignedProvider the assigned provider
 * @property {model.AWSDKProvider} proxyProvider the proxy provider
 * @property {model.AWSDKConsumer} consumer the consumer
 * @property {model.AWSDKConsumer} consumerProxy the consumer proxy
 * @property {model.AWSDKVisitCost} cost the cost of the appointment
 * @property {model.AWSDKVisitSchedule} schedule the schedule for the appointment
 * @property {model.AWSDKCheckInStatus} checkInStatus the check in status for the appointment
 * @property {model.AWSDKTopic[]} topics an array of {@link model.AWSDKTopic} objects
 * @property {String} practiceName the practice name for the appointment
 * @property {model.AWSDKDisposition} disposition the disposition for the appointment
 * @property {model.AWSDKProviderType} specialty the provider's specialty
 * @property {String} noteToPatient the note to the patient for the appointment
 * @property {String} href the url to more details for this appointment.
 * @property {String} sourceId the sourceId related to this appointment.
 * @since 1.4.0
 * @property {String} initiatorOverridePhoneNumber the consumer's preferred phone number for this appointment
 * @since 1.4.0
 * @property {string} formattedInitiatorOverridePhoneNumber the consumer's preferred formatted phone number for this appointment
 * @since 1.4.0
 * @property {model.AWSDKModality} modality represents the modality for this appointment
 * @since 1.1.0
 * @extends model.AWSDKEntity
 */
class AWSDKAppointment extends AWSDKEntity {
  get assignedProvider() {
    return this.__data.assignedProvider ? GenericParser.parseObject(this.__data.assignedProvider, AWSDKProvider) : null;
  }

  get proxyProvider() {
    return this.__data.proxyProvider ? GenericParser.parseObject(this.__data.proxyProvider, AWSDKProvider) : null;
  }

  get consumer() {
    return GenericParser.parseObject(this.__data.member, AWSDKConsumer);
  }

  get consumerProxy() {
    return this.__data.memberProxy ? GenericParser.parseObject(this.__data.memberProxy, AWSDKConsumer) : null;
  }

  get cost() {
    return this.__data.cost ? GenericParser.parseObject(this.__data.cost, AWSDKVisitCost) : null;
  }

  get schedule() {
    return this.__data.schedule ? GenericParser.parseObject(this.__data.schedule, AWSDKVisitSchedule) : null;
  }

  get checkInStatus() {
    return GenericParser.parseEnum(this.__data.checkInStatus, AWSDKCheckInStatus);
  }

  get topics() {
    const topics = this.__data.topics;
    if (!Array.isArray(topics) || topics.length === 0) {
      return null;
    }
    return topics.map(item => GenericParser.parseObject(item, AWSDKTopic));
  }

  get practiceName() {
    return this.__data.practiceName;
  }

  get disposition() {
    return GenericParser.parseEnum(this.__data.disposition, AWSDKDisposition);
  }

  get specialty() {
    return GenericParser.parseObject(this.__data.specialty, AWSDKProviderType);
  }

  get noteToPatient() {
    return this.__data.noteToPatient;
  }

  get href() {
    return this.__data.href;
  }

  /**
   * @since 1.2.1
   */
  get sourceId() {
    return this.__data.sourceId;
  }

  /**
   * @since 1.3.1
   */
  set proxies(proxies) {
    if (Array.isArray(proxies) && proxies.every(item => item instanceof AWSDKConsumer)) {
      this.__proxies = proxies;
    } else {
      this.__proxies = null;
    }
  }

  /**
   * @since 1.4.0
   */
  get initiatorOverridePhoneNumber() {
    return this.__data.initiatorEngagementOverridePhone;
  }

  /**
   * @since 1.4.0
   */
  set initiatorOverridePhoneNumber(initiatorOverridePhoneNumber) {
    this.__data.initiatorEngagementOverridePhone = initiatorOverridePhoneNumber;
  }

  /**
   * @since 1.4.0
   */
  get formattedInitiatorOverridePhoneNumber() {
    return this.__data.formattedInitiatorEngagementOverridePhone;
  }

  /**
   * @since 1.4.0
   */
  get modality() {
    return GenericParser.parseObject(this.__data.modality, AWSDKModality);
  }
}

export default AWSDKAppointment;
