import AWSDKLanguage from '../../../model/awsdk_language';
import GenericParser from '../../parser/generic_parser';
import AWSDKError from '../../../error/awsdk_error';

/**
 * Internal data object used to initiate a first-available search on behalf of a consumer
 * @property {String} specialtyId the id of the {@link model.AWSDKOnDemandSpecialty} in which to search
 * @property {model.AWSDKLanguage=} languageSpoken the language to filter the search by, if any
 * @property {String} [practiceId] the encrypted practice id of the {@link model.AWSDKPractice} in which to search. When practice id is provided, the practice associated to the id will be used instead of the practice associated to the specialty
 * @property {String[]} [providerTypes] an array of provider types to include in the search. When the list of provider types is provided, the list of provider types will be combined with the list of provider types associated to the specialty
 */
class AWSDKFirstAvailableRequest {
  constructor(onDemandSpecialtyId, languageSpoken, practiceId, providerTypes) {
    this.__specialtyId = onDemandSpecialtyId;
    if (languageSpoken != null) {
      if (!(languageSpoken instanceof AWSDKLanguage)) {
        throw AWSDKError.AWSDKIllegalArgument('languageSpoken must be of type AWSDKLanguage');
      }
      this.__languageSpoken = languageSpoken;
      this.__practiceId = practiceId;
      this.__providerTypes = providerTypes;
    }
  }

  get specialtyId() {
    return this.__specialtyId;
  }

  get languageSpoken() {
    return this.__languageSpoken ? GenericParser.parseObject(this.__languageSpoken, AWSDKLanguage) : null;
  }

  get practiceId() {
    return this.__practiceId;
  }

  get providerTypes() {
    return this.__providerTypes;
  }

  getAsJSONString() {
    const content = {
      specialtyId: this.specialtyId,
      practiceId: this.practiceId,
      providerTypes: this.providerTypes && this.providerTypes.map(pt => pt.resourceKey),
    };
    if (this.languageSpoken) {
      content.languageSpoken = this.languageSpoken.value;
    }
    return JSON.stringify(content);
  }
}
export default AWSDKFirstAvailableRequest;
