/**
 * This class contains all necessary information for representing a secure message uploaded attachment.
 * Get an instance of this via {@link service.SecureMessageService#newUploadAttachment|SecureMessageService.newUploadAttachment}
 * @property {Blob|File} data a blob of data represented by a blob or a file.
 * @property {String} name the name of the attachment.
 * @since 1.1.0
 */
class AWSDKUploadAttachment {
  set data(content) {
    this.__data = content;
  }

  get data() {
    return this.__data;
  }

  set name(name) {
    this.__name = name;
  }

  get name() {
    return this.__name;
  }
}
export default AWSDKUploadAttachment;
