import AWSDKTermEntity from '../../internal/model/awsdk_term_entity';
import GenericParser from '../../internal/parser/generic_parser';

/**
 * This class represents the base class for selectable health items
 * @param {Boolean} isCurrent boolean true if the item is current, false otherwise.
 * @extends model.AWSDKTermEntity
 */
class AWSDKConsumerHealthItem extends AWSDKTermEntity {
  get isCurrent() {
    return GenericParser.parseBoolean(this.__data.current);
  }

  set isCurrent(isCurrent) {
    this.__data.current = isCurrent;
  }
}
export default AWSDKConsumerHealthItem;
