import GenericParser from '../../internal/parser/generic_parser';
import AWSDKProvider from '../provider/awsdk_provider';
import AWSDKConsumer from '../consumer/awsdk_consumer';
import AWSDKPractice from '../practice/awsdk_practice';
import AWSDKEntity from '../awsdk_entity';

/**
 * This Entity represents the details of a practice follow up
 * @property {model.AWSDKPractice} practice The practice that the consumer is being referred to
 * @property {string} notes Notes for the follow up from the referring provider
 * @property {model.AWSDKConsumer} consumer The consumer being referred to a practice
 * @property {model.AWSDKProvider} referringProvider The provider that created this follow up
 * @property {Date} dateCreated The date when the follow up was created
 * @property {Boolean} resolved, The resolution status of the practice follow up item
 * @since 3.2.0
 */
class AWSDKPracticeFollowUpItem extends AWSDKEntity {
  get practice() {
    return GenericParser.parseObject(this.__data.practice, AWSDKPractice);
  }

  get notes() {
    return this.__data.notes;
  }

  get consumer() {
    return GenericParser.parseObject(this.__data.member, AWSDKConsumer);
  }

  get referringProvider() {
    return GenericParser.parseObject(this.__data.referringProvider, AWSDKProvider);
  }

  get dateCreated() {
    return GenericParser.parseDate(this.__data.dateCreated);
  }

  get resolved() {
    return GenericParser.parseBoolean(this.__data.resolved);
  }
}
export default AWSDKPracticeFollowUpItem;
