import AWSDKResponse from './awsdk_response';
import AWSDKDisclaimer from '../../../model/awsdk_disclaimer';
import GenericParser from '../../parser/generic_parser';

/**
 * Simple wrapper around an {@link model.AWSDKDisclaimer}
 * @property {model.AWSDKDisclaimer} legalNotice the {@link model.AWSDKDisclaimer}
 * @extends model.AWSDKResponse
 */
class AWSDKDisclaimerResponse extends AWSDKResponse {
  get legalNotice() {
    return this.__data.legalNotice ? GenericParser.parseObject(this.__data.legalNotice, AWSDKDisclaimer) : null;
  }
}

export default AWSDKDisclaimerResponse;
