import AWSDKTrackerDataPoint from './awsdk_tracker_data_point';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKDataObject from '../awsdk_dataobject';

/**
 * This class represents a single tracker entry
 * @property {String} source the string representing the information's origin
 * @property {Date} date the date for this entry
 * @property {model.AWSDKTrackerDataPoint[]} data an array of {@link model.AWSDKTrackerDataPoint|AWSDKTrackerDataPoint} for this tracker entry
 * @since 1.4.0
 * @extends model.AWSDKDataObject
 */
class AWSDKTrackerEntry extends AWSDKDataObject {
  get source() {
    return this.__data.source;
  }
  get date() {
    return GenericParser.parseDate(this.__data.date);
  }
  get data() {
    return (Array.isArray(this.__data.data) && this.__data.data.map(entry => GenericParser.parseObject(entry, AWSDKTrackerDataPoint))) || [];
  }
}
export default AWSDKTrackerEntry;
