import GenericParser from '../../parser/generic_parser';
import AWSDKDataObject from '../../../model/awsdk_dataobject';
import AWSDKConferenceRole from './awsdk_conference_role';
import AWSDKConferenceModalityType from './awsdk_conference_modality_type';

/**
 * A participant in the {@link model.AWSDKConferenceDetails|AWSDKConferenceDetails}. This could be a consumer, provider, facilitator etc.
 *
 * @property {model.AWSDKConferenceRole} role The role the participant is taking in this conference.
 * @property {Date} addDate When the participant joined the conference.
 * @property {boolean} joined Indicating whether the participant is currently in the conference.
 * @property {string} vendorId The unique vendor id (id provided by the vendor) for this user in the conference.
 * @property {model.ConferenceModalityType} modalityType What type of visit this conference represents.
 * @property {string=} userAgent The browser/device details for this participant.
 * @since 2.0.0
 * @private
 */
class AWSDKConferenceParticipant extends AWSDKDataObject {
  get role() {
    return GenericParser.parseEnum(this.__data.role, AWSDKConferenceRole);
  }

  get addDate() {
    return GenericParser.parseDate(this.__data.addDate);
  }

  get joined() {
    return GenericParser.parseBoolean(this.__data.joined);
  }

  get vendorId() {
    return this.__data.vendorId;
  }

  get modalityType() {
    return GenericParser.parseEnum(this.__data.modalityType, AWSDKConferenceModalityType);
  }

  get userAgent() {
    return this.__data.userAgent;
  }

  get status() {
    return this.__data.status;
  }

  get ivr() {
    return GenericParser.parseBoolean(this.__data.ivr);
  }
}
export default AWSDKConferenceParticipant;
