import AWSDKResponse from './awsdk_response';
import AWSDKDisclaimer from '../../../model/awsdk_disclaimer';
import GenericParser from '../../parser/generic_parser';

/**
 * The AWSDKRegistrationDisclaimerResponse represents a wrapper response object around a single instance of {@link model.AWSDKDisclaimer|AWSDKDisclaimer} <br>
 * as a result of a request for a registration disclaimer.
 * @property {model.AWSDKDisclaimer} registrationDisclaimer the {@link model.AWSDKDisclaimer|AWSDKDisclaimer} object
 * @extends model.AWSDKResponse
 */
class AWSDKRegistrationDisclaimerResponse extends AWSDKResponse {
  get registrationDisclaimer() {
    return GenericParser.parseObject(this.__data.LegalTextEntity, AWSDKDisclaimer);
  }
}
export default AWSDKRegistrationDisclaimerResponse;
