import AWSDKTrackerComponent from './awsdk_tracker_component';
import AWSDKError from '../../error/awsdk_error';

/**
 * This class represents a single tracker data point request for a tracker component
 * @property {model.AWSDKTrackerComponent} trackerComponent the tracker component whose data point we want to add
 * @property {Number} value the float number representing the value of the measured data point
 * @since 1.4.0
 */
class AWSDKTrackerDataPointRequest {
  get trackerComponent() {
    return this.__trackerComponent;
  }
  get value() {
    return this.__value;
  }
  set trackerComponent(trackerComponent) {
    if (!(trackerComponent instanceof AWSDKTrackerComponent)) {
      throw AWSDKError.AWSDKIllegalArgument('trackerComponent must be an instance of AWSDKTrackerComponent');
    }
    this.__trackerComponent = trackerComponent;
  }
  set value(value) {
    if (typeof value !== 'number') {
      throw AWSDKError.AWSDKIllegalArgument('value must be a number');
    }
    this.__value = value;
  }
  /**
   * @private
   */
  __getAsRequestBody() {
    const res = {};
    if (this.__trackerComponent) {
      res.uuid = this.__trackerComponent.uuid;
    }
    res.value = this.__value;
    return JSON.stringify(res);
  }

  /**
   * This method is a convenience method for presenting this object as an array element within a JSON string
   * @private
   * @returns {Object} an array element representation of this object suitable to be used as an element on a request that adds a health tracker
   */
  __toRequestElement() {
    return {
      uuid: this.__trackerComponent ? this.__trackerComponent.uuid : undefined,
      value: this.__value,
    };
  }
}
export default AWSDKTrackerDataPointRequest;
