import AWSDKTrackerUnitOfMeasure from './awsdk_tracker_unit_of_measure';

/**
 * This class represents a single measurable component of a medical test indicator. For example the blood pressure indicator has systolic and diastolic component
 * @property {Number} maximum the maximum allowed value for this component
 * @property {Number} minimum the minimum allowed value for this component
 * @since 1.4.0
 * @extends model.AWSDKTrackerUnitOfMeasure
 */
class AWSDKTrackerComponent extends AWSDKTrackerUnitOfMeasure {
  get maximum() {
    return this.__data.maximum;
  }

  get minimum() {
    return this.__data.minimum;
  }
}
export default AWSDKTrackerComponent;

