import AWSDKEntity from '../awsdk_entity';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKPostVisitFollowUpItemType from './awsdk_post_visit_follow_up_item_type';
import AWSDKPostVisitFollowUpItemCategory from './awsdk_post_visit_follow_up_item_category';
import AWSDKPostVisitFollowUpDetail from './awsdk_post_visit_follow_up_detail';

/**
 * This class represents an action that the {@link model.AWSDKConsumer|AWSDKConsumer} must take following the visit with a {@link model.AWSDKProvider|AWSDKProvider}
 * @property {model.AWSDKPostVisitFollowUpItemCategory} category the category of the follow up item @since 3.0.0
 * @property {String} description the description of the post visit follow up item
 * @property {Number} time the timestamp of the item (GMT), which will only be available when the item type is APPOINTMENT
 * @property {model.AWSDKPostVisitFollowUpItemType} type the type of the follow up item.
 * @property {boolean} resolved TRUE if this item has been resolved and doesn't require any action
 * @property {Date} createdDate the date this item was created
 * @extends model.AWSDKEntity
 */
class AWSDKPostVisitFollowUpItem extends AWSDKEntity {
  get category() {
    return GenericParser.parseEnum(this.__data.category, AWSDKPostVisitFollowUpItemCategory);
  }

  /**
   * The description of the post visit follow up item.
   * Internally defined common language description of this object, not entered by the provider.
   * Example "Referral to Cardiology"
   */
  get description() {
    return this.__data.description;
  }

  /**
   * This is the reason entered by the provider that they wish for the consumer to see another provider.
   * @since 3.0.0
   */
  get reason() {
    return this.__data.reason;
  }

  /**
   * Optional notes that may be entered by the referring provider for the benefit of the referred provider
   * @since 3.0.0
   */
  get notes() {
    return this.__data.notes;
  }

  /**
   * The name of the specialty for which the consumer is being referred, this is derived from the
   * taxonomy code - which is an administrative 'code' used to identify a provider type and area of speciality
   * @since 3.0.0
   */
  get specialtyName() {
    return this.__data.specialtyName;
  }

  /**
   * A detail object for External referrals, external referrals may contain a facility name, provider name,
   * address and phone number
   * @since 3.0.0
   */
  get detail() {
    return GenericParser.parseObject(this.__data.detail, AWSDKPostVisitFollowUpDetail);
  }

  get time() {
    return this.type === AWSDKPostVisitFollowUpItemType.APPOINTMENT ? this.__data.time : null;
  }

  get type() {
    return GenericParser.parseEnum(this.__data.type, AWSDKPostVisitFollowUpItemType);
  }

  /**
   * @since 2.8.0
   */
  get resolved() {
    return GenericParser.parseBoolean(this.__data.resolved);
  }

  /**
   * @since 2.8.0
   */
  get createdDate() {
    return GenericParser.parseDate(this.__data.createdDate);
  }
}
export default AWSDKPostVisitFollowUpItem;
