import GenericParser from '../../internal/parser/generic_parser';
import AWSDKEntity from '../awsdk_entity';

/**
 * This class represents a prescription that was prescribed during a visit.
 * @property {String} description the description of the prescription
 * @property {Date} createDate the date by which to create the prescription
 * @property {Boolean} isDispenseAsWritten boolean true if prescription is to be dispensed as written
 * @property {Boolean} isAsNeeded boolean true if prescription as needed
 * @property {Boolean} isStopWhenDirected boolean true if the prescription is to be stoped when directed
 * @property {String} dosage the dosage of the prescription
 * @property {String} duration the duration of the prescription
 * @property {String} frequency the frequency of the prescription
 * @property {String} instructions the instructions for the prescription
 * @property {String} additionalInstructions any additional instructions for the prescription
 * @property {String} pharmacyNote a pharmacy note
 * @property {String} quantity the quantity of the prescription
 * @property {Number} refills the number of refills for the prescription
 * @property {String} measurementUnitQualifier the measurement unit qualifier
 * @property {Boolean} isHasBeenReported boolean true if has been reported
 * @property {String} rxInstructions instructions for the prescription
 * @extends model.AWSDKEntity
 */
class AWSDKVisitRx extends AWSDKEntity {
  get description() {
    return this.__data.term.description;
  }

  get createDate() {
    return this.__data.createDate ? GenericParser.parseDate(this.__data.createDate) : null;
  }

  get isDispenseAsWritten() {
    return GenericParser.parseBoolean(this.__data.dispenseAsWritten);
  }

  get isAsNeeded() {
    return GenericParser.parseBoolean(this.__data.asNeeded);
  }

  get isStopWhenDirected() {
    return GenericParser.parseBoolean(this.__data.stopWhenDirected);
  }

  get dosage() {
    return this.__data.dosage;
  }

  get duration() {
    return this.__data.duration;
  }

  get frequency() {
    return this.__data.frequency;
  }

  get instructions() {
    return this.__data.instructions;
  }

  get additionalInstructions() {
    return this.__data.addlInstr;
  }

  get pharmacyNote() {
    return this.__data.pharmacyNote;
  }

  get quantity() {
    return this.__data.quantity;
  }

  get refills() {
    return this.__data.refills;
  }

  get measurementUnitQualifier() {
    return this.__data.measurementUnitQualifier;
  }

  get isHasBeenReported() {
    return GenericParser.parseBoolean(this.__data.hasBeenReported);
  }

  get rxInstructions() {
    return this.__data.rxInstructions;
  }
}
export default AWSDKVisitRx;
