import AWSDKResponse from './awsdk_response';
import AWSDKInbox from '../../../model/secure_message/awsdk_inbox';
import GenericParser from '../../parser/generic_parser';

/**
 * Simple wrapper around an {@link model.AWSDKInbox}
 * @property {model.AWSDKInbox} inbox the {@link model.AWSDKInbox}
 * @extends model.AWSDKResponse
 */
class AWSDKInboxResponse extends AWSDKResponse {
  get inbox() {
    return this.__data.mailbox ? GenericParser.parseObject(this.__data.mailbox, AWSDKInbox) : null;
  }
}

export default AWSDKInboxResponse;
