import AWSDKLanguage from '../awsdk_language';
import AWSDKError from '../../error/awsdk_error';
import AWSDKConsumer from '../consumer/awsdk_consumer';
import Validator from '../../internal/validator/validator';

/**
 * This class represents the search criteria for finding a practice.
 * Get an instance of this via {@link service.PracticeService#getNewPracticeSearchCriteria()|PracticeService.getNewPracticeSearchCriteria}
 * @property {String} [practiceType] the practice type to search for. Possible values are: DEFAULT, EXCLUSIVE, NORMAL.
 * @property {String} [sourceId] the source Id to search for.
 * @property {model.AWSDKLanguage} [language] an AWSDKLanguage object which specifies the language.
 * @property {model.AWSDKConsumer} [consumer] an AWSDKConsumer object which specifies the consumer.
 * @property {Number} [maxResults] an integer which specifies the maximum number of results to return.
 * @since 1.3.0
 */
class AWSDKPracticeSearchCriteria {
  constructor(criteria = {}) {
    this.__practiceType = criteria.practiceType;
    this.__sourceId = criteria.sourceId;
    this.__language = criteria.language;
    this.__consumer = criteria.consumer;
    this.__maxResults = criteria.maxResults;
  }
  get practiceType() {
    return this.__practiceType;
  }
  set practiceType(practiceType) {
    this.__practiceType = practiceType;
  }
  get sourceId() {
    return this.__sourceId;
  }
  set sourceId(sourceId) {
    this.__sourceId = sourceId;
  }
  get consumer() {
    return this.__consumer;
  }
  set consumer(consumer) {
    if (!(consumer instanceof AWSDKConsumer)) {
      throw AWSDKError.AWSDKIllegalArgument('consumer must be of type AWSDKConsumer');
    }
    this.__consumer = consumer;
  }
  get language() {
    return this.__language;
  }
  set language(language) {
    if (!(language instanceof AWSDKLanguage)) {
      throw AWSDKError.AWSDKIllegalArgument('language must be of type AWSDKLanguage');
    }
    this.__language = language;
  }
  get maxResults() {
    return this.__maxResults;
  }
  set maxResults(maxResults) {
    if (!(Validator.isInt(maxResults))) {
      throw AWSDKError.AWSDKIllegalArgument('maxResults must be of type int');
    }
    this.__maxResults = maxResults;
  }
}
export default AWSDKPracticeSearchCriteria;
