import AWSDKNotifications from '../../../model/notifications/awsdk_notifications';
import GenericParser from '../../parser/generic_parser';
import AWSDKResponse from '../response/awsdk_response';

/**
 * This class represents the response obtained from a call to the telehealth platform for notifications pertaining to a {@link model.AWSDKConsumer|AWSDKConsumer}.
 * @property {model.AWSDKNotifications} notifications the notifications object
 * @since 1.3.0
 * @extends model.AWSDKResponse
 */
class AWSDKNotificationsResponse extends AWSDKResponse {
  get notifications() {
    return GenericParser.parseObject(this.__data.notifications, AWSDKNotifications);
  }
}
export default AWSDKNotificationsResponse;

