import AWSDKTrackerTemplate from '../../../model/tracker/awsdk_tracker_template';
import GenericParser from '../../parser/generic_parser';
import AWSDKResponse from './awsdk_response';

/**
 * This class represents a response whose payload is a list of {@link model.AWSDKTrackerTemplate|AWSDKTrackerTemplate}
 * @property {model.AWSDKTrackerTemplate[]} trackerTemplates an array of {@link model.AWSDKTrackerTemplate|AWSDKTrackerTemplate} objects
 * @extends model.AWSDKResponse
 * @since 1.4.0
 */
class AWSDKTrackerTemplatesList extends AWSDKResponse {
  get trackerTemplates() {
    return (Array.isArray(this.__data.trackerTemplates) && this.__data.trackerTemplates.map(item => GenericParser.parseObject(item, AWSDKTrackerTemplate))) || [];
  }
}
export default AWSDKTrackerTemplatesList;
