import AWSDKEntity from '../awsdk_entity';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKTopicType from '../awsdk_topic_type';

/**
 * This class contains all necessary information for representing a secure message.
 * @property {String} subject the subject of the message
 * @property {model.AWSDKTopicType} topicType the topic of the message
 * @property {Boolean} isLastMessageInThread whether or not the message is the last message in the thread
 * @property {Boolean} isSystemNotification whether or not the message is a system notification
 * @property {Boolean} isDeleted TRUE if the message is deleted
 * @property {Date} date the date of the message
 * @property {String} href string url that points to this secure message
 * @since 1.1.0
 * @extends model.AWSDKEntity
 */
class AWSDKSecureMessage extends AWSDKEntity {
  get subject() {
    return this.__data.subject;
  }
  get topicType() {
    return this.__data.topicType ? GenericParser.parseObject(this.__data.topicType, AWSDKTopicType) : null;
  }
  get isLastMessageInThread() {
    return GenericParser.parseBoolean(this.__data.isLastMessageInThread);
  }
  get isSystemNotification() {
    return GenericParser.parseBoolean(this.__data.isSystemNotification);
  }
  get isDeleted() {
    return GenericParser.parseBoolean(this.__data.isDeleted);
  }
  get date() {
    return GenericParser.parseDate(this.__data.date);
  }
  get href() {
    return this.__data.href;
  }
}
export default AWSDKSecureMessage;
