import GenericParser from '../../parser/generic_parser';
import AWSDKResponse from './awsdk_response';
import AWSDKPractice from '../../../model/practice/awsdk_practice';

/**
 * The AWSDKPracticeResponse class represents the response object to a call for a practice details
 * @property {model.AWSDKPractice} practiceDetails the practice details for a given practice
 * @extends model.AWSDKResponse
 */
class AWSDKPracticeResponse extends AWSDKResponse {
  get practiceDetails() {
    return GenericParser.parseObject(this.__data.practice, AWSDKPractice);
  }
}

export default AWSDKPracticeResponse;
