import AWSDKResponse from './awsdk_response';
import AWSDKHealthPlan from '../../../model/insurance/awsdk_health_plan';
import GenericParser from '../../parser/generic_parser';

class AWSDKHealthPlansResponse extends AWSDKResponse {
  get healthPlans() {
    return this.__data.healthPlans.map(item => GenericParser.parseObject(item, AWSDKHealthPlan));
  }
}

export default AWSDKHealthPlansResponse;
