import AWSDKMailbox from './awsdk_mailbox';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKInboxMessage from './awsdk_inbox_message';

/**
 * This class contains all necessary information for representing a secure message inbox.
 * @property {model.AWSDKInboxMessage[]} messages the messages in the inbox
 * @property {Number} unread the total number of unread messages
 * @since 1.1.0
 * @extends model.AWSDKMailbox
 */
class AWSDKInbox extends AWSDKMailbox {
  get messages() {
    return (this.__data.messages && this.__data.messages.map(item => GenericParser.parseObject(item, AWSDKInboxMessage))) || [];
  }
  get unread() {
    return this.__data.unread;
  }

  /**
   * @private
   */
  __assimilate(otherMailbox) {
    super.__assimilate(otherMailbox);
    this.__data.unread = otherMailbox.unread;
  }
}
export default AWSDKInbox;
