import AWSDKResponse from './awsdk_response';
import AWSDKRelationshipToSubscriberCode from '../../../model/insurance/awsdk_relationship_to_subscriber_code';
import GenericParser from '../../parser/generic_parser';

class AWSDKRelationshipsResponse extends AWSDKResponse {
  get relationshipsToSubscriber() {
    return this.__data.relationships.map(item => GenericParser.parseObject(item, AWSDKRelationshipToSubscriberCode));
  }
}

export default AWSDKRelationshipsResponse;
