import AWSDKResponse from './awsdk_response';
import AWSDKCreditCardType from '../../../model/billing/awsdk_credit_card_type';
import GenericParser from '../../parser/generic_parser';

class AWSDKCreditCardTypesResponse extends AWSDKResponse {
  get creditCardTypes() {
    return this.__data.creditCards.map(item => GenericParser.parseObject(item, AWSDKCreditCardType));
  }
}

export default AWSDKCreditCardTypesResponse;
