import AWCoreSDKVideoParticipantType from 'awcoresdk/lib/awcoresdk_video_participant_type';
import deprecated from 'awcoresdk/lib/util/deprecated';

import AWSDKEntity from '../awsdk_entity';
import AWSDKConsumer from '../consumer/awsdk_consumer';
import AWSDKProvider from '../provider/awsdk_provider';
import AWSDKVisitCost from './awsdk_visit_cost';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKChatReport from '../../model/chat/awsdk_chat_report';
import AWSDKFirstAvailableData from './awsdk_first_available_data';
import AWSDKConsumerInitiatedIVRStatus from './awsdk_consumer_initiated_ivr_status';
import AWSDKDisposition from './awsdk_disposition';
import AWSDKEndReason from './awsdk_end_reason';
import AWSDKServerLogParam from '../../internal/model/awsdk_server_log_param';
import AWSDKError from '../../error/awsdk_error';
import AWSDKConsumerFeedbackQuestion from './awsdk_consumer_feedback_question';
import AWSDKVisitExtension from './awsdk_visit_extension';
import AWSDKModality from '../awsdk_modality';
import AWSDKConferenceDetails from '../../internal/model/conference/awsdk_conference_details';
import AWSDKProviderEntries from './awsdk_provider_entries';
import AWSDKConferenceRole from '../../internal/model/conference/awsdk_conference_role';
import AWSDKConnectionType from './awsdk_connection_type';
import AWSDKIntegration from '../integration/awsdk_integration';
import AWSDKIntegrationContext from '../integration/awsdk_integration_context';

/**
 * The AWSDKVisit class represents the visit object itself.
 * @property {Number} timeRemainingMilliseconds the amount of time remaining (in milliseconds) in the visit or null
 * @property {Boolean} hidePatientTimer boolean true if the patient's timer is to be hidden during visit
 * @property {Boolean} multiwayVideoEnabled boolean true if multiway video is enabled, false if not or null if no information exist
 * @property {model.AWSDKConsumerInitiatedIVRStatus} consumerInitiatedIVRStatus the state of a consumer initiated IVR
 * @property {Number} videoInvitesSent the number of video invites that were sent for this visit
 * @property {Number} patientsAheadOfYou the number of patients ahead of a consumer for a visit
 * @property {Boolean} isProviderForTransferEligibleForQuickTransfer boolean true if the provider for transfer is eligible for a quick transfer, false otherwise
 * @property {Boolean} isSuggestedProviderEligibleForQuickTransfer boolean true if the provider suggested for a transfer is eligible for quick transfer, false otherwise
 * @property {model.AWSDKChatReport} chatReport a report of the content of the chat between provider and consumer
 * @property {model.AWSDKProvider} providerForTransfer the Provider to be used for a transfer
 * @property {Number} ivrretryCount the number of times an interactive voice response (ivr) has been retried
 * @property {Boolean} paidExtensionOffered boolean true if a paid extension has been offered, false otherwise
 * @property {String} initiatorEngagementOverridePhone the consumer's preferred phone number for this visit
 * @property {string} formattedInitiatorEngagementOverridePhone the consumer's preferred formatted phone number for this visit
 * @property {model.AWSDKConnectionType} initiatorConnectionType the software the member is using to access the visit - When using the American Well Consumer Web SDK, this will always be either WEB_SDK or WEB_SDK_AND_PHONE @since 2.0.0
 * @property {model.AWSDKConnectionType} providerConnectionType the software the provider is using to access the visit @since 2.0.0
 * @property {model.AWSDKConsumer} consumer the consumer in this visit
 * @property {model.AWSDKConsumer} consumerProxy a dependent to this consumer
 * @property {model.AWSDKDisposition} disposition  the disposition of this visit object
 * @property {model.AWSDKEndReason} endReason the reason for ending the visit
 * @property {model.AWSDKProvider} providerProxy a proxy to the provider in this visit, such as an assistant
 * @property {String} sourceId the source id of the visit used to synchronize this visit record with an external system
 * @property {Boolean} providerSuggestedEnd boolean true if the provider suggested that the visit ended, false otherwise
 * @property {model.AWSDKProvider} assignedProvider the provider object for this visit
 * @property {Boolean} finished boolean true if the visit has finished, false otherwise
 * @property {Boolean} providerConnected boolean true if the provider has connected to the visit, false otherwise
 * @property {model.AWSDKProvider} suggestedProviderForTransfer the provider object representing the suggested provider to be transferred to
 * @property {Boolean} optionForFindFirstAvailableTransferAvailable TRUE if the option of transferring to a Find First Available provider has been suggested
 * @property {Boolean} possibleFirstAvailableTransferAcceptedByConsumer TRUE if the option of transferring to a Find First Available provider has been accepted
 * @property {String} href the link url to this visit object
 * @property {model.AWSDKVisitExtension} shortExtensionEntity an object representing a shortExtension for a visit
 * @property {model.AWSDKVisitExtension} longExtensionEntity an object representing a longExtension for a visit
 * @property {model.AWSDKVisitExtension} noExtensionEntity an object representing a non-extension for a visit
 * @property {model.AWSDKVisitCost} cost the cost of the visit
 * @property {Boolean} telehealthVideoStarted boolean true if telehealthVideo has started
 * @property {String} onDemandSpecialtyId id of the specialty in which this visit takes place, if any
 * @property {model.AWSDKFirstAvailableData} firstAvailableData the first-available data associated with this visit
 * @property {String[]} guestInvitationEmails a collection of emails of guests to invite to this visit
 * @property {model.AWSDKConsumerFeedbackQuestion} consumerFeedbackQuestion the question to be presented to consumer for feedback
 * @property {model.AWSDKModality} modality represents the modality associated with this visit
 * @property {String[]} invitedGuestEmails a collection of emails of invited guests for this visit
 * @property {model.AWSDKProviderEntries} providerEntries represents information entered by provider during visit: notes, diagnoses, prescriptions, etc.
 * @property {boolean} isUsingWebRTC TRUE if this visit is configured for webRTC
 * @property {Object} integrations a map of {@link model.AWSDKIntegrationContext} to {@link model.AWSDKIntegration} integrations for this visit
 * @property {boolean} tytoLiveStreamingEnabled  TRUE if this visit allows Tyto Live stream
 * @property {boolean} hideSwitchToPhone TRUE if the `Switch To Phone` button in the visit console should be hidden for the visit.
 * @property {Boolean} isVideoCallbackActive boolean TRUE if Video Callback option should be active
 * @property {Boolean} isVideoCallbackReturnInitiated boolean TRUE if Video Callback has been accepted through another means
 * @property {Boolean} isVideoCallbackReturnCanceled boolean TRUE if Video Callback has been canceled through another means
 * @extends model.AWSDKEntity
 */
class AWSDKVisit extends AWSDKEntity {

  get timeRemainingMilliseconds() {
    return GenericParser.parseObject(this.__data.timeRemainingMilliseconds, Number);
  }

  get consumerInitiatedIVRStatus() {
    return GenericParser.parseEnum(this.__data.memberInitiatedIVRStatus, AWSDKConsumerInitiatedIVRStatus);
  }

  get hidePatientTimer() {
    return GenericParser.parseBoolean(this.__data.hidePatientTimer);
  }

  get multiwayVideoEnabled() {
    return GenericParser.parseBoolean(this.__data.multiwayVideoEnabled);
  }

  get videoInvitesSent() {
    return this.__data.videoInvitesSent;
  }

  get patientsAheadOfYou() {
    return this.__data.patientsAheadOfYou;
  }

  get isProviderForTransferEligibleForQuickTransfer() {
    return GenericParser.parseBoolean(this.__data.isProviderForManualTransferEligibleForQuickTransfer);
  }

  get isSuggestedProviderEligibleForQuickTransfer() {
    return GenericParser.parseBoolean(this.__data.isSuggestedProviderEligibleForQuickTransfer);
  }

  get chatReport() {
    return GenericParser.parseObject(this.__data.chatReport, AWSDKChatReport);
  }

  get ivrretryCount() {
    return this.__data.ivrretryCount;
  }

  get paidExtensionOffered() {
    return GenericParser.parseBoolean(this.__data.paidExtensionOffered);
  }

  get consumer() {
    return GenericParser.parseObject(this.__data.member, AWSDKConsumer);
  }

  get disposition() {
    return GenericParser.parseEnum(this.__data.disposition, AWSDKDisposition);
  }

  get endReason() {
    return GenericParser.parseEnum(this.__data.endReason, AWSDKEndReason);
  }

  get providerSuggestedEnd() {
    return GenericParser.parseBoolean(this.__data.providerSuggestedEnd);
  }

  get assignedProvider() {
    return GenericParser.parseObject(this.__data.assignedProvider, AWSDKProvider);
  }

  set assignedProvider(provider) {
    this.__data.assignedProvider = provider.__data;
  }

  get finished() {
    return GenericParser.parseBoolean(this.__data.finished);
  }

  get providerConnected() {
    return GenericParser.parseBoolean(this.__data.providerConnected);
  }

  get href() {
    return this.__data.href;
  }

  get suggestedProviderForTransfer() {
    return GenericParser.parseObject(this.__data.suggestedProviderForTransfer, AWSDKProvider);
  }

  get optionForFindFirstAvailableTransferAvailable() {
    return GenericParser.parseBoolean(this.__data.suggestedProviderForAskMeTransfer);
  }

  get possibleFirstAvailableTransferAcceptedByConsumer() {
    return GenericParser.parseBoolean(this.__data.possibleFirstAvailableTransferAcceptedByMember);
  }

  get providerForTransfer() {
    return GenericParser.parseObject(this.__data.providerForManualTransfer, AWSDKProvider);
  }

  get initiatorEngagementOverridePhone() {
    return this.__data.initiatorEngagementOverridePhone;
  }

  get formattedInitiatorEngagementOverridePhone() {
    return this.__data.formattedInitiatorEngagementOverridePhone;
  }

  get initiatorConnectionType() {
    return GenericParser.parseEnum(this.__data.initiatorConnectionType, AWSDKConnectionType);
  }

  get providerConnectionType() {
    return GenericParser.parseEnum(this.__data.providerConnectionType, AWSDKConnectionType);
  }

  get consumerProxy() {
    return GenericParser.parseObject(this.__data.memberProxy, AWSDKConsumer);
  }

  get providerProxy() {
    return GenericParser.parseObject(this.__data.providerProxy, AWSDKProvider);
  }

  get sourceId() {
    return this.__data.sourceId;
  }

  get noExtensionEntity() {
    return GenericParser.parseObject(this.__data.noExtensionEntity, AWSDKVisitExtension);
  }

  get shortExtensionEntity() {
    return GenericParser.parseObject(this.__data.shortExtensionEntity, AWSDKVisitExtension);
  }

  get longExtensionEntity() {
    return GenericParser.parseObject(this.__data.longExtensionEntity, AWSDKVisitExtension);
  }

  get cost() {
    return GenericParser.parseObject(this.__data.cost, AWSDKVisitCost);
  }

  set cost(cost) {
    this.__data.cost = cost && cost.__data;
  }

  get telehealthVideoStarted() {
    return GenericParser.parseBoolean(this.__data.consumerElectronStarted);
  }

  get canTransfer() {
    const declineAndTransfer = this.disposition === AWSDKDisposition.Declined && (this.endReason === AWSDKEndReason.PROVIDER_DECLINE_AND_TRANSFER || this.endReason === AWSDKEndReason.ASSISTANT_DECLINE_AND_TRANSFER);
    const suggestedTransfer = this.disposition === AWSDKDisposition.ConsumerCanceled && this.endReason === AWSDKEndReason.CONSUMER_TRANSFER;
    const postVisitTransfer = (this.disposition === AWSDKDisposition.Completed || this.disposition === AWSDKDisposition.ProviderWrapup);
    return this.providerForTransfer != null && (declineAndTransfer || suggestedTransfer || postVisitTransfer);
  }

  get onDemandSpecialtyId() {
    return this.__data.onDemandSpecialtyId;
  }

  set onDemandSpecialtyId(onDemandSpecialtyId) {
    this.__data.onDemandSpecialtyId = onDemandSpecialtyId;
  }
  // @internal
  get isMatchmaking() {
    if (this.firstAvailableData != null) {
      return this.firstAvailableData.isMatchmaking;
    }
    return this.__data.isMatchmaking;
  }
  // @internal
  set isMatchmaking(isMatchmaking) {
    this.__data.isMatchmaking = isMatchmaking;
  }

  get firstAvailableData() {
    return this.__data.firstAvailableData ? GenericParser.parseObject(this.__data.firstAvailableData, AWSDKFirstAvailableData) : null;
  }

  /**
   * Used for WebRTC visits to find the visit's representation of a participant based on the UUID provided by the client-side WebRTC library.
   *
   * @param {string} uuid The unique identifier for this participant in the conference.
   * @private
   * @internal
   * @since 2.0.0
   * @returns {Object} The user associated with the uuid (Provider or Consumer). undefined if the participant cannot be found.
   */
  participantById(uuid) {
    if (this.conferenceDetails) {
      const foundParticipant = this.conferenceDetails.participants.find(participant => participant.vendorId === uuid);
      if (foundParticipant) {
        if (foundParticipant.role === AWSDKConferenceRole.PROVIDER) {
          return { type: AWCoreSDKVideoParticipantType.PROVIDER, user: this.assignedProvider };
        }
        return { type: AWCoreSDKVideoParticipantType.CONSUMER, user: this.consumer };
      }
    }
    return undefined;
  }

  /**
   * A convenience method to get a {@link model.AWSDKServerLogParam|AWSDKServerLogParam} object
   * @returns {Object} the server log param object
   * @since 1.3.0
   */
  getServerLogParam() {
    return this.consumer ? new AWSDKServerLogParam(this.consumer.id.encryptedId, 'member') : null;
  }

  set firstAvailableData(firstAvailableData) {
    this.__data.firstAvailableData = firstAvailableData;
  }

  /**
   * @since 1.3.0
   */
  get guestInvitationEmails() {
    return this.__data.guestInvitationEmails;
  }
  /**
   * @since 1.3.0
   */
  set guestInvitationEmails(guestInvitationEmails) {
    if (!Array.isArray(guestInvitationEmails) || guestInvitationEmails.length === 0) {
      throw AWSDKError.AWSDKIllegalArgument('guestInvitationEmails argument is not a non-empty array');
    }
    this.__data.guestInvitationEmails = guestInvitationEmails;
  }

  /**
   * @since 1.4.0
   */
  get consumerFeedbackQuestion() {
    return GenericParser.parseObject(this.__data.consumerFeedbackQuestion, AWSDKConsumerFeedbackQuestion);
  }

  /**
   * @since 1.4.0
   * @type {model.AWSDKModality}
   */
  get modality() {
    return GenericParser.parseObject(this.__data.modality, AWSDKModality);
  }

  /**
   * A collection of emails for guest(s) already invited to this visit
   * @since 2.0.0
   */
  get invitedGuestEmails() {
    return this.__data.invitedGuestEmails || [];
  }

  /**
   * TRUE if this visit is configured for WebRTC
   * @since 2.0.0
   * @type {boolean}
   */
  get isUsingWebRTC() {
    return this.__data.conference && this.__data.conference.selectedModalityType.includes('RTC');
  }

  /**
   * Conference details for WebRTC.
   * @private
   * @since 2.0.0
   */
  get conferenceDetails() {
    return this.__data.conference ? GenericParser.parseObject(this.__data.conference, AWSDKConferenceDetails) : undefined;
  }

  /**
   * Represents information entered by provider during visit: notes, diagnoses, prescriptions, etc.
   * @type {model.AWSDKProviderEntries}
   * @since 2.0.0
   */
  get providerEntries() {
    return this.__data.providerEntries ? GenericParser.parseObject(this.__data.providerEntries, AWSDKProviderEntries) : null;
  }

  /**
   * TRUE if this visit allows adding phone
   * @since 2.0.0
   * @type {boolean}
   */
  get allowsVisitAddPhone() {
    return GenericParser.parseBoolean(this.__data.allowsVisitAddPhone);
  }

  /**
   * TRUE if this visit allows chatting via text with the provider
   *
   * @since 2.2.0
   * @type {boolean}
   */
  get allowsChatWithProvider() {
    return this.providerConnectionType !== AWSDKConnectionType.MOBILE
        && this.providerConnectionType !== AWSDKConnectionType.PHONE;
  }

  /**
   * A map of {@link model.AWSDKIntegrationContext} to {@link model.AWSDKIntegration}
   *
   * @since 2.8.0
   * @type {object}
   */
  get integrations() {
    const integration = {};
    if (this.__data.integrations) {
      Object.entries(this.__data.integrations).forEach(([key, value]) => {
        integration[GenericParser.parseEnum(key, AWSDKIntegrationContext)] = GenericParser.parseObject(value, AWSDKIntegration);
      });
    }
    return integration;
  }

  /**
   * TRUE if this visit allows Tyto Live stream
   * Used to determine when to use the Tyto Live stream functionality during the visit
   *
   * @since 2.6.0
   * @type {boolean}
   */
  get tytoLiveStreamingEnabled() {
    return GenericParser.parseBoolean(this.__data.tytoLiveStreamingEnabled);
  }

  /**
   * TRUE if the `Switch To Phone` button in the visit console should be hidden for the visit
   *
   * @since 2.6.0
   * @type {boolean}
   */
  get hideSwitchToPhone() {
    return GenericParser.parseBoolean(this.__data.hideVisitAddPhoneOptionForEngagement);
  }

  /**
   * Indicates if the Video Callback feature should be active for this visit.
   *
   * @returns {boolean} TRUE if Video Callback should be active
   * @since 3.5.0
   */
  get isVideoCallbackActive() {
    return GenericParser.parseBoolean(this.__data.isVideoCallbackActive);
  }

  /**
   * Indicates if the Video Callback has been started elsewhere. For example through an link
   * provided via SMS. This indicates the user should stop waiting on this device and return to
   * a neutral screen such as the home screen or the providers information screen.
   *
   * @returns {boolean} TRUE if Video Callback has been accepted through another means
   * @since 3.5.0
   */
  get isVideoCallbackReturnInitiated() {
    return GenericParser.parseBoolean(this.__data.isVideoCallbackReturnInitiated);
  }

  /**
   * Indicates if the Video Callback has been canceled elsewhere. For example through an link
   * provided via SMS. This indicates the user should stop waiting on this device and return to
   * a neutral screen such as the home screen or the providers information screen.
   *
   * @returns {boolean} TRUE if Video Callback has been canceled through another means
   * @since 3.5.0
   */
  get isVideoCallbackReturnCanceled() {
    return GenericParser.parseBoolean(this.__data.isVideoCallbackReturnCanceled);
  }
}

export default AWSDKVisit;
