import AWSDKResponse from './awsdk_response';
import AWSDKConsumer from '../../../model/consumer/awsdk_consumer';
import GenericParser from '../../parser/generic_parser';

class AWSDKDependentsListResponse extends AWSDKResponse {
  get dependents() {
    return (Array.isArray(this.__data.dependents) && this.__data.dependents.map(item => GenericParser.parseObject(item, AWSDKConsumer))) || [];
  }
}
export default AWSDKDependentsListResponse;
