import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';
import AWSDKTrackerEntry from '../../../model/tracker/awsdk_tracker_entry';

/**
 * This class represents a response whose payload is a list of {@link model.AWSDKTrackerEntry|AWSDKTrackerEntry}
 * @property {model.AWSDKTrackerEntry[]} trackers an array of {@link model.AWSDKTrackerEntry|AWSDKTrackerEntry}
 * @extends model.AWSDKResponse
 * @since 1.4.0
 */
class AWSDKTrackerEntriesList extends AWSDKResponse {
  get trackers() {
    return (Array.isArray(this.__data.trackers) && this.__data.trackers.map(entry => GenericParser.parseObject(entry, AWSDKTrackerEntry))) || [];
  }
}
export default AWSDKTrackerEntriesList;
