import AWSDKResponse from './awsdk_response';
import AWSDKVitals from '../../../model/health/awsdk_vitals';
import GenericParser from '../../parser/generic_parser';

/**
 * Simple wrapper around a {@link model.AWSDKVitals}
 * @property {model.AWSDKVitals} vitals the patient's vitals
 * @extends model.AWSDKResponse
 */
class AWSDKVitalsResponse extends AWSDKResponse {
  get vitals() {
    return GenericParser.parseObject(this.__data.vitals, AWSDKVitals);
  }
}

export default AWSDKVitalsResponse;
