import AWCoreSDKVideoParticipantType from 'awcoresdk/lib/awcoresdk_video_participant_type';
import AWSDKEntity from '../awsdk_entity';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKConferenceStatus from './awsdk_conference_status';
import AWSDKConferenceDetails from '../../internal/model/conference/awsdk_conference_details';
import AWSDKConferenceRole from '../../internal/model/conference/awsdk_conference_role';

/**
 * Data encapsulating a guest's video visit
 * @property {Number} remainingMins the number of minutes remaining in the video visit
 * @property {model.AWSDKConferenceStatus} conferenceStatus the current status of the visit
 * @property {Boolean} telehealthVideoStarted whether or not the visit has started
 * @property {Boolean} finished whether or not the visit can be considered complete
 * @property {Boolean} isUsingWebRTC TRUE if the visit the participant is invited to is using webRTC
 * @property {String} [displayName] The optional display name entered by the guest when joining the visit
 * @since 1.1.0
 * @extends model.AWSDKEntity
 */
class AWSDKVideoParticipant extends AWSDKEntity {
  get remainingMins() {
    return this.__data.remainingMins;
  }
  get conferenceStatus() {
    return GenericParser.parseEnum(this.__data.conferenceStatus, AWSDKConferenceStatus);
  }
  /**
   * @since 2.0.0
   */
  get finished() {
    return this.conferenceStatus === AWSDKConferenceStatus.Ended || this.conferenceStatus === AWSDKConferenceStatus.Cancelled || this.conferenceStatus === AWSDKConferenceStatus.Disabled;
  }

  // @internal
  get href() {
    return this.__data.href;
  }
  // @internal
  get conferenceId() {
    return this.__data.conferenceId;
  }
  // @internal
  get memberEntityId() {
    return this.__data.memberEntityId;
  }
  // @internal
  get providerEntityId() {
    return this.__data.providerEntityId;
  }

  /**
   * TRUE if the visit the participant is invited to is using webRTC
   * @since 2.0.0
   * @type {boolean}
   */
  get isUsingWebRTC() {
    return this.__data.conference && this.__data.conference.selectedModalityType.includes('RTC');
  }

  /**
   * The optional display name entered by the guest when joining the visit
   * @since 2.0.0
   * @type {string}
   */
  get displayName() {
    return this.__data.displayName;
  }

  /**
   * The visit's underlying conference details
   * @private
   * @since 2.0.0
   */
  get conferenceDetails() {
    return this.__data.conference ? GenericParser.parseObject(this.__data.conference, AWSDKConferenceDetails) : undefined;
  }

  /**
   * Used for WebRTC visits to find the visit's representation of a participant based on the UUID provided by the client-side WebRTC library.
   *
   * @param {string} uuid The unique identifier for this participant in the conference.
   * @private
   * @internal
   * @since 2.0.0
   * @returns {Object} The user associated with the uuid (Provider or Consumer). undefined if the participant cannot be found.
   */
  participantById(uuid) {
    if (this.conferenceDetails) {
      const foundParticipant = this.conferenceDetails.participants.find(participant => participant.vendorId === uuid);
      if (foundParticipant) {
        if (foundParticipant.role === AWSDKConferenceRole.PROVIDER) {
          return { type: AWCoreSDKVideoParticipantType.PROVIDER };
        }
        return { type: AWCoreSDKVideoParticipantType.CONSUMER };
      }
    }
    return undefined;
  }
}
export default AWSDKVideoParticipant;
