import GenericParser from '../../parser/generic_parser';
import AWSDKDataObject from '../../../model/awsdk_dataobject';

/**
 * Basic conference state metrics.
 * @property {boolean} hasAProviderAndConsumerConnected TRUE if at least one provider and one consumer are connected.
 * @property {number} consumersConnected the number of consumers connected to the visit.
 * @property {number} facilitatorsConnected the number of facilitators connected to the visit.
 * @property {number} providersConnected the number of providers connected to the visit.
 * @property {number} translatorsConnected the number of translators connected to the visit.
 * @property {number} disconnected the number of participants disconnected.
 * @property {number} failed the number of participants who failed to connect.
 * @property {number} inflightCount the number of participants that are trying to connect to the conference.
 * @property {boolean} allConnected TRUE if all participants are connected.
 * @private
 * @since 2.0.0
 */
class AWSDKConferenceMetrics extends AWSDKDataObject {
  get hasAProviderAndConsumerConnected() {
    return GenericParser.parseBoolean(this.__data.hasAProviderAndMemberConnected);
  }

  get consumersConnected() {
    return this.__data.membersConnected;
  }

  get facilitatorsConnected() {
    return this.__data.facilitatorsConnected;
  }

  get providersConnected() {
    return this.__data.providersConnected;
  }

  get translatorsConnected() {
    return this.__data.translatorsConnected;
  }

  get disconnected() {
    return this.__data.disconnected;
  }

  get failed() {
    return this.__data.failed;
  }

  get inflightCount() {
    return this.__data.inflightCount;
  }

  get allConnected() {
    return GenericParser.parseBoolean(this.__data.allConnected);
  }
}
export default AWSDKConferenceMetrics;
