import AWSDKResponse from './awsdk_response';
import AWSDKVideoInvitation from '../../../model/visit/awsdk_video_invitation';
import GenericParser from '../../parser/generic_parser';

/**
 * Simple response wrapper around a single instance of {@link model.AWSDKVideoInvitation}
 * @property {model.AWSDKVideoInvitation} videoInvitation the {@link model.AWSDKVideoInvitation}
 * @extends model.AWSDKResponse
 * @since 1.1.0
 */
class AWSDKVideoInvitationResponse extends AWSDKResponse {
  get videoInvitation() {
    return this.__data.videoInvitation ? GenericParser.parseObject(this.__data.videoInvitation, AWSDKVideoInvitation) : null;
  }
}
export default AWSDKVideoInvitationResponse;
