import GenericParser from '../../internal/parser/generic_parser';
import AWSDKVisitSchedule from './awsdk_visit_schedule';
import AWSDKEntity from '../awsdk_entity';
import AWSDKDisposition from './awsdk_disposition';
import AWSDKEndReason from './awsdk_end_reason';
import AWSDKProviderType from '../provider/awsdk_provider_type';
import AWSDKModality from '../awsdk_modality';

/**
 * A simplified representation of an {@link model.AWSDKVisit|AWSDKVisit} that has already occurred.
 * @property {model.AWSDKDisposition} disposition the disposition of the visit
 * @property {model.AWSDKVisitSchedule} schedule the schedule of the visit
 * @property {String} memberName the name of the member in the visit
 * @property {String} providerName the name of the provider in the visit
 * @property {model.AWSDKProviderType} providerSpecialty the speciality of the provider in the visit
 * @property {Boolean} completed whether or not the visit has been completed
 * @property {String} memberProxyName the name of the visit's member proxy, if any
 * @property {String} date a timestamp representing the actual start time of the visit
 * @property {String} initiatorEngagementOverridePhone the consumer's preferred phone number for this visit
 * @property {string} formattedInitiatorEngagementOverridePhone the consumer's preferred formatted phone number for this visit
 * @since 1.3.0
 * @property {model.AWSDKEndReason} endReason the reason for ending the visit
 * @since 1.4.0
 * @property {model.AWSDKModality} modality represents the modality associated with the visit
 * @extends model.AWSDKEntity
 */
class AWSDKVisitReport extends AWSDKEntity {
  get disposition() {
    return GenericParser.parseEnum(this.__data.disposition, AWSDKDisposition);
  }
  get schedule() {
    return GenericParser.parseObject(this.__data.schedule, AWSDKVisitSchedule);
  }
  get memberName() {
    return this.__data.memberName;
  }
  get providerName() {
    return this.__data.providerName;
  }
  get providerSpecialty() {
    return GenericParser.parseObject(this.__data.specialty, AWSDKProviderType);
  }
  get completed() {
    return GenericParser.parseBoolean(this.__data.completed);
  }
  get memberProxyName() {
    return this.__data.memberProxyName;
  }
  get date() {
    return this.__data.date;
  }

  get initiatorEngagementOverridePhone() {
    return this.__data.initiatorEngagementOverridePhone;
  }

  get formattedInitiatorEngagementOverridePhone() {
    return this.__data.formattedInitiatorEngagementOverridePhone;
  }

  get endReason() {
    return GenericParser.parseEnum(this.__data.endReason, AWSDKEndReason);
  }

  /**
   * @since 1.4.0
   */
  get modality() {
    return GenericParser.parseObject(this.__data.modality, AWSDKModality);
  }
}
export default AWSDKVisitReport;
