import AWSDKDataObject from './awsdk_dataobject';

/**
 * The AWSDKResource class is supposed to represent a resource that needs to be displayed in i18n scenarios
 * @property {String} resourceKey the key for this resource
 * @property {String} value the String value for this resource
 * @extends model.AWSDKDataObject
 */
class AWSDKResource extends AWSDKDataObject {
  get resourceKey() {
    return this.__data.resourceKey;
  }

  get value() {
    return this.__data.value;
  }
}
export default AWSDKResource;
