import AWSDKMailbox from './awsdk_mailbox';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKSentMessage from './awsdk_sent_message';

/**
 * This class contains all necessary information for representing a collection of sent messages.
 * @property {model.AWSDKSentMessage[]} messages the sent messages
 * @since 1.1.0
 * @extends model.AWSDKMailbox
 */
class AWSDKSentMessages extends AWSDKMailbox {
  get messages() {
    return (this.__data.messages && this.__data.messages.map(item => GenericParser.parseObject(item, AWSDKSentMessage))) || [];
  }
}
export default AWSDKSentMessages;
