import GenericParser from '../../internal/parser/generic_parser';
import AWSDKMedication from './awsdk_medication';
import AWSDKResponse from '../../internal/model/response/awsdk_response';

/**
 * This a wrapper class around a collection of {@link model.AWSDKMedication|AWSDKMedication}
 * @property {model.AWSDKMedication[]} medications a collection (Array) of {@link model.AWSDKMedication|AWSDKMedication} for the consumer
 * @extends model.AWSDKResponse
 */
class AWSDKMedicationList extends AWSDKResponse {
  get medications() {
    return this.__data.medications.map(item => GenericParser.parseObject(item, AWSDKMedication));
  }
}
export default AWSDKMedicationList;

