import AWSDKEntity from '../awsdk_entity';

/**
 * A class that represents the device pairing details
 * @property {String} pairingCode the code used for pairing a device.
 * @property {String} applicationServerUrl the URL of the remote server the device is pairing with.
 * @since 2.6.0
 * @extends model.AWSDKEntity
 */
class AWSDKDevicePairingDetails extends AWSDKEntity {
  get pairingCode() {
    return this.__data.pairingCode;
  }

  get applicationServerUrl() {
    return this.__data.applicationServerUrl;
  }
}
export default AWSDKDevicePairingDetails;
