import AWSDKDataObject from '../../../model/awsdk_dataobject';

/**
 * The AWSDKEligibilitySuggestionResponse represents an eligibility suggestion response object
 * @property {String} value the name of the suggestion response text
 * @property {String} title the localized reason title text
 * @property {String} description the localized reason description text
 * @property {String} suggestion the localized recovery suggestion text
 * @extends model.AWSDKDataObject
 */
class AWSDKEligibilitySuggestedResponse extends AWSDKDataObject {
  get value() {
    return this.__data.value;
  }

  get title() {
    return this.__data.title;
  }

  get suggestion() {
    return this.__data.suggestion;
  }

  get description() {
    return this.__data.description;
  }
}
export default AWSDKEligibilitySuggestedResponse;
