import AWSDKError from '../../error/awsdk_error';
import AWSDKLanguage from '../awsdk_language';
import Validator from '../../internal/validator/validator';
import AWSDKPractice from './awsdk_practice';
import AWSDKPracticeSubCategory from './awsdk_practice_sub_category';

/**
 * This class represents the search criteria for finding a practice's future availability.
 * Get an instance of this via {@link service.PracticeService#getNewPracticeFutureAvailabilitySearchCriteria()|PracticeService.getNewPracticeFutureAvailabilitySearchCriteria}
 * @property {String} timeZone the canonical time zone name, i.e. America/New_York
 * @property {(model.AWSDKPractice|model.AWSDKPracticeSubCategory)} [practiceOrSubCategory] the {@link model.AWSDKPractice|AWSDKPractice} or {@link model.AWSDKPracticeSubCategory|AWSDKPracticeSubCategory} whose details we want to retrieve.
 * @property {String} [searchTerm] the text used to filter the available dates by provider names.
 * @property {model.AWSDKLanguage} [languageSpoken] the {@link model.AWSDKLanguage|AWSDKLanguage} spoken to filter the available dates on.
 * @property {Date} [appointmentDate] the appointment date to filter the available dates on.
 * @property {Number} [maxResults] limit the number of results returned. Should be an integer.
 * @since 1.3.1
 */
class AWSDKPracticeFutureAvailabilitySearchCriteria {
  constructor(criteria = {}) {
    if (criteria) {
      this.__timeZone = criteria.timeZone;
      this.__practiceOrSubCategory = criteria.practiceOrSubCategory;
      this.__searchTerm = criteria.searchTerm;
      this.__languageSpoken = criteria.languageSpoken;
      this.__appointmentDate = criteria.appointmentDate;
      this.__maxResults = criteria.maxResults;
    }
  }

  get timeZone() {
    return this.__timeZone;
  }

  set timeZone(timeZone) {
    this.__timeZone = timeZone;
  }

  get practiceOrSubCategory() {
    return this.__practiceOrSubCategory;
  }

  set practiceOrSubCategory(practiceOrSubCategory) {
    if (!(practiceOrSubCategory instanceof AWSDKPractice) && !(practiceOrSubCategory instanceof AWSDKPracticeSubCategory)) {
      throw AWSDKError.AWSDKIllegalArgument('practiceOrSubCategory must be an instance of AWSDKPractice or AWSDKPracticeSubCategory');
    }
    this.__practiceOrSubCategory = practiceOrSubCategory;
  }

  get searchTerm() {
    return this.__searchTerm;
  }

  set searchTerm(searchTerm) {
    this.__searchTerm = searchTerm;
  }

  get languageSpoken() {
    return this.__languageSpoken;
  }

  set languageSpoken(languageSpoken) {
    if (!(languageSpoken instanceof AWSDKLanguage)) {
      throw AWSDKError.AWSDKIllegalArgument('languageSpoken must be of type AWSDKLanguage');
    }
    this.__languageSpoken = languageSpoken;
  }

  get appointmentDate() {
    return this.__appointmentDate;
  }

  set appointmentDate(appointmentDate) {
    if (!(appointmentDate instanceof Date)) {
      throw AWSDKError.AWSDKIllegalArgument('appointmentDate must be of type Date');
    }
    this.__appointmentDate = appointmentDate;
  }

  get maxResults() {
    return this.__maxResults;
  }

  set maxResults(maxResults) {
    if (!(Validator.isInt(maxResults))) {
      throw AWSDKError.AWSDKIllegalArgument('maxResults must be of type int');
    }
    this.__maxResults = maxResults;
  }
}
export default AWSDKPracticeFutureAvailabilitySearchCriteria;
