import AWSDKConsumerUpdate from '../consumer/awsdk_consumer_update';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKConsumerMiddleNameHandling from '../consumer/awsdk_consumer_middle_name_handling';
import Utils from '../../internal/util/util';

/**
 * Contains data needed to register a new {@link model.AWSDKConsumer|AWSDKConsumer} <br>
 * Get an instance of this via {@link service.ConsumerService#newConsumerRegistration|ConsumerService.newConsumerRegistration}
 * @property {Boolean} hasAcceptedDisclaimer boolean true if the Consumer has accepted legal disclaimer, false otherwise.
 * @property {Boolean} sendWelcomeEmail boolean true to send the welcome email, false otherwise.
 * @extends model.AWSDKConsumerUpdate
 */
class AWSDKConsumerRegistration extends AWSDKConsumerUpdate {
  get hasAcceptedDisclaimer() {
    return GenericParser.parseBoolean(this.__data.hasAcceptedDisclaimer);
  }

  set hasAcceptedDisclaimer(hasAcceptedDisclaimer) {
    this.__data.hasAcceptedDisclaimer = hasAcceptedDisclaimer;
  }

  get sendWelcomeEmail() {
    return GenericParser.parseBoolean(this.__data.sendWelcomeEmail);
  }

  set sendWelcomeEmail(sendWelcomeEmail) {
    this.__data.sendWelcomeEmail = sendWelcomeEmail;
  }

  /**
   * @private
   */
  __toRequestBody(systemConfiguration) {
    const request = {
      email: this.email,
      password: this.password,
      firstName: this.firstName,
      lastName: this.lastName,
      dob: this.dob ? Utils.formatISODate(this.dob) : undefined,
      currentLocationState: this.legalResidence,
      currentLocationCountry: this.legalResidenceCountryCode,
      phoneNumber: this.phone,
      timeZone: this.timeZone,
      sendWelcomeEmail: this.sendWelcomeEmail,
      acceptedMemberDisclaimer: this.hasAcceptedDisclaimer,
      sdkUserId: this.sdkUserId,
      brandings: this.brandings,
    };

    if (systemConfiguration.consumerAddressRequired) {
      request.address1 = this.address1;
      request.address2 = this.address2;
      request.city = this.city;
      request.zipCode = this.zipCode;
      request.addressState = this.stateCode;
      request.addressCountry = this.countryCode;
    }

    if (this.gender) {
      switch (this.gender) {
        case 'M':
          request.gender = 'MALE';
          break;
        case 'F':
          request.gender = 'FEMALE';
          break;
        case 'U':
          request.gender = 'UNKNOWN';
          break;
        default: break;
      }
    }
    if (systemConfiguration.genderSupportEnabled) {
      request.genderIdentityKey = this.genderIdentityKey;
    }
    if (systemConfiguration.consumerMiddleNameHandling === AWSDKConsumerMiddleNameHandling.INITIAL) {
      request.middleInitial = this.middleInitial;
    } else if (systemConfiguration.consumerMiddleNameHandling === AWSDKConsumerMiddleNameHandling.FULLNAME) {
      request.middleInitial = this.middleName;
    }

    return JSON.stringify(request);
  }
}
export default AWSDKConsumerRegistration;
