/*
 * See https://github.com/ismailhabib/custom-protocol-detection
 */
class CustomProtocolHandler {
  constructor(timeout) {
    this.__timeout = timeout || 10000;
  }
  _registerEvent(target, eventType, cb) {
    if (target.addEventListener) {
      target.addEventListener(eventType, cb);
      return {
        remove: () => {
          target.removeEventListener(eventType, cb);
        },
      };
    }
    target.attachEvent(eventType, cb);
    return {
      remove: () => {
        target.detachEvent(eventType, cb);
      },
    };
  }

  _createHiddenIframe(target, uri) {
    const iframe = document.createElement('iframe');
    iframe.src = uri;
    iframe.id = 'hiddenIframe';
    iframe.style.display = 'none';
    target.appendChild(iframe);

    return iframe;
  }

  openUriWithHiddenFrame(uri, failCb, successCb) {
    let timeout = null;

    let iframe = document.querySelector('#hiddenIframe');
    if (!iframe) {
      iframe = this._createHiddenIframe(document.body, 'about:blank');
    }
    iframe.focus();
    const handler = this._registerEvent(window, 'blur', () => {
      clearTimeout(timeout);
      handler.remove();
      successCb();
    });

    timeout = setTimeout(() => {
      failCb();
      handler.remove();
    }, this.__timeout);

    iframe.contentWindow.location.href = uri;
  }

  openUriWithTimeoutHack(uri, failCb, successCb) {
    let timeout = null;
    // window must have focus for blur to work
    window.focus();
    // handle page running in an iframe (blur must be registered with top level window)
    let target = window;
    while (target !== target.parent) {
      target = target.parent;
    }
    const handler = this._registerEvent(target, 'blur', () => {
      clearTimeout(timeout);
      handler.remove();
      successCb();
    });

    timeout = setTimeout(() => {
      failCb();
      handler.remove();
    }, this.__timeout);


    window.location = uri;
  }

  openUriUsingFirefox(uri, failCb, successCb) {
    let iframe = document.querySelector('#hiddenIframe');

    if (!iframe) {
      iframe = this._createHiddenIframe(document.body, 'about:blank');
    }

    try {
      iframe.contentWindow.location.href = uri;
      successCb();
    } catch (e) {
      if (e.name === 'NS_ERROR_UNKNOWN_PROTOCOL') {
        failCb();
      }
    }
  }

  openUriUsingIEInOlderWindows(uri, failCb, successCb) {
    const ieVersion = this.getInternetExplorerVersion();
    if (ieVersion === 10) {
      this.openUriUsingIE10InWindows7(uri, failCb, successCb);
    } else if (ieVersion === 9) {
      this.openUriWithHiddenFrame(uri, failCb, successCb);
    } else {
      this.openUriInNewWindowHack(uri, failCb, successCb);
    }
  }

  openUriUsingIE10InWindows7(uri, failCb, successCb) {
    const timeout = setTimeout(failCb, this.__timeout);
    window.addEventListener('blur', () => {
      clearTimeout(timeout);
      successCb();
    });

    let iframe = document.querySelector('#hiddenIframe');
    if (!iframe) {
      iframe = this._createHiddenIframe(document.body, 'about:blank');
    }
    try {
      iframe.contentWindow.location.href = uri;
    } catch (e) {
      failCb();
      clearTimeout(timeout);
    }
  }

  openUriInNewWindowHack(uri, failCb, successCb) {
    const myWindow = window.open('', '', 'width=0,height=0');

    myWindow.document.write(`<iframe src='${uri}'></iframe>`);

    setTimeout(() => {
      try {
        myWindow.location.href; /* eslint no-unused-expressions: 'off' */
        myWindow.setTimeout('window.close()', this.__timeout);
        successCb();
      } catch (e) {
        myWindow.close();
        failCb();
      }
    }, this.__timeout);
  }

  checkBrowser() {
    const isOpera = !!window.opera || navigator.userAgent.indexOf(' OPR/') >= 0;
    return {
      isOpera,
      isFirefox: typeof InstallTrigger !== 'undefined',
      isSafari: /^((?!chrome|android).)*safari/i.test(navigator.userAgent),
      isChrome: !!window.chrome && !isOpera,
      isIE: false || !!document.documentMode, // At least IE6
    };
  }

  getInternetExplorerVersion() {
    let rv = -1;
    if (navigator.appName === 'Microsoft Internet Explorer') {
      const ua = navigator.userAgent;
      const re = new RegExp('MSIE ([0-9]{1,}[.0-9]{0,})');
      if (re.exec(ua) != null) {
        rv = parseFloat(RegExp.$1);
      }
    } else if (navigator.appName === 'Netscape') {
      const ua = navigator.userAgent;
      const re = new RegExp('Trident/.*rv:([0-9]{1,}[.0-9]{0,})');
      if (re.exec(ua) != null) {
        rv = parseFloat(RegExp.$1);
      }
    }
    return rv;
  }

  launchUri(uri, failCb, successCb) {
    const failCallback = () => {
      if (failCb) {
        failCb();
      }
    };

    const successCallback = () => {
      if (successCb) {
        successCb();
      }
    };

    if (navigator.msLaunchUri) { // for IE and Edge in Win 8 and Win 10
      navigator.msLaunchUri(uri, successCb, failCb);
    } else {
      const browser = this.checkBrowser();
      if (browser.isFirefox) {
        this.openUriUsingFirefox(uri, failCallback, successCallback);
      } else if (browser.isChrome || browser.isSafari) {
        this.openUriWithTimeoutHack(uri, failCallback, successCallback);
      } else if (browser.isIE) {
        this.openUriUsingIEInOlderWindows(uri, failCallback, successCallback);
      } else {
        failCallback();
      }
    }
  }
}

export default CustomProtocolHandler;
