import deprecated from 'awcoresdk/lib/util/deprecated';

import AWSDKPracticeBase from '../../internal/model/awsdk_practice_base';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKAddress from '../awsdk_address';
import Util from '../../internal/util/util';
import AWSDKEntityId from '../../internal/model/awsdk_entity_id';
import AWSDKModality from '../awsdk_modality';

/**
 * The AWSDKPractice class representing a practice object
 * @property {Boolean} hasSmallLogo true if the practice has a small logo, false otherwise
 * @property {Boolean} hasLogo true if this practice has logo, false otherwise
 * @property {String} href the url to a more details for this practice.
 * @property {model.AWSDKAddress} address the address of the practice
 * @property {String} hours the practice's hours of operation
 * @property {String} phone the phone number associated with this practice
 * @property {String} formattedPhone the formatted phone number associated with this practice
 * @property {String} welcomeMessage the String welcome message for this practice
 * @property {String} fax the String representing the fax number for this practice
 * @property {Boolean} paymentRequiredForScheduledVisits true if payment is required for scheduled visits, false otherwise
 * @property {String} paymentRequiredForScheduledVisitsText the String text associated with paymentRequiredForScheduledVisits
 * @property {Boolean} showAvailableNow true if the practice is available now, false otherwise.
 * @property {Boolean} showScheduling true if this practice is scheduling, false otherwise
 * @property {String} smallLogoUrl the url to the small practice logo
 * @property {String} logoUrl the url to the practice logo
 * @property {model.AWSDKEntityId[]} subCategoryIds the list of subcategory ids this practice
 * @property {model.AWSDKEntityId} practiceId the id for this practice.
 * @property {String} specialtyName the specialty of this practice
 * @property {String} specialtyColor the specialty color of this practice
 * @property {String} subtitle the subtitle of this practice
 * @property {String} cardOverlayUrl the url to the practice card overlay image
 * @property {String} sourceId the sourceId associated with this practice.
 * @property {model.AWSDKModality[]} availableModalities represents the modalities that can be offered for a visit within this practice
 * @property {Boolean} hideProviderVisitCostEstimate TRUE if the provider's estimated visit cost should be hidden for this practice
 * @property {Boolean} hideCancelAppointmentLink TRUE if the practice is configured to hide the cancel appointment link
 * @property {Boolean} hasOnDemandSpecialties TRUE if the practice has OnDemandSpecialties
 * @extends model.AWSDKPracticeBase
 */
class AWSDKPractice extends AWSDKPracticeBase {
  get hasSmallLogo() {
    return GenericParser.parseBoolean(this.__data.hasSmallLogo);
  }

  get hasLogo() {
    return GenericParser.parseBoolean(this.__data.hasLogo);
  }

  get address() {
    return GenericParser.parseObject(this.__data.address, AWSDKAddress);
  }

  get hours() {
    return this.__data.hours;
  }

  get phone() {
    return this.__data.phone;
  }

  get formattedPhone() {
    return this.__data.formattedPhone;
  }

  get welcomeMessage() {
    return this.__data.welcomeMessage;
  }

  get fax() {
    return this.__data.fax;
  }

  get paymentRequiredForScheduledVisits() {
    return GenericParser.parseBoolean(this.__data.paymentRequiredForScheduledVisits);
  }

  get paymentRequiredForScheduledVisitsText() {
    return this.__data.paymentRequiredForScheduledVisitsText;
  }

  get showAvailableNow() {
    return GenericParser.parseBoolean(this.__data.showAvailableNow);
  }

  get showScheduling() {
    return GenericParser.parseBoolean(this.__data.showScheduling);
  }

  get smallLogoUrl() {
    if (this.hasSmallLogo) {
      return Util.findNamedLinkUrl(this.links, 'smallLogo');
    }
    return null;
  }

  get logoUrl() {
    if (this.hasLogo) {
      return Util.findNamedLinkUrl(this.links, 'logo');
    }
    return null;
  }

  /**
   * @private
   */
  get subCategoryIds() {
    return this.__data.subCategoryIds.map(id => GenericParser.parseObject(id, AWSDKEntityId));
  }

  get practiceId() {
    return GenericParser.parseObject(this.__data.practice, AWSDKEntityId);
  }

  get specialtyName() {
    return this.__data.specialty;
  }

  get specialtyColor() {
    return this.__data.specialtyColor;
  }

  get subtitle() {
    return this.__data.subtitle;
  }

  get cardOverlayUrl() {
    return Util.findNamedLinkUrl(this.links, 'cardOverlay');
  }

  /**
   * @since 1.2.1
   */
  get sourceId() {
    return this.__data.sourceId;
  }

  /**
   * @since 1.4.0
   */
  get availableModalities() {
    return this.__data.availableModalities ? this.__data.availableModalities.map(item => GenericParser.parseObject(item, AWSDKModality)) : [];
  }

  /**
   * @since 1.5.0
   */
  get hideProviderVisitCostEstimate() {
    return GenericParser.parseBoolean(this.__data.hideProviderVisitCostEstimate);
  }

  /**
   * @since 1.5.0
   */
  get hideCancelAppointmentLink() {
    return GenericParser.parseBoolean(this.__data.hideCancelAppointmentLink);
  }

  /**
   * @since 2.8.0
   */
  get hasOnDemandSpecialties() {
    return GenericParser.parseBoolean(this.__data.hasOnDemandSpecialties);
  }
}

export default AWSDKPractice;
