import AWSDKNamedEntity from '../awsdk_named_entity';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKUserType from '../../awsdk_user_type';

/**
 * This class contains all necessary information for representing a secure message contact.
 * @property {model.AWSDKUserType} userType the type of userType of the contact
 * @property {Boolean} acceptsSecureMessage TRUE if the contact accepts secure messages
 * @since 1.1.0
 * @extends model.AWSDKNamedEntity
 */
class AWSDKSecureMessageContact extends AWSDKNamedEntity {
  get userType() {
    return GenericParser.parseEnum(this.__data.userType, AWSDKUserType);
  }
  get acceptsSecureMessage() {
    return GenericParser.parseBoolean(this.__data.acceptsSecureMessage);
  }
}
export default AWSDKSecureMessageContact;
