import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';
import AWSDKDetailedMessage from '../../../model/secure_message/awsdk_detailed_message';

/**
 * Simple wrapper around an {@link model.AWSDKDetailedMessage}
 * @property {model.AWSDKDetailedMessage} message the {@link model.AWSDKDetailedMessage}
 * @extends model.AWSDKResponse
 */
class AWSDKDetailedMessageResponse extends AWSDKResponse {
  get message() {
    return this.__data.message ? GenericParser.parseObject(this.__data.message, AWSDKDetailedMessage) : null;
  }
}

export default AWSDKDetailedMessageResponse;
