import GenericParser from '../../internal/parser/generic_parser';
import Util from '../../internal/util/util';
import AWSDKError from '../../error/awsdk_error';
import AWSDKAppointment from './awsdk_appointment';


/**
 * This class should be used to update an appointment readiness. Get an instance of this class by calling {@see service.AppointmentService#getNewAppointmentReadinessRequest|AppointmentService.getNewAppointmentReadinessRequest}
 * @property {Date} datePassedTechCheck the date the consumer passed the tech check
 * @property {Boolean} microphonePassed a boolean describing whether or not the microphone test was successfully completed
 * @property {Boolean} speakerPassed a boolean describing whether or not the speaker test was successfully completed
 * @property {Boolean} cameraPassed a boolean describing whether or not the camera test was successfully completed
 * @property {Number} downloadSpeedMbps the numeric value representing the download speed registered by the testing tool
 * @property {Number} uploadSpeedMbps the numeric float value representing the upload speed registered by the testing tool
 * @property {Number} latencyMs the numeric float value representing the latency registered by the testing tool
 * @property {Number} jitter the numeric integer value representing the variation in latency on a packet flow between two systems
 * @property {String} microphoneDeviceName the string name of the microphone device
 * @property {String} speakerDeviceName the string name of the speaker device
 * @property {String} cameraDeviceName the string name of the front-facing camera
 * @property {String} sourceId the source id for this appointment readiness request
 * @property {Boolean} techCheckPassed a boolean defining whether or not tech check passed
 * @property {model.AWSDKAppointment} appointment the appointment associated with the readiness test
 * @since 1.4.0
 */
class AWSDKAppointmentReadinessRequest {
  // getters
  get datePassedTechCheck() {
    return this.__datePassedTechCheck;
  }
  get microphonePassed() {
    return GenericParser.parseBoolean(this.__microphonePassed);
  }
  get speakerPassed() {
    return GenericParser.parseBoolean(this.__speakerPassed);
  }
  get cameraPassed() {
    return GenericParser.parseBoolean(this.__cameraPassed);
  }
  get downloadSpeedMbps() {
    return this.__downloadSpeedMbps;
  }
  get uploadSpeedMbps() {
    return this.__uploadSpeedMbps;
  }
  get latencyMs() {
    return this.__latencyMs;
  }
  get jitter() {
    return this.__jitter;
  }
  get microphoneDeviceName() {
    return this.__microphoneDeviceName;
  }
  get cameraDeviceName() {
    return this.__cameraDeviceName;
  }
  get speakerDeviceName() {
    return this.__speakerDeviceName;
  }
  get appointment() {
    return this.__appointment;
  }
  get techCheckPassed() {
    return GenericParser.parseBoolean(this.__techCheckPassed);
  }
  get sourceId() {
    return this.__sourceId;
  }
  // setters
  set datePassedTechCheck(datePassed) {
    this.__datePassedTechCheck = datePassed;
  }
  set microphonePassed(isMicTestPassed) {
    this.__microphonePassed = isMicTestPassed;
  }
  set speakerPassed(isSpeakerTestPassed) {
    this.__speakerPassed = isSpeakerTestPassed;
  }
  set cameraPassed(isCameraTestPassed) {
    this.__cameraPassed = isCameraTestPassed;
  }
  set downloadSpeedMbps(downloadSpeed) {
    this.__downloadSpeedMbps = downloadSpeed;
  }
  set uploadSpeedMbps(uploadSpeed) {
    this.__uploadSpeedMbps = uploadSpeed;
  }
  set latencyMs(latency) {
    this.__latencyMs = latency;
  }
  set jitter(jitter) {
    this.__jitter = jitter;
  }
  set microphoneDeviceName(micName) {
    this.__microphoneDeviceName = micName;
  }
  set cameraDeviceName(cameraName) {
    this.__cameraDeviceName = cameraName;
  }
  set speakerDeviceName(speakerName) {
    this.__speakerDeviceName = speakerName;
  }
  set sourceId(sourceId) {
    this.__sourceId = sourceId;
  }
  set techCheckPassed(techCheckPassed) {
    this.__techCheckPassed = techCheckPassed;
  }
  set appointment(appointment) {
    if (!(appointment instanceof AWSDKAppointment)) {
      throw AWSDKError.AWSDKIllegalArgument('appointment must be instance of AWSDKAppointment');
    }
    this.__appointment = appointment;
  }
  /**
   * @private
   */
  __getAsRequestBody() {
    const res = {};
    if (this.__datePassedTechCheck) res.datePassedTechCheck = Util.formatISODateTime(this.__datePassedTechCheck);
    res.microphonePassed = this.microphonePassed;
    res.speakerPassed = this.speakerPassed;
    res.cameraPassed = this.cameraPassed;
    res.techCheckPassed = this.techCheckPassed;
    res.downloadSpeedMbps = this.__downloadSpeedMbps;
    res.uploadSpeedMbps = this.__uploadSpeedMbps;
    res.latencyMs = this.__latencyMs;
    res.jitter = this.__jitter;
    res.microphoneDeviceName = this.__microphoneDeviceName;
    res.speakerDeviceName = this.__speakerDeviceName;
    res.cameraDeviceName = this.__cameraDeviceName;
    res.sourceId = this.__sourceId;
    res.engagementId = this.__appointment && this.__appointment.id.encryptedId;
    return JSON.stringify(res);
  }
}
export default AWSDKAppointmentReadinessRequest;
