import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';
import AWSDKPracticeSearchResult from '../../../model/practice/awsdk_practice_search_result';

class AWSDKPracticeSearchResponse extends AWSDKResponse {
  get practiceSearchResults() {
    return this.__data.practiceSearchResults.map(item => GenericParser.parseObject(item, AWSDKPracticeSearchResult));
  }
}
export default AWSDKPracticeSearchResponse;
