import AWSDKTrackerEntryRequest from './awsdk_tracker_entry_request';
import AWSDKError from '../../error/awsdk_error';
import Validator from '../../internal/validator/validator';
import Util from '../../internal/util/util';

/**
 * A class that contains all the pertinent properties for successfully adding a tracker.
 * @property {Date} measurementDateTime the dateTime when this measurement was taken
 * @property {String} canonicalTimeZone the timeZone that the consumer is using, so that correct dates are used
 * @property {model.AWSDKTrackerEntryRequest[]} entries the array of tracker entries associated with this request
 * @since 1.4.0
 */
class AWSDKTrackerRequest {
  get measurementDateTime() {
    return this.__measurementDateTime;
  }
  get canonicalTimeZone() {
    return this.__canonicalTimeZone;
  }
  get entries() {
    return this.__entries;
  }
  set measurementDateTime(measurementDateTime) {
    if (!(measurementDateTime instanceof Date)) {
      throw AWSDKError.AWSDKIllegalArgument('measurementDateTime must be instance of Date', 'measurementDateTime');
    }
    if (measurementDateTime - new Date(Date.now()) > 0) {
      throw AWSDKError.AWSDKIllegalArgument('measurementDateTime is a date in the future', 'measurementDateTime');
    }
    this.__measurementDateTime = measurementDateTime;
  }
  set canonicalTimeZone(canonicalTimeZone) {
    if (typeof canonicalTimeZone !== 'string' || !Validator.isValidString(canonicalTimeZone)) {
      throw AWSDKError.AWSDKIllegalArgument('canonicalTimeZone must be a valid string');
    }
    this.__canonicalTimeZone = canonicalTimeZone;
  }
  set entries(entries) {
    if (!Array.isArray(entries)) {
      throw AWSDKError.AWSDKIllegalArgument('entries must be an array');
    }
    if (entries.length > 0 && entries.some(item => !(item instanceof AWSDKTrackerEntryRequest))) {
      throw AWSDKError.AWSDKIllegalArgument('All elements in "entries" must be instance of AWSDKTrackerEntryRequest');
    }
    this.__entries = entries;
  }

  /**
   * @private
   */
  __getAsRequestBody() {
    const res = {};
    if (this.__measurementDateTime) {
      res.measurementDateTime = Util.formatISODateTime(this.__measurementDateTime);
    }
    res.canonicalTimeZone = this.__canonicalTimeZone;
    res.entries = (this.__entries && this.__entries.map(item => item.__toRequestElement())) || [];
    return JSON.stringify(res);
  }
}
export default AWSDKTrackerRequest;
