import AWSDKEntity from '../awsdk_entity';

/**
 * This class contains all the meta-data needed to download the health document which it represents, including mime-type, size, link and name.
 * @property {String} name the name of this attachment
 * @property {Number} size the size of the document in kilobytes
 * @property {String} type the Mime-Type associated with this document
 * @extends model.AWSDKEntity
 */
class AWSDKHealthDocumentAttachment extends AWSDKEntity {
  get name() {
    return this.__data.name;
  }

  get size() {
    return this.__data.size;
  }

  get type() {
    return this.__data.type;
  }
}
export default AWSDKHealthDocumentAttachment;
