import AWSDKDataObject from '../awsdk_dataobject';

/**
 * This class represents a medical diagnosis made by the {@link model.AWSDKProvider|AWSDKProvider} about a condition that he/she believes is afflicting the {@link model.AWSDKConsumer|AWSDKConsumer}
 * @property {String} description the description of the diagnosis
 * @property {Number} priority the priority of the diagnosis. 0 is the highest priority
 * @property {String} code the medical code for this diagnosis.
 * @property {String} displayName the displayName for this diagnosis.
 * @extends model.AWSDKDataObject
 */
class AWSDKVisitDiagnosis extends AWSDKDataObject {
  get description() {
    return this.__data.diagnosis.description;
  }

  get priority() {
    return this.__data.priority;
  }

  get code() {
    return this.__data.diagnosis.code;
  }

  get displayName() {
    return this.__data.diagnosis.displayName;
  }
}
export default AWSDKVisitDiagnosis;
