/**
 * The AWSDKTransfer class represents a wrapper for the status of the transfer
 * as well as the visitContext and visit
 * @property {Boolean} isQuickTransfer returns true if is the visit is a quick transfer
 * @property {model.AWSDKVisitContext} visitContext the visitContext for the transfer
 * @property {model.AWSDKVisit} visit If the transfer was a quick transfer the visit will be created automatically
 */
class AWSDKTransfer {
  constructor(visitContext, visit = null) {
    this.__visitContext = visitContext;
    this.__visit = visit;
  }

  get isQuickTransfer() {
    return this.__visit != null;
  }

  get visitContext() {
    return this.__visitContext;
  }

  get visit() {
    return this.__visit;
  }
}

export default AWSDKTransfer;
