import AWSDKConsumerHealthItem from './awsdk_consumer_health_item';

/**
 * This class represents allergy information for intake and Consumer update
 * @extends {model.AWSDKConsumerHealthItem}
 * @property {String} name the name of the allergy
 * @extends model.AWSDKConsumerHealthItem
 */
class AWSDKAllergy extends AWSDKConsumerHealthItem {
  get name() {
    return this.__data.displayName;
  }
}
export default AWSDKAllergy;
