import AWSDKResponse from './awsdk_response';
import GerericParser from '../../parser/generic_parser';
import AWSDKChatReport from '../../../model/chat/awsdk_chat_report';

/**
 * This class represents a response object obtained from a call to {@link service.VisitService#addChatMessage}
 * @property {model.AWSDKChatReport} chatReport represents a report of chat messages between consumer and provider
 * @extends model.AWSDKResponse
 */
class AWSDKChatReportResponse extends AWSDKResponse {
  get chatReport() {
    return GerericParser.parseObject(this.__data.chatReport, AWSDKChatReport);
  }
}
export default AWSDKChatReportResponse;
