import AWSDKEntity from '../../../model/awsdk_entity';
import Util from '../../../internal/util/util';
import GenericParser from '../../../internal/parser/generic_parser';

/**
 * The AWSDKVisitRequestBody class represents the JSON payload that is expected on call to {@link service.VisitService#createVisit|VisitService.createVisit} method <br>
 *
 * For an appointment the data that will be sent is
 *      engagementId + user input data
 * For a visit with a specific provider
 *      memberId + providerId + user input data
 * For a visit with the first available provider
 *      memberId + onDemandSpecialtyId + user input data
 * For a transfer visit
 *      memberId + providerId + transferFromVisitId + optional first available data if the transfer used a first available search to find the provider
 *
 * The following is a list of user input data
 *      shareHealthSummary
 *      otherTopic
 *      topics
 *      callbackNumber
 *      triageIntakeId
 *      triageIntakeAnswerList
 *      disclaimers
 *      isQuickConnect
 *      sourceId
 *      consumerOverrideDetails
 *      modalityType
 *
 */
class AWSDKVisitRequestBody {
  constructor(visitContext, ignorePropagation = false) {
    this.visitContext = visitContext;
    this.ignorePropagation = ignorePropagation;
  }

  /**
   * This method is a convenience method for presenting this object as a JSON string
   * @returns {String} a json string representation of this object suitable to be used as payload for request that creates a visit
   */
  getAsJSONString(systemConfiguration) {
    const res = { connectionType: 'WebSDK' };
    const firstAvailableData = this.visitContext.firstAvailableData;

    const networkType = Util.getNetworkType();
    if (networkType) {
      res.networkType = networkType;
    }

    if (this.visitContext.engagementId != null) {
      res.engagementId = this.visitContext.engagementId;
    } else {
      res.memberId = this.visitContext.consumer != null ? this.visitContext.consumer.id.encryptedId : null;
      if (this.visitContext.onDemandSpecialtyId != null && firstAvailableData == null) { // new-style, first-available search has yet to be done
        res.onDemandSpecialtyId = this.visitContext.onDemandSpecialtyId;
      } else if (this.visitContext.onDemandSpecialtyId != null && firstAvailableData != null) { // old-style, first available search was performed using this context
        res.providerId = firstAvailableData.provider.id.encryptedId;
        res.firstAvailableSearchStartTime = firstAvailableData.getFormattedStartTime();
        res.firstAvailableSearchEndTime = firstAvailableData.getFormattedEndTime();
        if (firstAvailableData.firstAvailableSearchProviderAskCount != null) {
          res.firstAvailableProviderAskCount = firstAvailableData.firstAvailableSearchProviderAskCount;
        }
      } else { // normal visit flow
        res.providerId = this.visitContext.providerId;
      }
      if (this.visitContext.transferFromVisitId != null) {
        res.transferFromEngagementId = this.visitContext.transferFromVisitId;
      }
    }
    // if it is an engagement or if not doing a transfer
    // fill in the rest of the data
    if (this.visitContext.engagementId != null || !this.visitContext.transferFromVisitId) {
      res.shareHealthSummary = this.visitContext.shareHealthSummary;
      res.otherTopic = this.visitContext.otherTopic;
      res.topicIds = this.arrayOfIds(this.visitContext.topics ? this.visitContext.topics.filter(topic => topic.selected) : []);
      res.disclaimerIds = this.arrayOfIds(this.visitContext.disclaimers);
      res.callback = this.visitContext.callbackNumber;
      res.triageIntakeId = this.visitContext.intakeId ? this.visitContext.intakeId.encryptedId : null;
      res.triageIntakeAnswerList = this.visitContext.triageQuestionAnswers;
    }

    if (this.visitContext.isQuickConnect) {
      res.isQuickConnect = this.visitContext.isQuickConnect;
    }

    if (this.visitContext.sourceId) {
      res.sourceId = this.visitContext.sourceId;
    }

    if (this.visitContext.consumerOverrideDetails) {
      res.consumerOverrideDetails = this.visitContext.consumerOverrideDetails.__getAsRequestBody(systemConfiguration);
    }
    res.ignorePropagation = this.ignorePropagation;

    if (this.visitContext.modalityType) {
      res.modality = GenericParser.parseAndConvertEnumValue(this.visitContext.modalityType);
    }

    return JSON.stringify(res);
  }

  /**
   * Convenience method that takes an takes an array of {@link model.AWSDKEntity|AWSDKEntity} objects and returns an array of its encrypted ids <br>
   * @param {model.AWSDKEntity[]} array the array of objects whose encrypted Ids we want to extract
   * @returns {String[]} an array of encrypted Ids
   * @internal
   */
  arrayOfIds(array) {
    if (!Array.isArray(array) || array.length === 0) {
      return [];
    }
    const res = [];
    array.forEach((item) => {
      if (item instanceof AWSDKEntity) {
        const id = item.id.encryptedId;
        if (id) {
          res.push(id);
        }
      }
    });
    return res;
  }
}

export default AWSDKVisitRequestBody;
