import GenericParser from '../../internal/parser/generic_parser';
import AWSDKDataObject from '../../model/awsdk_dataobject';

/**
 * This class represents a notifications object for a {@link model.AWSDKConsumer|AWSDKConsumer} obtained from an {@link model.AWSDKNotificationsResponse|AWSDKNotificationsResponse}.
 * @property {Number} totalInboxCount the total number of secure messages in the inbox
 * @property {Number} unreadInboxCount the total number of unread secure messages in the consumer's inbox
 * @property {Number} dependentAccessRequestCount the total number of dependent access requests made to this consumer from another spouse
 * @property {Number} appointmentCount the total number of appointments for this consumer
 * @property {Boolean} isTimeForNextAppointment a boolean representing whether or not it is time for this consumer to start a visit
 * @since 1.3.0
 * @extends model.AWSDKDataObject
 */
class AWSDKNotifications extends AWSDKDataObject {
  get totalInboxCount() {
    return this.__data.totalInboxCount;
  }

  get unreadInboxCount() {
    return this.__data.unreadInboxCount;
  }

  get dependentAccessRequestCount() {
    return this.__data.dependentAccessRequestCount;
  }

  get appointmentCount() {
    return this.__data.appointmentCount;
  }

  get isTimeForNextAppointment() {
    return GenericParser.parseBoolean(this.__data.isTimeForNextAppointment);
  }
}
export default AWSDKNotifications;

