import AWSDKDataObject from '../awsdk_dataobject';

/**
 * A class that encapsulates the common properties to all trackers and their sub-components
 * @property {String} uuid the universal unique identifier for this component. This will uniquely identify the component against all other existing components
 * @property {String} title the string title name of this component
 * @since 1.4.0
 * @extends model.AWSDKDataObject
 */
class AWSDKTrackerBaseObject extends AWSDKDataObject {
  get uuid() {
    return this.__data.uuid;
  }

  get title() {
    return this.__data.title;
  }
}
export default AWSDKTrackerBaseObject;
