import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';
import AWSDKSpeedPass from '../../../model/visit/awsdk_speed_pass';

/**
 * Represents a response object wrapper to a single {@link model.AWSDKSpeedPass} object type
 * @property {model.AWSDKSpeedPass} speedPass the speedPass object representing information about the speedPass eligible visit
 * @extends model.AWSDKResponse
 */
class AWSDKSpeedPassResponse extends AWSDKResponse {
  get speedPass() {
    return GenericParser.parseObject(this.__data.speedPassEligibility, AWSDKSpeedPass);
  }
}
export default AWSDKSpeedPassResponse;
