import AWSDKVisit from './awsdk_visit';

/**
 * The AWSDKSpeedPass class represents a speed pass eligible visit.
 * For a description of the available properties see the JSDoc for {@link model.AWSDKVisit|AWSDKVisit}
 * @since 1.2.0
 * @extends model.AWSDKVisit
 */
class AWSDKSpeedPass extends AWSDKVisit {
  /**
  * @private
  */
  buildVisitRequestJson() {
    const request = {};
    request.memberId = this.consumer.id.encryptedId;
    request.accountKey = this.consumerProxy ? this.consumerProxy.id.persistentId : this.consumer.id.persistentId;
    request.previousEngagementIdForSpeedPass = this.id.encryptedId;
    if (this.onDemandSpecialtyId) {
      request.onDemandSpecialtyId = this.onDemandSpecialtyId;
    } else if (this.assignedProvider) {
      request.providerId = this.assignedProvider.id.encryptedId;
    }
    return JSON.stringify(request);
  }
}
export default AWSDKSpeedPass;
