/**
 * A class to represent the param object in server logs
 * @property {String} encryptedId the encryptedId of the entity being logged
 * @property {String} type the type of the entity
 * @since 1.3.0
 */
class AWSDKServerLogParam {
  constructor(encryptedId, type) {
    this.encryptedId = encryptedId;
    this.type = type;
  }

  toString() {
    return JSON.stringify(this);
  }
}
export default AWSDKServerLogParam;
