import AWSDKEntity from '../awsdk_entity';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKAddress from '../awsdk_address';

/**
 * This class represents the current stored payment information (credit card) for a given {@link model.AWSDKConsumer|AWSDKConsumer}
 * @extends model.AWSDKEntity
 * @property {String} billingName The billing name associated with this payment method
 * @property {model.AWSDKAddress} billingAddress The billing address associated with this payment method
 * @property {String} type The type of credit card (Visa, Mastercard, Amex)
 * @property {String} lastDigits the last 4 digits of the currently stored credit card
 * @property {Boolean} isExpired boolean true if this payment method is expired, false otherwise
 */
class AWSDKPaymentMethod extends AWSDKEntity {
  get billingName() {
    return this.__data.billingName;
  }

  get billingAddress() {
    return GenericParser.parseObject(this.__data.billingAddress, AWSDKAddress);
  }

  get type() {
    return this.__data.type;
  }

  get lastDigits() {
    return this.__data.lastDigits;
  }

  get isExpired() {
    return GenericParser.parseBoolean(this.__data.expired);
  }
}
export default AWSDKPaymentMethod;

