import AWSDKEntity from '../awsdk_entity';

/**
 * This class expands on the concept of gender to make it more inclusive to modern gender identifications
 */
class AWSDKGenderIdentity extends AWSDKEntity {
  /**
   * The string that uniquely identifies this gender identity
   * @type {string}
   * @since 2.5.0
   */
  get key() {
    return this.__data.genderKey;
  }

  /**
   * The string representing the localized display name for the gender identity
   * @type {string}
   * @since 2.5.0
   */
  get genderText() {
    return this.__data.genderText;
  }

  /**
   * The object representing a mapping of additional locales to genderText
   * @type {Object}
   * @since 2.5.0
   */
  get genderTexts() {
    return this.__data.genderTexts;
  }
}
export default AWSDKGenderIdentity;
