import GenericParser from '../../internal/parser/generic_parser';
import AWSDKTopic from './awsdk_topic';
import AWSDKAllergy from '../health/awsdk_allergy';
import AWSDKCondition from '../health/awsdk_condition';
import AWSDKMedication from '../medication/awsdk_medication';
import AWSDKDataObject from '../awsdk_dataobject';

/**
 * A representation of the intake information gathered prior to a visit starting
 * @property {model.AWSDKCondition[]} conditions the provided conditions
 * @property {model.AWSDKAllergy[]} allergies the provided allergies
 * @property {model.AWSDKMedication[]} medications the provided medications
 * @property {model.AWSDKTopic[]} topics the provided topics
 * @since 1.2.0
 * @extends model.AWSDKDataObject
 */
class AWSDKIntake extends AWSDKDataObject {
  get conditions() {
    return this.__data.conditions.map(item => GenericParser.parseObject(item, AWSDKCondition));
  }

  get allergies() {
    return this.__data.allergies.map(item => GenericParser.parseObject(item, AWSDKAllergy));
  }

  get medications() {
    return this.__data.allergies.map(item => GenericParser.parseObject(item, AWSDKMedication));
  }

  get topics() {
    return this.__data.topics.map(item => GenericParser.parseObject(item, AWSDKTopic));
  }
}
export default AWSDKIntake;