import AWSDKDataObject from '../awsdk_dataobject';
import GenericParser from '../../internal/parser/generic_parser';

/**
 * The AWSDKConsumerFeedbackQuestion class represents a single Q&A that the {@link model.AWSDKProvider|AWSDKProvider} displays to the {@link model.AWSDKConsumer|AWSDKConsumer} <br>
 * during the Consumer's visit summary review.
 * @property {Boolean} show boolean true if should show the feedback question, false otherwise
 * @property {String} questionText the question to pose to the consumer
 * @property {String[]} responseOptions an array of {@link String} representing response options to the question above
 * @extends model.AWSDKDataObject
 */
class AWSDKConsumerFeedbackQuestion extends AWSDKDataObject {
  get show() {
    return GenericParser.parseBoolean(this.__data.show);
  }

  get questionText() {
    return this.__data.questionText;
  }

  get responseOptions() {
    const respOptions = this.__data.responseOptions;
    if (respOptions == null || !(respOptions instanceof Array)) {
      return null;
    }
    return respOptions;
  }
}
export default AWSDKConsumerFeedbackQuestion;
