import GenericParser from '../../internal/parser/generic_parser';
import AWSDKMailboxMessage from './awsdk_mailbox_message';

/**
 * This class contains all necessary information for representing an inbox message.
 * @property {String} selfRecipient the self recipient of the message (current consumer)
 * @property {Boolean} isUnread whether or not the message is unread
 * @since 1.1.0
 * @extends model.AWSDKMailboxMessage
 */
class AWSDKInboxMessage extends AWSDKMailboxMessage {
  get selfRecipient() {
    return this.__data.selfRecipient;
  }
  get isUnread() {
    return GenericParser.parseBoolean(this.__data.isUnread);
  }
}
export default AWSDKInboxMessage;
