import AWSDKTrackerDataPointRequest from './awsdk_tracker_data_point_request';
import AWSDKError from '../../error/awsdk_error';
import AWSDKTrackerTemplate from './awsdk_tracker_template';

/**
 * This class represents a entry request on the tracker request object
 * @property {model.AWSDKTrackerTemplate} trackerTemplate the tracker template whose uuid we need
 * @property {model.AWSDKTrackerDataPointRequest[]} data the array of data point requests pertaining to this tracker
 * @since 1.4.0
 */
class AWSDKTrackerEntryRequest {
  get trackerTemplate() {
    return this.__trackerTemplate;
  }
  get data() {
    return this.__data;
  }
  set trackerTemplate(trackerTemplate) {
    if (!(trackerTemplate instanceof AWSDKTrackerTemplate)) {
      throw AWSDKError.AWSDKIllegalArgument('trackerTemplate must be an instance of AWSDKTrackerTemplate');
    }
    this.__trackerTemplate = trackerTemplate;
  }
  set data(data) {
    if (!Array.isArray(data)) {
      throw AWSDKError.AWSDKIllegalArgument('data must be an array');
    }
    if (data.length > 0 && data.some(item => !(item instanceof AWSDKTrackerDataPointRequest))) {
      throw AWSDKError.AWSDKIllegalArgument('All elements in "data" must be instance of AWSDKTrackerDataPointRequest');
    }
    this.__data = data;
  }
  /**
   * @private
   */
  __getAsRequestBody() {
    const res = {};
    if (this.__trackerTemplate) {
      res.uuid = this.__trackerTemplate.uuid;
    }
    res.data = (this.__data && this.__data.map(entry => entry.__getAsRequestBody())) || [];
    return JSON.stringify(res);
  }

  /**
   * This method is a convenience method for presenting this object as an array element within a JSON string
   * @private
   * @returns {Object} an element representation of this object suitable to be used as an element on a request that adds a health tracker
   */
  __toRequestElement() {
    return {
      uuid: this.__trackerTemplate ? this.__trackerTemplate.uuid : undefined,
      data: (this.__data && this.__data.map(entry => entry.__toRequestElement())) || [],
    };
  }
}
export default AWSDKTrackerEntryRequest;
