import AWSDKEntity from '../awsdk_entity';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKDevicePairingStatusEnum from './awsdk_device_pairing_status_enum';

/**
 * A class that represents the device pairing status response.
 * @property {model.AWSDKDevicePairingStatus} devicePairingStatus the device pairing status enum.
 * @property {boolean} isDeviceOnline true if the device is connected to an online network, such as WIFI or cellular.
 * @since 2.6.0
 * @extends model.AWSDKEntity
 */
class AWSDKDevicePairingStatus extends AWSDKEntity {
  get devicePairingStatus() {
    return GenericParser.parseEnum(this.__data.devicePairingStatus, AWSDKDevicePairingStatusEnum);
  }
  get isDeviceOnline() {
    return GenericParser.parseBoolean(this.__data.isDeviceOnline);
  }
}
export default AWSDKDevicePairingStatus;
