import GenericParser from '../../internal/parser/generic_parser';
import AWSDKConsumer from './awsdk_consumer';
import AWSDKEntity from '../awsdk_entity';

/**
 * This class represents the dependent access request object
 * @property {model.AWSDKConsumer[]} dependents the list of dependents associated with the guardian to whom this request was made
 * @property {model.AWSDKConsumer} guardian the legal guardian of the dependent for whom this request was made
 * @property {model.AWSDKConsumer} requestor the consumer who is asking access to a dependent
 * @since 1.3.0
 * @extends model.AWSDKEntity
 */
class AWSDKDependentAccessRequest extends AWSDKEntity {
  get dependents() {
    return (Array.isArray(this.__data.dependents) && this.__data.dependents.map(element => GenericParser.parseObject(element, AWSDKConsumer))) || [];
  }
  get guardian() {
    return GenericParser.parseObject(this.__data.guardian, AWSDKConsumer);
  }
  get requestor() {
    return GenericParser.parseObject(this.__data.requestor, AWSDKConsumer);
  }
}
export default AWSDKDependentAccessRequest;
