import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';
import AWSDKVisit from '../../../model/visit/awsdk_visit';

/**
 * Represents a response object wrapper around an array of {@link model.AWSDKVisit|AWSDKVisit} object types
 * @property {model.AWSDKVisit[]} visits the array of visit objects
 * @extends model.AWSDKResponse
 */
class AWSDKVisitsResponse extends AWSDKResponse {
  get visits() {
    return Array.isArray(this.__data.list) ?
      this.__data.list.map(item => GenericParser.parseObject(item, AWSDKVisit)) : [];
  }
}
export default AWSDKVisitsResponse;
