import AWSDKCondition from '../../../model/health/awsdk_condition';
import GenericParser from '../../parser/generic_parser';
import AWSDKResponse from './awsdk_response';

/**
 * Simple wrapper around a collection of {@link model.AWSDKCondition}s
 * @property {model.AWSDKCondition[]} conditions the {@link model.AWSDKCondition}s
 * @extends model.AWSDKResponse
 */
class AWSDKConditionsResponse extends AWSDKResponse {
  get conditions() {
    return this.__data.conditions.map(item => GenericParser.parseObject(item, AWSDKCondition));
  }
}

export default AWSDKConditionsResponse;
