import AWSDKResponse from './awsdk_response';
import AWSDKSystemConfiguration from '../../../model/awsdk_system_configuration';
import GenericParser from '../../parser/generic_parser';

class AWSDKSystemConfigurationResponse extends AWSDKResponse {
  get systemConfiguration() {
    return GenericParser.parseObject(this.__data.configuration, AWSDKSystemConfiguration);
  }
}

export default AWSDKSystemConfigurationResponse;
