/**
 * Logs messages to a local logger and to a remote logger as well.
 *
 * @private
 */
class CombinedLogger {
  constructor(localLog, remoteLog) {
    this.__log = localLog;
    this.__remoteLog = remoteLog;
    this.info = this.info.bind(this);
    this.error = this.error.bind(this);
    this.fatal = this.fatal.bind(this);
    this.warn = this.warn.bind(this);
    this.debug = this.debug.bind(this);
    this.trace = this.trace.bind(this);
  }

  /**
   * @private
   */
  fatal(visitLoggingDataObject, ...messages) {
    const logMessage = `AWSDK ${messages}`;
    this.__log.fatal(logMessage);
    // no such thing as 'fatal' for loglevel library
    this.__logToRemote(this.__log.levels.ERROR, logMessage, visitLoggingDataObject);
  }

  /**
   * @private
   */
  error(visitLoggingDataObject, ...messages) {
    const logMessage = `AWSDK ${messages}`;
    this.__log.error(logMessage);
    this.__logToRemote(this.__log.levels.ERROR, logMessage, visitLoggingDataObject);
  }
  /**
   * @private
   */
  warn(visitLoggingDataObject, ...messages) {
    const logMessage = `AWSDK ${messages}`;
    this.__log.warn(logMessage);
    this.__logToRemote(this.__log.levels.WARN, logMessage, visitLoggingDataObject);
  }
  /**
   * @private
   */
  info(visitLoggingDataObject, ...messages) {
    const logMessage = `AWSDK ${messages}`;
    this.__log.info(logMessage);
    this.__logToRemote(this.__log.levels.INFO, logMessage, visitLoggingDataObject);
  }
  /**
   * @private
   */
  debug(visitLoggingDataObject, ...messages) {
    const logMessage = `AWSDK ${messages}`;
    this.__log.debug(logMessage);
    this.__logToRemote(this.__log.levels.DEBUG, logMessage, visitLoggingDataObject);
  }
  /**
   * @private
   */
  trace(visitLoggingDataObject, ...messages) {
    const logMessage = `AWSDK ${messages}`;
    this.__log.trace(logMessage);
    this.__logToRemote(this.__log.levels.TRACE, logMessage, visitLoggingDataObject);
  }

  /**
   * @private
   */
  __logToRemote(level, message, visitLoggingDataObject) {
    if (this.__remoteLog && this.__log.getLevel() <= level) {
      this.__remoteLog.logToServer(this.getLevelString(level), message, visitLoggingDataObject);
    }
  }

  getLevelString(level) {
    switch (level) {
      case this.__log.levels.TRACE: return 'trace';
      case this.__log.levels.DEBUG: return 'debug';
      case this.__log.levels.INFO: return 'info';
      case this.__log.levels.WARN: return 'warn';
      case this.__log.levels.ERROR: return 'error';
      default: return undefined;
    }
  }
}

export default CombinedLogger;
