import GenericParser from '../../internal/parser/generic_parser';
import AWSDKEntityId from '../../internal/model/awsdk_entity_id';
import AWSDKConsumer from '../consumer/awsdk_consumer';
import AWSDKConsumerFeedbackQuestion from './awsdk_consumer_feedback_question';
import AWSDKDisclaimer from '../awsdk_disclaimer';
import AWSDKPractice from '../practice/awsdk_practice';
import AWSDKTopic from './awsdk_topic';
import AWSDKTriageQuestion from './awsdk_triage_question';
import Validator from '../../internal/validator/validator';
import AWSDKError from '../../error/awsdk_error';
import AWSDKFirstAvailableData from './awsdk_first_available_data';
import AWSDKVisitModalityType from '../visit/awsdk_visit_modality_type';
import AWSDKDataObject from '../awsdk_dataobject';

/**
 * AWSDKVisitContext class which represents the initial preparation for a visit.
 * @property {model.AWSDKConsumer} consumer the consumer to which this visit relates
 * @property {String} onDemandSpecialtyId the id of the onDemandSpecialty for this visit
 * @property {String} engagementId the id of the engagement for this visit
 * @property {Boolean} requireAddress boolean true if the address is required by provider, false otherwise.
 * @property {Boolean} canPrescribe boolean true if the provider can prescribe medication, false otherwise.
 * @property {Boolean} showTriageQuestions boolean true if the provider displays triage questions, false otherwise.
 * @property {Boolean} showConditionsQuestion boolean true if the provider shows conditions questions, false otherwise
 * @property {Boolean} showAllergiesQuestion boolean true if the provider shows allergies questions, false otherwise
 * @property {Boolean} showMedicationsQuestion boolean true if the provider shows medications questions, false otherwise
 * @property {Boolean} showVitalsQuestion boolean true if the provider shows vitals questions, false otherwise
 * @property {Boolean} hasHealthHistory boolean true if the provider has health history for the consumer, false otherwise
 * @property {model.AWSDKConsumerFeedbackQuestion} consumerFeedbackQuestion a question to ask the consumer at end of visit with possible answers
 * @property {model.AWSDKDisclaimer[]} disclaimers an array of {@link model.AWSDKDisclaimer|AWSDKDisclaimer} objects
 * @property {String[]} triageQuestionList array of {@link String} triage questions for the consumer
 * @property {model.AWSDKTopic[]} topics an array of {@link model.AWSDKTopic|AWSDKTopic}
 * @property {model.AWSDKEntityId} intakeId a {@link model.AWSDEntityId|AWSDEntityId} object with encrypted and persitent id fields
 * @property {model.AWSDKPractice} practice a {@link model.AWSDKPractice|AWSDKPractice} practice object
 * @property {Boolean} requireResidencyCheck boolean true if the provider requires residency check
 * @property {String} providerId the provider's encryptedId
 * @property {Boolean} hasProvider boolean true if the providerId is exists and it's non-empty, false otherwise
 * @property {Boolean} hasOnDemandSpecialty boolean true ith the specialtyId exists and it's non-empty, false otherwise
 * @property {Boolean} shareHealthSummary a boolean true if the provider can share health summary pertaining to consumer, false otherwise
 * @property {model.AWSDKTriageQuestion[]} triageQuestions collection of {@link model.AWSDKTriageQuestion} derived from the triageQuestionList
 * @property {String} otherTopic an additional topic to be added to this visit
 * @property {Boolean} hasAppointment boolean true if an engagementId exists and it's non-empty, false otherwise
 * @property {String} callbackNumber a phone number where the Provider can reach the consumer
 * @property {String[]} triageQuestionAnswers a collection of the answers to triage questions
 * @property {Number} timeThisWasCreated the time in milliseconds when this visitContext was created
 * @property {Boolean} isMatchmakerVisit boolean true if this is a matchmaker visit
 * @property {Boolean} [isQuickConnect] boolean true if this is a quick connect visit
 * @property {String} [sourceId] the source id for the visit
 * @property {model.AWSDKConsumer} [consumerOverrideDetails] override the consumer for the visit
 * @since 1.4.0
 * @property {model.AWSDKVisitModalityType} modalityType represents the modality type for the visit
 * @property {model.AWSDKConsumer[]} proxies array of proxies to be used for the visit
 * @extends model.AWSDKDataObject
 */
class AWSDKVisitContext extends AWSDKDataObject {
  constructor(data = {}) {
    super(data);
    this.__data.timeThisWasCreated = new Date().getTime();
  }

  get hasProvider() {
    return Validator.isValidString(this.__data.providerId);
  }

  get requireResidencyCheck() {
    return GenericParser.parseBoolean(this.__data.requireResidencyCheck);
  }

  get intakeId() {
    return GenericParser.parseObject(this.__data.intakeId, AWSDKEntityId);
  }

  get consumer() {
    const consumer = GenericParser.parseObject(this.__data.member, AWSDKConsumer);
    if (this.proxies && this.proxies.length > 0) {
      consumer.proxies = this.proxies;
    }
    return consumer;
  }

  get onDemandSpecialtyId() {
    return this.__data.onDemandSpecialtyId;
  }

  get hasOnDemandSpecialty() {
    return Validator.isValidString(this.__data.onDemandSpecialtyId);
  }

  get practice() {
    return GenericParser.parseObject(this.__data.practice, AWSDKPractice);
  }

  get engagementId() {
    return this.__data.engagementId;
  }

  get hasAppointment() {
    return Validator.isValidString(this.__data.engagementId);
  }

  get requireAddress() {
    return GenericParser.parseBoolean(this.__data.requireAddress);
  }

  get canPrescribe() {
    return GenericParser.parseBoolean(this.__data.canPrescribe);
  }

  get showTriageQuestions() {
    return GenericParser.parseBoolean(this.__data.showTriageQuestions);
  }

  get showConditionsQuestion() {
    return GenericParser.parseBoolean(this.__data.showConditionsQuestion);
  }

  get showAllergiesQuestion() {
    return GenericParser.parseBoolean(this.__data.showAllergiesQuestion);
  }

  get showMedicationsQuestion() {
    return GenericParser.parseBoolean(this.__data.showMedicationsQuestion);
  }

  get showVitalsQuestion() {
    return GenericParser.parseBoolean(this.__data.showVitalsQuestion);
  }

  get hasHealthHistory() {
    return GenericParser.parseBoolean(this.__data.hasHealthHistory);
  }

  get topics() {
    if (this.__topics === undefined) {
      const topics = this.__data.topics;
      if (topics != null && Array.isArray(topics) && topics.length > 0) {
        this.__topics = topics.map(item => GenericParser.parseObject(item, AWSDKTopic));
      } else {
        this.__topics = null;
      }
    }
    return this.__topics;
  }

  get shareHealthSummary() {
    return GenericParser.parseBoolean(this.__data.shareHealthSummary);
  }

  get consumerFeedbackQuestion() {
    return GenericParser.parseObject(this.__data.consumerFeedbackQuestion, AWSDKConsumerFeedbackQuestion);
  }

  get disclaimers() {
    const disclaimers = this.__data.disclaimers;
    if (disclaimers == null || !Array.isArray(disclaimers) || disclaimers.length === 0) {
      return null;
    }
    return disclaimers.map(item => GenericParser.parseObject(item, AWSDKDisclaimer));
  }

  get triageQuestionList() {
    return this.__data.triageQuestionList;
  }

  get providerId() {
    return this.__data.providerId;
  }

  get triageQuestions() {
    if (this.__data.triageQuestions == null) {
      const triageQs = [];
      const triageQL = this.triageQuestionList;
      if (!Array.isArray(triageQL) || triageQL.length === 0) {
        this.__data.triageQuestions = triageQs;
        return this.__data.triageQuestions;
      }
      triageQL.forEach((item) => {
        const triageQuestion = new AWSDKTriageQuestion();
        triageQuestion.question = item;
        triageQs.push(triageQuestion);
      });
      this.__data.triageQuestions = triageQs;
    }
    return this.__data.triageQuestions;
  }

  get otherTopic() {
    return this.__data.otherTopic;
  }

  get callbackNumber() {
    return this.__data.callbackNumber;
  }

  get triageQuestionAnswers() {
    const answers = [];
    const questions = this.triageQuestions;
    if (questions != null) {
      questions.forEach((item) => {
        const answ = item.answer || '';
        answers.push(answ);
      });
    }
    return answers;
  }

  get timeThisWasCreated() {
    return this.__data.timeThisWasCreated;
  }

  get isMatchmakerVisit() {
    return GenericParser.parseBoolean(this.__data.isMatchmakerVisit);
  }

  get isQuickConnect() {
    return GenericParser.parseBoolean(this.__data.isQuickConnect);
  }

  set isQuickConnect(isQuickConnect) {
    this.__data.isQuickConnect = isQuickConnect;
  }

  get sourceId() {
    return this.__data.sourceId;
  }

  set sourceId(sourceId) {
    this.__data.sourceId = sourceId;
  }

  get consumerOverrideDetails() {
    return this.__data.consumerOverrideDetails;
  }

  set consumerOverrideDetails(consumerOverrideDetails) {
    this.__data.consumerOverrideDetails = consumerOverrideDetails;
  }

  /**
   *  @since 1.3.1
   */
  get proxies() {
    return (Array.isArray(this.__data.proxies) && this.__data.proxies.map(element => GenericParser.parseObject(element, AWSDKConsumer))) || [];
  }

  set callbackNumber(callbackNumber) {
    this.__data.callbackNumber = callbackNumber;
  }

  set shareHealthSummary(shareHealthSummary) {
    this.__data.shareHealthSummary = shareHealthSummary;
  }

  set otherTopic(otherTopic) {
    this.__data.otherTopic = otherTopic;
  }

  /**
   *  @since 1.3.1
   */
  set proxies(proxies) {
    if (Array.isArray(proxies) && proxies.every(item => item instanceof AWSDKConsumer)) {
      this.__data.proxies = proxies.map(item => item.__data);
    } else {
      this.__data.proxies = null;
    }
  }

  /**
   * @since 1.4.0
   */
  get modalityType() {
    return this.__data.modality ? GenericParser.parseEnum(this.__data.modality, AWSDKVisitModalityType) : null;
  }

  /**
   * @since 1.4.0
   */
  set modalityType(modalityType) {
    let modalityValue;
    try {
      modalityValue = GenericParser.parseAndConvertEnumValue(modalityType);
    } catch (error) {
      modalityValue = null;
    }
    this.__data.modality = modalityValue;
  }

  /**
   * Get transferFromVisitId
   * @private
   * @type {String}
   */
  get transferFromVisitId() {
    return this.__data.transferFromVisitId;
  }
  /**
   * Get proposed coupon code used to apply a coupon during transfers
   * @private
   * @type {String}
   */
  get proposedCouponCode() {
    return this.__data.proposedCouponCode;
  }

  /**
   * Get the first-available information related to this visit, if any
   * @private
   * @type {model.AWSDKFirstAvailableData}
   */
  get firstAvailableData() {
    return this.__data.firstAvailableData ? GenericParser.parseObject(this.__data.firstAvailableData, AWSDKFirstAvailableData) : null;
  }

  /**
   * @private
   */
  set firstAvailableData(firstAvailableData) {
    this.__data.firstAvailableData = firstAvailableData;
  }

  /**
   * This method will copy data from the visit being transferred from.
   * @private
   */
  setupTransfer(visit) {
    this.__data.transferFromVisitId = visit.id.encryptedId;
    if (visit.cost && visit.cost.proposedCouponCode) {
      this.__data.proposedCouponCode = visit.cost.proposedCouponCode;
    }
    if (visit.modality) {
      this.__data.modality = visit.modality.name;
    }
    this.isQuickConnect = visit.isProviderForTransferEligibleForQuickTransfer;
  }
}
export default AWSDKVisitContext;
