import AWSDKDataObject from '../awsdk_dataobject';

/**
 * Note made by an {@link model.AWSDKProvider|AWSDKProvider} during an {@link model.AWSDKVisit|AWSDKVisit}
 * @property {String} message the contents of the note
 * @since 1.2.1
 * @extends model.AWSDKDataObject
 */
class AWSDKProviderNote extends AWSDKDataObject {
  get message() {
    return this.__data.message;
  }
}
export default AWSDKProviderNote;
