import AWSDKEntity from '../awsdk_entity';
import GenericParser from '../../internal/parser/generic_parser';

/**
 * AWSDKTopic class represents any item of interest to be discussed between provider and consumer.
 * @property {String} title the title of this topic
 * @property {String} description the description of this topic
 * @property {Boolean} selected boolean true if this topic has been selected
 * @extends model.AWSDKEntity
 */
class AWSDKTopic extends AWSDKEntity {
  get title() {
    return this.__data.title;
  }

  get description() {
    return this.__data.description;
  }

  set selected(selected) {
    this.__data.selected = selected;
  }

  get selected() {
    return GenericParser.parseBoolean(this.__data.selected);
  }
}
export default AWSDKTopic;
