/**
 * The AWSDKAppointmentUpdateRequest class represents a request to update an Appointment for a consumer. It is an expected parameter for the {@link service.AppointmentService#updateAppointment|AppointmentService.updateAppointment} function <br>
 * Get an instance of this via {@link service.AppointmentService#getNewAppointmentUpdateRequest|AppointmentService.getNewAppointmentUpdateRequest}
 * @property {String} initiatorOverridePhoneNumber the override phone number associated with the appointment update request
 * @since 1.4.0
 */
class AWSDKAppointmentUpdateRequest {
  constructor(appointment) {
    this.__appointment = appointment;
  }

  get initiatorOverridePhoneNumber() {
    return this.__initiatorOverridePhoneNumber;
  }

  set initiatorOverridePhoneNumber(initiatorOverridePhoneNumber) {
    this.__initiatorOverridePhoneNumber = initiatorOverridePhoneNumber;
  }

  /**
   * This method is a convenience method for presenting this object as a JSON string
   * @returns {String} a json string representation of this object suitable to be used as payload for request that updates an appointment
   */
  getAsJSONString() {
    const request = {};
    request.initiatorOverridePhoneNumber = this.initiatorOverridePhoneNumber || undefined;
    return JSON.stringify(request);
  }
}
export default AWSDKAppointmentUpdateRequest;
