import AWSDKResponse from './awsdk_response';
import AWSDKVideoParticipant from '../../../model/visit/awsdk_video_participant';
import GenericParser from '../../parser/generic_parser';

/**
 * Simple response wrapper around a single instance of {@link model.AWSDKVideoParticipant}
 * @property {model.AWSDKVideoParticipant} videoParticipant the {@link model.AWSDKVideoParticipant}
 * @extends model.AWSDKResponse
 */
class AWSDKVideoInvitationResponse extends AWSDKResponse {
  get videoParticipant() {
    return this.__data.videoParticipant ? GenericParser.parseObject(this.__data.videoParticipant, AWSDKVideoParticipant) : null;
  }
}
export default AWSDKVideoInvitationResponse;
