import GenericParser from '../../internal/parser/generic_parser';
import AWSDKDataObject from '../awsdk_dataobject';
import Util from '../../internal/util/util';
import AWSDKGender from '../consumer/awsdk_gender';
import AWSDKUserType from '../../awsdk_user_type';

/**
 * @extends model.AWSDKDataObject
 * @property {String} message the chat message text
 * @property {Date} date the date and time the message was originally sent
 * @property {Boolean} isSelf boolean true if the current consumer sent the message, false otherwise
 * @property {String} firstName the first name of the person who sent the message
 * @property {String} lastName the last name of the person who sent the message
 * @property {String} fullName the full name of the person who sent the message
 * @property {Number} ordinal a number representing the position of this message in relation to other messages
 * @property {String} messageType the message type
 * @property {model.AWSDKGender} gender the gender of the person who sent the message
 * @property {String} localizedGender the localized gender of the person who sent the message
 * @property {model.AWSDKUserType} userType the type of the user who created the chat item
 * @since 1.1.0
 */
class AWSDKChatItem extends AWSDKDataObject {
  get message() {
    return Util.b64DecodeUnicode(this.__data.message);
  }

  get isSelf() {
    return GenericParser.parseBoolean(this.__data.isSelf);
  }

  get date() {
    return GenericParser.parseDate(this.__data.timeStamp);
  }

  get fullName() {
    return this.__data.fullName;
  }

  get firstName() {
    return this.__data.firstName;
  }

  get lastName() {
    return this.__data.lastName;
  }

  get ordinal() {
    return this.__data.ordinal;
  }

  get messageType() {
    return this.__data.messageType;
  }

  get gender() {
    return GenericParser.parseEnum(this.__data.genderEnum, AWSDKGender);
  }

  get localizedGender() {
    return this.__data.localizedGenderDisplayName;
  }

  get userType() {
    return GenericParser.parseEnum(this.__data.userType, AWSDKUserType);
  }
}
export default AWSDKChatItem;
