import GenericParser from '../../parser/generic_parser';
import AWSDKEligibilityCheck from '../../../model/insurance/awsdk_eligibility_check';
import AWSDKResponse from './awsdk_response';

/**
 * The AWSDKEligibilityResponse represents a wrapper around the AWSDKEligibilityCheck object
 * @extends model.AWSDKResponse
 */
class AWSDKEligibilityResponse extends AWSDKResponse {
  get eligibilityCheck() {
    return GenericParser.parseObject(this.__data.eligibilityCheck, AWSDKEligibilityCheck);
  }
}
export default AWSDKEligibilityResponse;
