import AWSDKError from '../../error/awsdk_error';
import AWSDKVisit from '../visit/awsdk_visit';
import AWSDKExamDataRequest from '../device_integration/awsdk_exam_data_request';
import GenericParser from '../../internal/parser/generic_parser';

/**
 * This class represents the device integration request object.
 * Get an instance of this class by calling {@see service.ConsumerService#getNewDeviceIntegrationRequest|ConsumerService.getNewDeviceIntegrationRequest}
 * @property {model.AWSDKExamDataRequest[]} examDataEntries the list of exam data entries associated with the device integration request
 * @property {model.AWSDKVisit} [visit] the optional visit associated with this device integration request
 * @since 1.4.0
 */
class AWSDKDeviceIntegrationRequest {
  constructor(examDataEntries, visit = null) {
    this.__validate(examDataEntries, visit);
    this.__examDataEntries = examDataEntries;
    this.__visit = visit;
  }

  get examDataEntries() {
    return (Array.isArray(this.__examDataEntries) && this.__examDataEntries.map(item => GenericParser.parseObject(item, AWSDKExamDataRequest))) || [];
  }

  /**
   * This is a convenience method for validating the constructor's parameters
   * @private
   */
  __validate(examDataEntries, visit) {
    if (!Array.isArray(examDataEntries)) {
      throw AWSDKError.AWSDKIllegalArgument('examDataEntries must be of type Array');
    }
    const validationErrors = [];
    examDataEntries.forEach((examDataEntry) => {
      if (!(examDataEntry instanceof AWSDKExamDataRequest)) {
        validationErrors.push(AWSDKError.AWSDKValidationError('examDataEntries', examDataEntry));
      }
    });
    if (validationErrors.length > 0) {
      throw AWSDKError.AWSDKValidationErrors(validationErrors);
    }
    if (visit && !(visit instanceof AWSDKVisit)) {
      throw AWSDKError.AWSDKIllegalArgument('visit must be of type AWSDKVisit');
    }
  }
  /**
   * This method is a convenience method for presenting this object as a JSON string
   * @private
   * @returns {String} a JSON string representation of this object suitable to be used as payload for a request that adds device integration exam data
   */
  __toRequestBody() {
    const request = {
      visitId: this.__visit ? this.__visit.id.encryptedId : undefined,
      examDataEntries: this.examDataEntries ? this.examDataEntries.map(item => item.__toRequestElement()) : undefined,
    };
    return JSON.stringify(request);
  }
}
export default AWSDKDeviceIntegrationRequest;
