import AWSDKPaymentMethod from '../../../model/billing/awsdk_payment_method';
import GenericParser from '../../parser/generic_parser';
import AWSDKResponse from '../response/awsdk_response';

class AWSDKPaymentMethodResponse extends AWSDKResponse {
  get paymentMethod() {
    return GenericParser.parseObject(this.__data.payment, AWSDKPaymentMethod);
  }
}
export default AWSDKPaymentMethodResponse;
