import AWSDKEntity from '../awsdk_entity';
import AWSDKError from '../../error/awsdk_error';
import AWSDKConsumerMiddleNameHandling from './awsdk_consumer_middle_name_handling';
import Utils from '../../internal/util/util';

/**
 * AWSDKBaseConsumerUpdate represents a base interface for {@link model.AWSDKConsumer|AWSDKConsumer} and Dependent update and registration data. <br>
 * See {@link model.AWSDKConsumerRegistration|AWSDKConsumerRegistration} for usage.
 * @property {String} firstName the consumer's first name.
 * @property {String} middleInitial the consumer's middle initial
 * @property {String} middleName the consumer's middle name
 * @property {String} lastName the consumer's last name
 * @property {Date} dob the consumer's date of birth
 * @property {String} gender the consumer's gender
 * @property {String} sdkUserId the sdkUserId which associates an external ID with this consumer and the SDK configuration in use
 * @property {String} sourceId the consumer's source ID
 * @property {String[]} brandings an Array of Strings representing a consumer's branding attributes.
 * @extends model.AWSDKEntity
 */
class AWSDKBaseConsumerUpdate extends AWSDKEntity {
  get firstName() {
    return this.__data.firstName;
  }

  get middleInitial() {
    return this.__data.middleInitial;
  }

  get middleName() {
    return this.__data.middleName;
  }

  get lastName() {
    return this.__data.lastName;
  }

  get dob() {
    return this.__data.dob;
  }

  get gender() {
    return this.__data.gender;
  }

  /**
   * @since 2.0.0
   */
  get sdkUserId() {
    return this.__data.sdkUserId;
  }

  /**
   * @since 1.2.2
   */
  get sourceId() {
    return this.__data.sourceId;
  }

  set firstName(firstName) {
    this.__data.firstName = firstName;
  }

  set middleInitial(middleInitial) {
    this.__data.middleInitial = middleInitial;
  }

  set middleName(middleName) {
    this.__data.middleName = middleName;
  }

  set lastName(lastName) {
    this.__data.lastName = lastName;
  }

  set dob(dob) {
    if (!(dob instanceof Date)) {
      throw AWSDKError.AWSDKIllegalArgument('dob');
    }
    this.__data.dob = dob;
  }

  set gender(gender) {
    this.__data.gender = gender;
  }

  /**
   * @since 1.2.2
   */
  set sourceId(sourceId) {
    this.__data.sourceId = sourceId;
  }

  /**
   * @since 2.0.0
   */
  set sdkUserId(sdkUserId) {
    this.__data.sdkUserId = sdkUserId;
  }

  /**
   * @since 1.5.0
   */
  get brandings() {
    return this.__data.brandings;
  }

  /**
   * @since 1.5.0
   */
  set brandings(brandingArr) {
    this.__data.brandings = brandingArr;
  }

  /**
   * A string representing the genderIdentityKey
   * @type {string}
   * @since 2.5.0
   */
  get genderIdentityKey() {
    return this.__data.genderIdentityKey;
  }

  /**
   * The setter for the genderIdentityKey
   * @since 2.5.0
   */
  set genderIdentityKey(key) {
    this.__data.genderIdentityKey = key;
  }

  /**
   * @private
   */
  __toRequestObject(systemConfiguration) {
    const request = {
      firstName: this.firstName,
      lastName: this.lastName,
      dob: this.dob ? Utils.formatISODate(this.dob) : undefined,
      sdkUserId: this.sdkUserId,
      sourceId: this.sourceId,
    };

    if (this.gender) {
      switch (this.gender) {
        case 'M':
          request.gender = 'MALE';
          break;
        case 'F':
          request.gender = 'FEMALE';
          break;
        case 'U':
          request.gender = 'UNKNOWN';
          break;
        default: break;
      }
    }
    if (systemConfiguration.genderSupportEnabled && this.genderIdentityKey) {
      request.genderIdentityKey = this.genderIdentityKey;
    }
    if (systemConfiguration.consumerMiddleNameHandling === AWSDKConsumerMiddleNameHandling.INITIAL) {
      request.middleName = this.middleInitial;
    } else if (systemConfiguration.consumerMiddleNameHandling === AWSDKConsumerMiddleNameHandling.FULLNAME) {
      request.middleName = this.middleName;
    }

    return request;
  }

  /**
   * @private
   */
  __toRequestBody(systemConfiguration) {
    return JSON.stringify(this.__toRequestObject(systemConfiguration));
  }
}
export default AWSDKBaseConsumerUpdate;
