import AWSDKEntity from '../awsdk_entity';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKAgendaItemFollowUpType from './awsdk_agenda_item_follow_up_type';

/**
 * This class represents an agenda follow-up item pertaining to the {@link model.AWSDKConsumer|AWSDKConsumer} that should be followed up with a {@link model.AWSDKProvider|AWSDKProvider}.
 * @property {String} description the description of the agenda item
 * @property {Boolean} isRecurring boolean true if this is a recurring agenda item
 * @property {String} duration the duration for which to continue this agenda item. Will return value if is a recurring agenda item
 * @property {String} frequency the frequency of the recurrence of this item. Will return value if is a recurring agenda item
 * @property {model.AWSDKAgendaItemFollowUpType} type the agenda item type
 * @extends model.AWSDKEntity
 */
class AWSDKAgendaItemFollowUp extends AWSDKEntity {
  get description() {
    return this.__data.description;
  }

  get isRecurring() {
    return GenericParser.parseBoolean(this.__data.recurring);
  }

  get duration() {
    return this.__data.duration;
  }

  get frequency() {
    return this.__data.frequency;
  }

  get type() {
    return GenericParser.parseEnum(this.__data.type, AWSDKAgendaItemFollowUpType);
  }
}
export default AWSDKAgendaItemFollowUp;
