import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';
import AWSDKEstimatedVisitCost from '../../../model/visit/awsdk_estimated_visit_cost';

/**
 * This class represents a response from a call to {@link service.ProviderService#getEstimatedVisitCost|ProviderService.getEstimatedVisitCost()}
 * @property {model.AWSDKEstimatedVisitCost} estimatedCost an object representing the estimated visit cost with a Provider
 * @extends model.AWSDKResponse
 */
class AWSDKEstimatedVisitCostResponse extends AWSDKResponse {
  get estimatedCost() {
    return GenericParser.parseObject(this.__data.estimatedCost, AWSDKEstimatedVisitCost);
  }
}
export default AWSDKEstimatedVisitCostResponse;
