import AWSDKDataObject from '../awsdk_dataobject';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKEligibilitySuggestedResponse from '../../internal/model/response/awsdk_eligibility_suggested_response';
import AWSDKEligibilityRequestStatus from './awsdk_eligibility_request_status';

/**
 * The AWSDKVisitCost class represents an object with all the necessary information for the estimated cost of a visit <br>
 * @property {String} costCalculationStatus the property representing the status of a cost calculation.
 * If the status is 'FINISHED' then the cost calculation is completed. If it's 'FAILED' then the cost calculation failed.
 * @property {String} couponCode the coupon code that has been applied to this visit.
 * @property {String} href the link to this cost of engagement object
 * @property {String} proposedCouponCode a coupon code that can be applied to the visit for discounts
 * @property {String} deferredBillingText text to be displayed if deferred billing is enabled
 * @property {String} deferredBillingWrapUpText text to be displayed while doing wrap up if deferred billing is enabled
 * @property {Number} extensionCost the cost value of an extension with the provider
 * @property {Number} baseCost the base cost of the visit for this provider without fees or copay.
 * @property {Boolean} zeroCostVisit boolean true if this is a visit with a cost of zero, false otherwise
 * @property {Boolean} totalCostWaived boolean true if the total cost of this visit is waived, false otherwise
 * @property {String} paymentMessage text to be displayed when asking for payment info
 * @property {Number} expectedConsumerCopayCost the amount that the consumer is expected to pay as copay for a visit
 * @property {Boolean} canApplyCouponCode boolean true if this visit allows coupoun codes to be applied to it
 * @property {Boolean} deferredBillingInEffect boolean true if deferred billing is in effect
 * @property {Boolean} hasCostChangedWithVisitTransfer boolean true if the cost of the visit has changed due to a transfer, false otherwise
 * @property {model.AWSDKEligibilityRequestStatus} eligibilityRequestStatus text represents the status of the eligibility request.
 * @property {model.AWSDKEligibilitySuggestedResponse} eligibilityRequestErrorSuggestion text representing a suggestion in case of eligibility request error
 * @property {String} eligibilityRequestError text representing the error message to an eligibility request
 * @property {String} eligibilityExceptionReason text representing an explanation for the reason why an eligibility request threw an error
 * @extends model.AWSDKDataObject
 */
class AWSDKVisitCost extends AWSDKDataObject {
  get costCalculationStatus() {
    return this.__data.costCalculationStatus;
  }

  get couponCode() {
    return this.__data.couponCode;
  }

  get href() {
    return this.__data.href;
  }

  get proposedCouponCode() {
    return this.__data.proposedCouponCode;
  }

  get eligibilityRequestErrorSuggestion() {
    return GenericParser.parseObject(this.__data.eligibilityRequestErrorSuggestion, AWSDKEligibilitySuggestedResponse);
  }

  get deferredBillingText() {
    return this.__data.deferredBillingText;
  }

  get deferredBillingWrapUpText() {
    return this.__data.deferredBillingWrapUpText;
  }

  get extensionCost() {
    return this.__data.extensionCost;
  }

  get baseCost() {
    return this.__data.baseCost;
  }

  get zeroCostVisit() {
    return GenericParser.parseBoolean(this.__data.zeroCostVisit);
  }

  get totalCostWaived() {
    return GenericParser.parseBoolean(this.__data.totalCostWaived);
  }

  get paymentMessage() {
    return this.__data.paymentMessage;
  }

  get eligibilityRequestStatus() {
    return GenericParser.parseEnum(this.__data.eligibilityRequestStatus, AWSDKEligibilityRequestStatus);
  }

  get expectedConsumerCopayCost() {
    return this.__data.expectedMemberCopayCost;
  }

  get canApplyCouponCode() {
    return GenericParser.parseBoolean(this.__data.canApplyCouponCode);
  }

  get deferredBillingInEffect() {
    return GenericParser.parseBoolean(this.__data.deferredBillingInEffect);
  }

  get hasCostChangedWithVisitTransfer() {
    return GenericParser.parseBoolean(this.__data.hasCostChangedWithVisitTransfer);
  }

  get eligibilityRequestError() {
    return this.__data.eligibilityRequestError;
  }

  get eligibilityExceptionReason() {
    return this.__data.eligibilityExceptionReason;
  }
}
export default AWSDKVisitCost;
