/**
* These utilities are designed to do mimetype lookup/sniffing based on the header bytes in a  blob.
*
* blob.type is used first, but is not always defined so this code is a fallback.
*
* At the time of writing this code, a simple library didn't exist that was ie11 friendly (or babel friendly)
* and not massive. If one/a better approach becomes available this code can be removed.
*/


/**
 * Takes in a magic number string and finds the corresponding mimetype.
 *
 * Built by cross referencing the list of mimetypes in Mimetype.java in caretalks and
 * https://en.wikipedia.org/wiki/List_of_file_signatures
 * https://www.garykessler.net/library/file_sigs.html
 *
 * Note:
 * In general it seems the collapsing of multiple mimetypes into one is OK if there is only 1 magic number.
 * i.e. image/bmp == image/x-ms-bmp
 * also text/plain aka txt files don't have magic numbers
 *
 * @param {string} magicNumber the magic number to look up and convert to a mimetype
 * @return {string|undefined} the mimeType or undefined if no match found
 * @private
 */
export function lookupMimetypeFromMagicNumber(magicNumber) {
  const lookup = (mn) => {
    switch (mn) {
      case '25504446':
        return 'application/pdf';
      case 'FFD8FFDB':
      case 'FFD8FFE0':
      case 'FFD8FFEE':
      case 'FFD8FFE1':
        return 'image/jpeg';
      case '89504E47':
        return 'image/png';
      case '424D':
        return 'image/bmp'; // aka image/x-ms-bmp
      case '47494638':
        return 'image/gif';
      case '492049':
      case '4D4D002B':
      case '49492A00':
      case '4D4D002A':
        return 'image/tif'; // aka image/tiff
      case '52494646':
        return 'audio/wav';
      case '464F524D':
        return 'audio/aiff'; // aka audio/x-aiff
      case '504B3414060':
      case '504B030414000600':
        return 'application/vnd.openxmlformats-officedocument.wordprocessingml.document'; // aka application/vnd.openxmlformats-officedocument.spreadsheetml.sheet
      case 'D0CF11E0A1B11AE1':
      case 'DBA52D00':
        return 'application/msword'; // aka application/vnd.ms-excel
      case '464C56':
      case '464C5601':
      case '667479704D345620':
        return 'video/flv'; // aka application/x-shockwave-flash
      case '000001BA':
      case '000001B3':
      case '000001BX':
        return 'video/mpeg';
      case '6D6F6F76':
        return 'video/quicktime';
      case 'FFEX':
      case 'FFFX':
        return 'audio/mpeg';
      case '00000018':
        return 'audio/mp4';
      case '667479704D534E56':
        return 'video/mp4';
      case 'FFFB':
      case 'FFF3':
      case 'FFF2':
        return 'audio/mp3'; // aka audio/mpeg3
      case '494433':
        return 'audio/mpeg';
      case '667479704D344120':
        return 'audio/m4a';
      case '4F676753':
        return 'audio/ogg'; // aka video/ogg
      case '1A45DFA3':
        return 'video/webm'; // aka audio/webm
      case '667479706D703432':
        return 'video/x-m4v';
      default:
        return undefined;
    }
  };
  // try it with the full number, then first 3 bytes, then first 2 bytes
  return lookup(magicNumber) || lookup(magicNumber.substr(0, 6)) || lookup(magicNumber.substr(0, 4));
}


/**
 * Takes an array of bytes and converts it to a string of hex values aka the magic number
 *
 * @param {Uint8Array} bytes the array of bytes to convert
 * @return {string} the magic number
 */
export function convertBytesToMagicNumber(bytes) {
  let magicNumber = '';
  bytes.forEach((byte) => {
    magicNumber += byte.toString(16).padStart(2, '0');
  });
  return magicNumber.toUpperCase();
}

/**
 * Takes an array of bytes for a file and returns the mimetype for that file
 *
 * @param {Uint8Array} bytes the file data as an array of bytes
 * @return {string} the mime type
 */
export function handleFileByteArray(byteArray) {
  // grab different headers where magic numbers might live
  const firstFour = byteArray.subarray(0, 4);
  const firstEight = byteArray.subarray(0, 8);
  const fourOffsetByFour = byteArray.subarray(4, 8);
  const eightOffestByFour = byteArray.subarray(4, 12);

  // iterate through different magic numbers and resolve when a valid mimetype is found or we run out of numbers
  let mimeType;
  [firstFour, firstEight, fourOffsetByFour, eightOffestByFour].some((bytes) => {
    mimeType = lookupMimetypeFromMagicNumber(convertBytesToMagicNumber(bytes));
    return mimeType;
  });
  return mimeType;
}

/**
 * Takes a blob and determines its mimetype
 *
 * inspired by https://stackoverflow.com/questions/18299806/how-to-check-file-mime-type-with-javascript-before-upload
 *
 * @param {Blob} blob the blob to inspect and determine the mimeType for
 * @return {Promise<string>} a promise that will resolve to the mimeType
 */
export default async function getMimeType(blob) {
  // if we have a type already on the blob then use it and skip the reading of the file
  if (blob.type) {
    return Promise.resolve(blob.type);
  }

  // fall back to sniffing bytes and stuff
  return new Promise((resolve) => {
    const fileReader = new FileReader();
    fileReader.onloadend = (event) => {
      resolve(handleFileByteArray(new Uint8Array(event.target.result)));
    };
    fileReader.readAsArrayBuffer(blob);
  });
}
