import AWSDKDataObject from '../awsdk_dataobject';

/**
 * The AWSDKVisitExtension class represents a visit extension object
 * @property {Number} extensionCost the cost amount of this extension
 * @property {Number} extensionTime the amount of time to the visit this extension represents
 * @property {String} href the link url to this extension object
 * @property {String} engagementExtendedType string representation of an enum with values: NONE, WITH_NO_COPAY, WITH_COPAY.
 * @extends model.AWSDKDataObject
 */
class AWSDKVisitExtension extends AWSDKDataObject {
  get extensionCost() {
    return this.__data.extensionCost;
  }

  get extensionTime() {
    return this.__data.extensionTime;
  }

  get href() {
    return this.__data.href;
  }

  get engagementExtendedType() {
    return this.__data.engagementExtendedType;
  }
}
export default AWSDKVisitExtension;
