import AWSDKResponse from './awsdk_response';
import GenericParser from '../../parser/generic_parser';
import AWSDKTechCheckURL from '../awsdk_tech_check_url';

/**
 * Internal use only!
 * Simple wrapper around an {@link model.AWSDKTechCheckUrl}
 * @property {model.AWSDKTechCheckUrl} techCheckUrl the {@link model.AWSDKTechCheckUrl}
 * @private
 * @extends model.AWSDKResponse
 */
class AWSDKTechCheckURLResponse extends AWSDKResponse {
  get techCheckURL() {
    return GenericParser.parseObject(this.__data.techCheck, AWSDKTechCheckURL);
  }
}

export default AWSDKTechCheckURLResponse;
