/**
 * Represents the components that are required to have a WebRTC video visit.
 *
 * @property {function} successCallback Function to execute when the video visit has ended successfully
 * @property {function} errorCallback Function to execute when the video visit has encountered an unrecoverable error
 * @property {object} messages The localized strings to be used during the visit
 * @property {HTMLElement} [consoleWrapperAttachPoints] the DOM nodes that are specific to this Consumer Web SDK
 * @property {HTMLElement} videoConsoleContainer the DOM node where the video console will be rendered
 * @since 1.2.1
 */
class AWSDKVideoContext {
  constructor(args) {
    this.__config = args;
  }
  get successCallback() {
    return this.__config.successCallback;
  }
  get errorCallback() {
    return this.__config.errorCallback;
  }
  get consoleWrapperAttachPoints() {
    return this.__config.consoleWrapperAttachPoints;
  }
  get messages() {
    return this.__config.messages;
  }
  /**
   * The video console container
   * @type {HTMLElement}
   * @since 2.0.0
   */
  get videoConsoleContainer() {
    return this.__config.videoConsoleContainer;
  }

  /**
   * The callback that gets invoked on an updated visit
   * @type {Function}
   * @since 2.0.0
   */
  get updatedCallback() {
    return this.__config.updatedCallback;
  }
}
export default AWSDKVideoContext;
