import GenericParser from '../../internal/parser/generic_parser';
import AWSDKPaginated from '../awsdk_paginated';
import AWSDKPracticeFollowUpItem from './awsdk_practice_follow_up_item';

/**
 * This class represents the unresolved practice follow ups for a consumer {@link model.AWSDKPracticeFollowUpItem}s.
 * @property {model.AWSDKPracticeFollowUpItem[]} practiceFollowUpItems The result list of {@link model.AWSDKPracticeFollowUpItem}s for the authenticated consumer.
 * @extends model.AWSDKPaginated
 * @since 3.2.0
 */
class AWSDKPaginatedPracticeFollowUpItems extends AWSDKPaginated {
  get practiceFollowUpItems() {
    return (Array.isArray(this.__data.list) && this.__data.list.map(item => GenericParser.parseObject(item, AWSDKPracticeFollowUpItem))) || [];
  }
}
export default AWSDKPaginatedPracticeFollowUpItems;
