import AWSDKTrackerComponent from './awsdk_tracker_component';
import AWSDKTrackerBaseObject from './awsdk_tracker_base_object';
import GenericParser from '../../internal/parser/generic_parser';

/**
 * This class represents a template for all existing trackers
 * @property {model.AWSDKTrackerComponent[]} components an array of components that combined make up the full measurement of this tracker template.
 * @since 1.4.0
 * @extends model.AWSDKTrackerBaseObject
 */
class AWSDKTrackerTemplate extends AWSDKTrackerBaseObject {
  get components() {
    return (Array.isArray(this.__data.components) && this.__data.components.map(item => GenericParser.parseObject(item, AWSDKTrackerComponent))) || [];
  }
}
export default AWSDKTrackerTemplate;

