import GenericParser from '../../parser/generic_parser';
import AWSDKResponse from './awsdk_response';
import AWSDKVisitReportDetail from '../../../model/visit/awsdk_visit_report_detail';

/**
 * The AWSDKVisitReportDetailResponse class represents the response object to a call for a visit report's details
 * @property {model.AWSDKVisitReportDetail} visitReportDetail the details for a given visit report
 * @extends model.AWSDKResponse
 */
class AWSDKVisitReportDetailResponse extends AWSDKResponse {
  get visitReportDetail() {
    return GenericParser.parseObject(this.__data.visitReportData, AWSDKVisitReportDetail);
  }
}

export default AWSDKVisitReportDetailResponse;
