import AWSDKResponse from './awsdk_response';
import AWSDKVideoInvitation from '../../../model/visit/awsdk_video_invitation';
import GenericParser from '../../parser/generic_parser';

/**
 * Simple response wrapper around an array of {@link model.AWSDKVideoInvitation}
 * @property {model.AWSDKVideoInvitation[]} videoInvitations an array of {@link model.AWSDKVideoInvitation} objects
 * @extends model.AWSDKResponse
 * @since 2.0.0
 */
class AWSDKVideoInvitationListResponse extends AWSDKResponse {
  get videoInvitations() {
    return (Array.isArray(this.__data.videoInvitations) && this.__data.videoInvitations.map(item => GenericParser.parseObject(item, AWSDKVideoInvitation))) || [];
  }
}
export default AWSDKVideoInvitationListResponse;
