import AWSDKResponse from '../../internal/model/response/awsdk_response';
import GenericParser from '../../internal/parser/generic_parser';

/**
 * The AWSDKAvailabilityList class represents a wrapper object that encapsulates a response to a request for a list of date/times.
 * @property {Date[]} availability an array of {@link Date}
 * @since 1.1.0
 * @extends model.AWSDKResponse
 */
class AWSDKAvailabilityList extends AWSDKResponse {
  get availability() {
    return this.__data.availability.map(item => GenericParser.parseDate(item));
  }
}

export default AWSDKAvailabilityList;
