import AWSDKResponse from './awsdk_response';
import AWSDKVisitContext from '../../../model/visit/awsdk_visit_context';
import GenericParser from '../../parser/generic_parser';


/**
 * Represents a response object wrapper to a single {@link model.AWSDKVisitContext|AWSDKVisitContext} object type
 * @property {model.AWSDKVisitContext} visitContext the visit context in preparation for a visit
 * @extends model.AWSDKResponse
 */
class AWSDKVisitContextResponse extends AWSDKResponse {
  get visitContext() {
    return GenericParser.parseObject(this.__data.visitConfigurations, AWSDKVisitContext);
  }
}

export default AWSDKVisitContextResponse;
