import AWSDKEntity from '../awsdk_entity';
import GenericParser from '../../internal/parser/generic_parser';
import AWSDKProviderType from '../provider/awsdk_provider_type';
/**
 * Encapsulates information about a {@link model.AWSDKPractice}'s specialty applicable to on-demand visits
 * @property {Boolean} intakeConditionsEnabled whether the specialty collects {@link model.AWSDKCondition}s on intake intake
 * @property {Boolean} intakeAllergiesEnabled whether the specialty collects {@link model.AWSDKAllergy}s on intake intake
 * @property {Boolean} intakeMedicationsEnabled whether the specialty collects {@link model.AWSDKMedication}s on intake
 * @property {Boolean} intakeVitalsEnabled whether the specialty collects {@link model.AWSDKVital}s on intake
 * @property {Boolean} providersAvailable whether the specialty has any providers available at the time of last update
 * @since 1.3.1
 * @property {model.AWSDKProviderType[]} providerTypes the provider types associated with this specialty
 * @since 1.1.0
 * @extends model.AWSDKEntity
 */
class AWSDKOnDemandSpecialty extends AWSDKEntity {
  get intakeConditionEnabled() {
    return GenericParser.parseBoolean(this.__data.intakeConditionEnabled);
  }
  get intakeAllergiesEnabled() {
    return GenericParser.parseBoolean(this.__data.intakeAllergiesEnabled);
  }
  get intakeMedicationsEnabled() {
    return GenericParser.parseBoolean(this.__data.intakeMedicationsEnabled);
  }
  get intakeVitalsEnabled() {
    return GenericParser.parseBoolean(this.__data.intakeVitalsEnabled);
  }
  get providersAvailable() {
    return GenericParser.parseBoolean(this.__data.providersAvailable);
  }
  get providerTypes() {
    return (Array.isArray(this.__data.providerTypes) && this.__data.providerTypes.map(item => GenericParser.parseObject(item, AWSDKProviderType))) || [];
  }
}

export default AWSDKOnDemandSpecialty;
