import AWSDKResponse from './awsdk_response';
import AWSDKAppointment from '../../../model/appointment/awsdk_appointment';
import GenericParser from '../../parser/generic_parser';

/**
 * Simple wrapper around an {@link model.AWSDKAppointment}
 * @property {model.AWSDKAppointment} appointment the {@link model.AWSDKAppointment}
 * @extends model.AWSDKResponse
 */
class AWSDKAppointmentResponse extends AWSDKResponse {
  get appointment() {
    return GenericParser.parseObject(this.__data.appointment, AWSDKAppointment);
  }
}

export default AWSDKAppointmentResponse;
