import AWSDKTrackerBaseObject from './awsdk_tracker_base_object';

/**
 * This class represents a wrapper for the unit of measure's short and full descriptions
 * @property {String} unitOfMeasureShortDescription the description of the units of measure for this component in symbolic notation. Ex: mg/dL
 * @property {String} unitOfMeasureFullDescription the description of the units of measure for this component in words rather than symbols. Ex: milligrams per deciliter
 * @since 1.4.0
 * @extends model.AWSDKTrackerBaseObject
 */
class AWSDKTrackerUnitOfMeasure extends AWSDKTrackerBaseObject {
  get unitOfMeasureShortDescription() {
    return this.__data.unitOfMeasureShortDescription;
  }

  get unitOfMeasureFullDescription() {
    return this.__data.unitOfMeasureFullDescription;
  }
}
export default AWSDKTrackerUnitOfMeasure;
